<template>
<!-- 设备运转正常率 -->
    <div id="operationRate">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;设备运转正常率&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName" lay-verify="" placeholder="" autocomplete="off" class="layui-input widthI">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block widthI">
                            <select name="name" lay-verify="" class="widthI">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">起始时间</label>
                        <div class="layui-input-block widthI">
                            <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">终止时间</label>
                        <div class="layui-input-block widthI">
                            <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="btn">
                        <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome" >查询</a>
                        <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                    </div>
                </form>
                
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <div id="main" style="width: 600px;height:400px;" v-show="isShow"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,totalEquipmentOperation,exportRepairReport,exportEquipmentOperation} from '../../../config/axiosUtil'


export default {
    name: "OperationRate",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            pic: '',
            isShow: false,
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem("username")
            layui.use(['table','laydate','form'], function () {
                var table = layui.table,laydate = layui.laydate,form = layui.form;
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-280'
                    , cellMinWidth: 140
                    ,url: that.$url+'equipmentOperation' //数据接口
					,where:{username : uname}
                    ,page: true //开启分页
                    // ,toolbar: '#toolbarDemo'
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width:60,fixed:'left'}
                        , {field: 'custArea', title: '客户名称',minWidth:180,fixed:'left'}
                        , {field: 'serviceArea', title: '服务区域',minWidth:180}
                        , {field: 'unitType', title: '设备型号'}
                        , {field: 'esNumber', title: '设备序列号'}
                        , {field: 'machCode', title: '机器编码'}
                        , {field: 'workTime', title: '运转时间(hr)'}
                        , {field: 'downTime', title: '停机时间(hr)'}
                        , {field: 'operationRate', title: '设备正常运转率'}
                    ]]
                    ,id: 'testReload'
                    
                });
                //头工具栏事件
                // table.on('toolbar(test)', function(obj){
                //     var checkStatus = table.checkStatus(obj.config.id);
                //     switch(obj.event){
                //     case 'getCheckData':
                //         var data = checkStatus.data;
                //         layer.alert(JSON.stringify(data));
                //     break;
                //     case 'getCheckLength':
                //         var data = checkStatus.data;
                //         layer.msg('选中了：'+ data.length + ' 个');
                //     break;
                //     case 'isAll':
                //         layer.msg(checkStatus.isAll ? '全选': '未全选');
                //     break;
                //     };
                // });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    var serviceArea = data.name;
                    var custName = data.custName;
                    var username = uname;
                    var startDate = data.startDate;
                    var endDate = data.endDate;
                    $('.layui-icon-close').click();
                    table.reload('testReload', {
                        url: that.$url+'equipmentOperation' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            serviceArea,
                            custName,
                            username,
                            startDate,
                            endDate
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                        // ,done:function(res){
                        //     if(res.count == 1){
                        //         that.isShow = true
                        //         echartsFun()
                        //     }else{
                        //         that.isShow = false
                        //     }
                            
                        // }
                    });
                    if(serviceArea || custName){
                        that.isShow = true
                        echartsFun(serviceArea,custName,username,startDate,endDate)
                    }else{
                        that.isShow = false
                    }
                    
                })
            });
            
            function echartsFun(s,c,u,sd,ed){
                console.log(s,c,u,sd,ed)
                // console.log(serviceArea,custName,username)
                var resdata = {serviceArea:s,custName:c,username : u,startDate:sd,endDate:ed}
                totalEquipmentOperation(resdata).then((res) => {
                    console.log(res)
                    var val = res.data.设备正常运转率;
                    // var eqValue = val.toFixed(1)
                    // console.log(eqValue)
                    var myChart = that.$echarts.init(document.getElementById('main'),'',{width:600,height:300})
                    var option = {
                        title:{
                            text: '设备正常运转率'
                        },
                        tooltip : {
                            formatter: "{a} <br/>{b} : {c}%"
                        },
                        toolbox: {
                            // feature: {
                            //     restore: {},
                            //     saveAsImage: {}
                            // }
                        },
                        series: [
                            {
                                name: '设备正常运转率',
                                type: 'gauge',
                                min: 50,
                                distance: 5,
                                detail: {formatter:'{value}%'},
                                axisLine: {            // 坐标轴线  
                                    lineStyle: {       // 属性lineStyle控制线条样式  
                                        color: [[0.6, '#c10404'], [0.9, '#f39407'], [1, '#009953']],
                                        width: 8
                                    }  
                                },
                                data: [{value: val, name: ''}]
                                // data: [{value: 69, name: ''}]
                            }
                        ],
                        animation:false,
                    };
                    myChart.setOption(option);
                    var picture = myChart.getDataURL();
                    that.pic = {picture : myChart.getDataURL()}
                })
                .catch((error) => {				
                })
            
            }
        },
        exportExcel(){
			 var that = this;
            let userN = sessionStorage.getItem('username')
            // that.$exportE('exportBranchKpi')
            exportEquipmentOperation(that.pic).then((res) => {
                console.log(res)	
                window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })	
		}
    }
}
</script>
<style scoped>
#operationRate{
    width: 85%;
}
.widthI {
    width: 250px;
}
</style>



