<template>
    <!-- 设备概况 -->
    <div id="equipment">
        <!-- <Index/> -->
        <div class="body">
            <div class="titleLab">&gt;&gt;设备概况&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">客户名称</label>
                    <div class="layui-input-block allinput">
                        <input type="text" name="name" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block allinput">
                        <!--<input type="text" name="area" v-model='v.serviceArea' autocomplete="off" class="layui-input"> -->
                        <select name="area" lay-verify="">
                            <option value=""  id="address"></option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">设备型号</label>
                    <div class="layui-input-block allinput">
                        <input type="text" name="type"  autocomplete="off" class="layui-input">
                    </div>
                </div>
            </div>
            <div class="btn">
                <!-- <a class="layui-btn layui-bg-blue" @click='selectAll()'>查询</a> -->
                <a class="layui-btn layui-bg-blue" lay-filter="formDome" lay-submit="">查询</a>
                <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
            </div>
            </form>
            
            <div class="labnum">
                <label>数量：&nbsp;&nbsp;{{ item.count1 }}&nbsp;&nbsp;</label>
            </div>
            </div>
            <div class="tab">
                <table id="tableData" lay-filter="tableData"></table>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {equipmentSituation} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    mounted() {
        this.getData();
    },
	data () {
		return {
			v : {
				custName : '',
				serviceArea: '',
				unitType: ''
			},
			item: {
				count1: '1',
			}	
		}
	},
    methods: {
        getData(){
			var that = this;
            var uname = sessionStorage.getItem("username")
            layui.use(['table','form'], function(){
                var table = layui.table,form = layui.form;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#tableData'
                    ,method: 'post'
                    ,height: 'full-320'
                    ,cellMinWidth: 120
                    ,url: that.$url+'equipmentSituation' //数据接口
                    ,where:{username:uname}
                    ,page: true //开启分页
                    ,cols: [[ //表头
                        {field: 'id', title: '序号', sort: true,type:'numbers',width:80}
                        ,{field: 'custArea', title: '客户名称'}
                        ,{field: 'serviceArea', title: '服务区域'}
                        ,{field: 'devName', title: '设备名称'}
                        ,{field: 'machCode', title: '机器编码'}
                        ,{field: 'unitType', title: '设备型号',}
                        ,{field: 'esNumber', title: '设备序列号'}
                        ,{field: 'location', title: '设备位置'}
                        ,{field: 'deviceBrand', title: '厂家'}
                        ,{field: 'installedTime', title: '安装日期',templet:"<div>{{layui.util.toDateString(d.installedTime,'yyyy-MM-dd')}}</div>"}
                        ,{field: 'khdz', title: '服务年限'}
                        ,{field: 'assetAttr', title: '设备归属'}
                        ,{field: 'bz', title: '备注'}
                    ]]
                    ,id: 'testReload'
                    ,parseData: function(res){
                        // console.log(res)
                        that.item.count1 = res.count;
                        return {
                            "code": res.code, //解析接口状态
                            "count": res.count, //解析数据长度
                            "data": res.data //解析数据列表
                        };
                    }
                });
                //服务区下拉内容
                equipmentSituation().then((res) => {
                    var data = res.data.data;
                    var serverArea = []
                    for(var i=0; i<data.length; i++){
                        if(serverArea.indexOf(data[i].serviceArea) == -1){
                            if(data[i].serviceArea != null){
                                serverArea.push(data[i].serviceArea);
                            }
                            
                        }
                    }
                    for(var j=0; j<serverArea.length; j++){
                        console.log(serverArea[j])
                        $('#address').after('<option value=\''+ serverArea[j] +'\'  v-model="v.serviceArea">'+ serverArea[j] +'</option>');
                    }
                    form.render()
                })
                .catch((error) => {
                    // console.log(error)
                }) 
                // 查询
                form.on('submit(formDome)',function(data){
                    $('.layui-icon-close').click();
                    var data = data.field;
                    // console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'equipmentSituation' ,//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.name,//客户名称
                            serviceArea : data.area,//服务区域
                            unitType : data.type, //设备型号
                            username : uname
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                }) 
            });
        },
		exportExcel(){
			var that = this;		
// 			var custName = that.v.custName;
// 			var serviceArea = that.v.serviceArea;
// 			var unitType = that.v.unitType;	
// 			window.open(that.$url+'exportSituationEquipment?custName='+custName+'&serviceArea='+serviceArea+'&unitType='+unitType)
			that.$exportE('exportSituationEquipment')	
		}
    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#equipment{
    width: 85%;
}
</style>



