<template>
    <!-- 客户满意度 -->
    <div id="satisfaction">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;客户满意度&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">客户名称</label>
					<div class="layui-input-block">
						<input type="text" name="custName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input" >
					</div>                    
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block">
                    	<select name="serviceArea" lay-verify="" class="allinput">
                    	    <option value="" id="address">请选择</option>     
                    	</select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务评价</label>
                    <div class="layui-input-block">
                        <select name="custSat" lay-verify="">
                            <option value="">请选择</option>
                            <option value="4">满意</option>
                            <option value="5">非常满意</option>
                            <option value="3">一般</option>
                            <option value="2">不满意</option>
                            <option value="1">非常不满意</option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">开始日期</label>
                    <div class="layui-input-inline">
                        <input type="text" name='startDate' class="layui-input" id="startTime" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">结束日期</label>
                    <div class="layui-input-inline">
                        <input type="text" name='endtDate' class="layui-input" id="endTime" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
            </div>
			<div class="btn">
				<a class="layui-btn layui-bg-blue" lay-filter="formDemo" lay-submit @click='selectAll()'>查询</a>
				<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
			</div>
            </form>
            
            </div>
            <div class="tab">
                <table id="demo" lay-filter="test"></table>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {customerSatisfaction} from '../../../config/axiosUtil'


export default {
    name: "Satisfaction",
    components: {
        Index
    },
    mounted() {
        this.getData();
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['form','laydate','table'], function(){
                var form = layui.form;
                // var a = $('#aaa').val();
                var laydate = layui.laydate;
                var table = layui.table;        
         
                laydate.render({
                    elem: '#startTime'
                });
                laydate.render({
                    elem: '#endTime'
                });
                form.render();
                table.render({
                    elem: '#demo'
                    ,height: 'full-320'
                    ,cellMinWidth: 120
                    ,url: that.$url+'customerSatisfaction'  //数据接口
                    ,where:{username:uname}
                    ,page: true //开启分页
                    ,cols: [[ //表头
                        {field: 'id', title: '序号', sort: true,type:'numbers',width:80}
                        ,{field: 'custName', title: '客户名称',sort: true}
                        ,{field: 'serviceArea', title: '服务区域'}
                        ,{field: 'woNumber', title: '工单号码',sort: true}
                        ,{field: 'repairMan', title: '报修人姓名',}
                        ,{field: 'repairPhone', title: '联系电话'}
                        ,{field: 'repairTime', title: '报修时间',}
                        ,{field: 'completeTime', title: '完成时间',}
                        ,{field: 'enginnerName', title: '维修工程师'}
                        ,{field: 'custScore', title: '实际得分'}
                        ,{field: 'custSat', title: '投诉与建议内容'}
                        ,{field: 'remark', title: '备注'}
                    ]]
                });
                // 下拉
                customerSatisfaction().then((res) => {
                    var data = res.data.data;
                    var serverArea = []
                    for(var i=0; i<data.length; i++){
                        if(serverArea.indexOf(data[i].serviceArea) == -1){
                            serverArea.push(data[i].serviceArea);
                        }
                    }
                    for(var j=0; j<serverArea.length; j++){
                        console.log(serverArea[j])
                        $('#address').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
                    }
                    form.render()
                })
                .catch((error) => {				
                });
            });
        },
        // 查询
		selectAll(){
			var that = this;
            $('.layui-icon-close').click();
			layui.use(['form','laydate','table'], function(){
				var form = layui.form;
				// var a = $('#aaa').val();
				var laydate = layui.laydate;
				var table = layui.table;
				//监听提交
				form.on('submit(formDemo)', function(data){
					var data = JSON.stringify(data.field)
					data = JSON.parse(data)
					// console.log(data)
					var custName = data.custName
					var serviceArea = data.serviceArea
					var custSat = data.custSat
					var startDate = data.startDate
					var endDate = data.endDate
					var username = sessionStorage.getItem("username")
					table.render({
						elem: '#demo'
						,height: 312
						,cellMinWidth: 120
						,url: that.$url+'customerSatisfaction'  //数据接口
						,method: 'post',
						where:{custName,serviceArea,custSat,startDate,endDate,username}
						,page: true //开启分页
						,cols: [[ //表头
							{field: 'id', title: '序号', sort: true,type:'numbers',width:80}
							,{field: 'custName', title: '客户名称',sort: true}
							,{field: 'serviceArea', title: '服务区域'}
							,{field: 'woNumber', title: '工单号码',sort: true}
							,{field: 'repairMan', title: '报修人姓名',}
							,{field: 'repairPhone', title: '联系电话'}
							,{field: 'repairTime', title: '报修时间'}
							,{field: 'completeTime', title: '完成时间'}
							,{field: 'enginnerName', title: '维修工程师'}
							,{field: 'custScore', title: '实际得分'}
							,{field: 'custSat', title: '投诉与建议内容'}
							,{field: 'remark', title: '备注'}
						]]
					});
					return false;
				});
// 				
				
			});
		},
		exportExcel(){
			var that = this;
            $('.layui-icon-close').click();
			that.$exportE('exportCustSatisfaction')
		}
    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#satisfaction{
    width:85%;
    
}
</style>



