<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;保养报表&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div class="btn">
                    <a class="layui-btn layui-bg-blue"  lay-submit lay-filter="formDome">查询</a>
                    <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                </div>
            </form>
            <div style="margin-left:30px">保养完成报表</div>
            </div>
            <div class="tab">
                <table id="tbmForms" lay-filter="tbmForms"></table>
            </div>
        <div class="abovebtn">
            <form class="layui-form" action="">
                <div class="layui-form-item">
                    
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="addressA">请选择</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div class="btn">
                    <a class="layui-btn layui-bg-blue"  lay-submit lay-filter="formDome1">查询</a>
                    <a class="layui-btn layui-bg-blue" @click='exportExcel1()'>导出</a>
                </div>
            </form>
            <div style="margin-left:30px">保养状态报表</div>
        </div>
        <div class="tab">
            <table id="tbmForm" lay-filter="tbmForm"></table>
            
        </div>
                
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem("username")
            layui.use(['table','form','laydate'], function () {
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                laydate.render({
                    elem: '#timeS'
                });
                laydate.render({
                    elem: '#timeE'
                });
                laydate.render({
                    elem: '#timeS2'
                });
                laydate.render({
                    elem: '#timeE2'
                });
                form.render();
                //保养完成报表
                table.render({
                    elem: '#tbmForms'
                    , height: 'full-280'
                    , cellMinWidth: 140
                    ,url: that.$url+'maintenanceSummary' //数据接口
					,where:{username : uname,}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'mainFrequency', title: 'NO', sort: true, type:'numbers',width:80}
                        , {field: 'custName', title: '客户名称',minWidth:160}
                        , {field: 'machCode', title: '机器编码'}
                        , {field: 'deviceType', title: '设备类型',
                            templet: function(d){
                                // console.log(d)
                                if(d.device.deviceType == null){
                                    return ''
                                }else{
                                    return d.device.deviceType
                                }
                                
                            }
                        }
                        , {field: 'mainFrequency', title: '保养周期'}
                        , {field: 'maintenStatus', title: '保养状态'}
                        , {field: 'lastTime', title: '上次保养时间',minWidth: 160,
                            templet:function(d){
								if(d.lastTime == null){
									return ''
								}else{
									var insDate = layui.util.toDateString(d.lastTime)
									return "<div>"+insDate+"</div>"
								}
							}
                        }
                        , {field: 'Reader', title: '黑白读数',
                            templet: function(d){
                                // console.log(d)
                                if(d.device.bwReader == null){
                                    return ''
                                }else{
                                    return d.device.bwReader
                                }
                            }
                        }
                        , {field: 'Reader', title: '彩色读数',
                            templet: function(d){
                                if(d.device.colorReader == null){
                                    return ''
                                }else{
                                    return d.device.colorReader
                                }
                            }
                        }
                        , {field: 'coverage', title: '覆盖率(%)'}
                        , {field: 'materialModel', title: '耗材存量'}
                        , {field: 'remark', title: '备注'}
                    ]]
                    ,id: 'testReload1'
                });
                //保养报表状态
                table.render({
                    elem: '#tbmForm'
                    , height: 470
                    , cellMinWidth: 120
                    ,url: that.$url+'maintenanceReport' //数据接口
					,where:{username : uname,}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width:80}
                        , {field: 'custName', title: '客户名称',minWidth:160}
                        // , {field: 'mainFrequency', title: '保养周期'}
                        , {field: 'deviceNumber', title: '设备数量',}
                        , {field: 'planDeviceNumber', title: '计划保养数量'}
                        , {field: 'reallyOverDeviceNumber', title: '实际完成'}
                        , {field: 'notOverDeviceNumber', title: '未完成'}
                        , {field: 'maintenanceRate', title: '完成率'}
                        // , {field: 'mainFrequency', title: '工程师姓名'}
                        , {field: 'remark', title: '备注'}
                    ]]
                    ,id: 'testReload'
                });
                
                // 查询客户
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload1', {
                        url: that.$url+'maintenanceSummary' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName: data.custName,
                            serviceArea : data.name,
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                var uRole = sessionStorage.getItem("userRole")
		        var serviceA = sessionStorage.getItem("serviceArea")
                if(uRole == '总部客服' || uRole == '运维总监' ){
                    areaManage().then((res) => {
                        var data = res.data.data;
                        var serverArea = []
                        for(var i=0; i<data.length; i++){
                            if(serverArea.indexOf(data[i].compName) == -1){
                                serverArea.push(data[i].compName);
                            }
                        }
                        for(var j=0; j<serverArea.length; j++){
                            // console.log(serverArea[j])
                            $('#addressA').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
                        }
                        form.render()
                    })
                    .catch((error) => {				
                    });
                }else if(uRole == '客户'){
                    $('#addressA').after('<option value=\''+ serviceA +'\' id="">'+ serviceA +'</option>');
                    form.render()
                }else{
                    if(serviceA == '广州乐派数码科技有限公司' || serviceA == '行业客户部' || serviceA == '系统推进部'){
                        $('#addressA').after('<option value="广州乐派数码科技有限公司">广州乐派数码科技有限公司</option><option value="行业客户部">行业客户部</option><option value="系统推进部">系统推进部</option>');
                        form.render()
                    }else{
                        $('#addressA').after('<option value=\''+ serviceA +'\' id="">'+ serviceA +'</option>');
                        form.render()
                    }
                    
                }	
                // 查询服务区
                form.on('submit(formDome1)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'maintenanceReport' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            serviceArea : data.name,
                            custName: data.custName,
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
                
            });
        },
        exportExcel(){
			var that = this
			that.$exportE('exportMaintenanceSummary')		
		},
        exportExcel1(){
			var that = this
			that.$exportE('exportMaintenanceReport')		
		}
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



