<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;区域管理&lt;&lt;</div>
            <div class="abovebtn">
                <div class="btn">
                    <!-- <button class="layui-btn layui-bg-blue" @click="add()">新增</button> -->
                    <a class="layui-btn layui-bg-blue" v-if='addShow' lay-submit="" lay-filter="add">新增</a>
                </div>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <script type="text/html" id="barDemo">
                <!-- <a class="layui-btn layui-btn-xs" lay-event="edit">修改</a>
                <a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="del">删除</a> -->
            </script>
        </div>
        <!-- 新增||修改 -->
        <div id='addWindow' class="sdd dis-n" >
            <form class="layui-form" lay-filter="example">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>服务区域</label>
                        <div class="layui-input-inline">
                            <input type="text" name="serviceArea" lay-verify="required" class="layui-input"  autocomplete="off">
                            <input type="text" name="compId" lay-verify="" class="layui-input dis-n"  autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>服务区缩写</label>
                        <div class="layui-input-inline">
                            <input type="text" name="abbre" lay-verify="required" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>区域地址</label>
                        <div class="layui-input-inline">
                            <input type="text" name="location" lay-verify="required" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item mar-left" v-if="refer">
                    <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="addSubmit" v-if="subBut">提交</a>
                    <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="preservation" v-else>保存</a>
                    <button type="reset" class="layui-btn dis-n reset">重置</button>
                </div>
                <div class="layui-form-item mar-left" v-else>
                    <a class="layui-btn layui-bg-blue" v-if="subBut">提交</a>
                    <a class="layui-btn layui-bg-blue" v-else>保存</a>
                    <button type="reset" class="layui-btn dis-n reset">重置</button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {addArea,deleteArea,updateArea} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            subBut:true,
			addShow: false,
            refer: true
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uRole = sessionStorage.getItem("userRole")
            layui.use(['table','layer','form'], function () {
                var table = layui.table, layer = layui.layer, form = layui.form;
                table.render({
                    elem: '#demo'
                    ,method:'post'
                    , height: 'full-240'
                    , cellMinWidth: 360
                    , url: that.$url+'areaManage' //数据接口
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: '', title: '序号', type: 'numbers',}
                        , {field: 'compName', title: '服务区域',}
                        , {field: 'abbreviation', title: '服务区域缩写',}
                        , {field: 'compLocation', title: '区域地址',minWidth: 800}
                        , {field: 'czx',fixed: 'right', title: '操作项',width: 120,
							templet: function (d) {
								var arr2 = ''
								return that.isShow(d);
							}
						}
                    ]]
                    ,id: 'testReload'
                    ,done:function(res){
						// console.log(res)
						if(uRole != '运维总监' && uRole != '总部客服'){
							$("[data-field='czx']").css('display','none');
						}
					}
                });
                
                table.on('tool(demo)',function(obj){
                    // 修改
                    that.subBut = false;
                    if(obj.event == 'edit'){
                        that.subBut = false;
                        layer.open({
                            type: 1,
                            title: '修改',
                            area: ['700px','500px'],
                            content: $('.sdd')
                        });	
                        //表单初始赋值
                        form.val('example', {
                            "compId": obj.data.compNumber,   //id
                            "serviceArea": obj.data.compName // 服务区域
                            ,"abbre": obj.data.abbreviation // 缩写
                            ,"location": obj.data.compLocation  //区域地址
                        })
                    //删除
                    }else if(obj.event == 'del'){
                        var delcontent = {compNumber : obj.data.compNumber};
                        layer.confirm('确定删除？', function(index){
							deleteArea(delcontent).then((res) => {
								if(res.data.code == 0){
                                    layer.msg(res.data.msg);
                                    obj.del();
                                    layer.close(index);											 
								}else{
                                    layer.msg(res.data.msg);
								}
							})
							.catch((error) => {
								// layer.msg('我的天呐，网炸了！')
							})						
						});
                    }
                })
                //新增弹窗
                form.on('submit(add)',function(){
                    that.subBut = true;
                    $(".reset").click();
                    layer.open({
                        type: 1,
                        title: '新增',
                        area: ['700px','500px'],
                        content: $('.sdd')
                    });
                    return false;
                })
                // 新增提交
                form.on('submit(addSubmit)',function(obj){
                    that.refer = false
                    var data = obj.field;
                    var addData = {
                        serviceArea: data.serviceArea, //服务区名称
                        abbre:data.abbre,//服务区缩写
                        location:data.location,//服务区地址
                    }
                    addArea(addData).then((res) => {
                        
                        if(res.data.code == 0){
                            layer.closeAll()
                            layer.msg(res.data.msg);
                            table.reload('testReload')
                            that.refer = true
                        }else{
                            that.refer = true
                            layer.msg(res.data.msg)
                        }
                    }).catch((error) => {
                    })
                })
                //修改
                form.on('submit(preservation)',function(obj){
                    // console.log(obj)
                    that.refer = false
                    var data = obj.field;
                    var saveData = {
                        compId: data.compId,
                        serviceArea: data.serviceArea, //服务区名称
                        abbre:data.abbre,//服务区缩写
                        location:data.location,//服务区地址
                    }
                    updateArea(saveData).then((res) => {
                        
                        if(res.data.code == 0){
                            layer.closeAll()
                            layer.msg(res.data.msg);
                            table.reload('testReload')
                            that.refer = true
                        }else{
                            layer.msg(res.data.msg)
                            that.refer = true
                        }
                    }).catch((error) => {
                    })
                })
            });
        },
		showA(){
			var that = this
			var arr = [that.$admin.zbkf,that.$admin.ywzj,that.$admin.admin]
			console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
			}
		},	
		isShow(){
			var that = this
			var arr2 = that.$admin
			var admin = ''
			if(arr2.zbkf == that.$userRole || arr2.admin == that.$userRole || arr2.ywzj == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="edit">修改</a><a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="del">删除</a>'	
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
			}
			return admin;			
		},
    },
	created(){
		this.showA()
	}
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
#addWindow input{
    width: 400px;
}
</style>



