<template>
<!-- 维修报表 -->
    <div id="orderStat">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;工单时效表&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                    
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">起始时间</label>
                        <div class="layui-input-inline">
                            <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">终止时间</label>
                        <div class="layui-input-inline">
                            <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="btn">
                        <a class="layui-btn layui-bg-blue"  lay-submit lay-filter="formDome">查询</a>
                        <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                    </div>
                </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <div id="main" style="width: 50%;height:400px;float: left;" v-show="isShow"></div>
            <div id="mainP" style="width: 50%;height:400px;float: left;" v-show="isShow"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,exportOrderPrescription} from '../../../config/axiosUtil'

export default {
    name: "OrderStat",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            hcgd:'',
            echarsType:'',
            isShow: false,
            arrTimeAvg: '',
            probTimeAvg: '',
            staffName: '',
            picA:'',
            pic:'',
            avgOrderNumber: '',
            avgTime: '',
            proTime: '',
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','laydate','form'], function () {
                var table = layui.table,laydate = layui.laydate,form = layui.form;
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-280'
                    , cellMinWidth: 120
                    , url: that.$url+'orderPrescription' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    ,totalRow:true
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width: 80,totalRowText: '平均值'}
                        ,{field: 'staffName', title: '工程师姓名'}
                        ,{field: 'orderNum', title: '维修单数',totalRow: true}
                        ,{field: 'arrTimeAvg', title: '到达现场平均用时(h)',totalRow: true}
                        , {field: 'probTimeAvg', title: '问题解决平均用时(h)',totalRow: true}
                        
                    ]]
                    ,id: 'testReload'
                    , done: function (res) {
                        // console.log(res)
                        var tableView = this.elem.next();
                        var totalRow = tableView.find('.layui-table-total');
                        var limit = this.page ? this.page.limit : this.limit;
                        var aaa = []
                        layui.each(totalRow.find('td'), function (index, tdElem) {
                            tdElem = $(tdElem);
                            var text = tdElem.text();
                            
                            if (text && !isNaN(text)) {
                                if(res.count>=20){
                                    text = text/20
                                }else{
                                    text = text/res.count
                                }
                                text = text.toFixed(1)
                                tdElem.find('div.layui-table-cell').html(text);
                            }
                            aaa.push(text)
                            console.log(text)
                        });
                        // that.isShow = false
                        // console.log(aaa)
                        that.avgOrderNumber = aaa[2]
                        that.avgTime = aaa[3]
                        that.proTime = aaa[4]
                    }
                    
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'orderPrescription' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            serviceArea : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                        ,done: function(res){
                            var tableView = this.elem.next();
                            var totalRow = tableView.find('.layui-table-total');
                            var limit = this.page ? this.page.limit : this.limit;
                            var aaa = []
                            layui.each(totalRow.find('td'), function (index, tdElem) {
                                tdElem = $(tdElem);
                                var text = tdElem.text();
                                
                                if (text && !isNaN(text)) {
                                    if(res.count>=20){
                                        text = text/20
                                    }else{
                                        text = text/res.count
                                    }
                                    text = text.toFixed(1)
                                    tdElem.find('div.layui-table-cell').html(text);
                                }
                                aaa.push(text)
                            });
                            that.avgOrderNumber = aaa[2]
                            that.avgTime = aaa[3]
                            that.proTime = aaa[4]
                            if(res.code2 == 1){
                                that.isShow = false
                            }else{
                                that.isShow = true
                                var engeerN = []
                                var engeerPro = []
                                var engeerArr = []
                                for(var i=0;i<res.data.length;i++){
                                    engeerN.push(res.data[i].staffName)
                                    engeerArr.push(res.data[i].arrTimeAvg)
                                    engeerPro.push(res.data[i].probTimeAvg)
                                }
                                that.staffName = engeerN
                                that.arrTimeAvg = engeerArr
                                that.probTimeAvg = engeerPro
                                echartsFun()
                                echartsFunP()
                            }
                            
                        }
                    });
                })
            });
            function echartsFun(){
            var myChart = that.$echarts.init(document.getElementById('main'),'',{width:600,height:300})
            // 指定图表的配置项和数据
            // console.log(that.engName)
            var option = {
                // color: ['#f0f'],
                title:{
                    text:'工程师到达现场平均用时'
                },
                xAxis: {
                    type: 'category',
                    data: that.staffName
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {  
                        show: true,  
                        interval: 'auto',  
                        formatter: '{value}(h)'  
                    }, 
                },
                series: [{
                    data: that.arrTimeAvg,
                    type: 'bar',
                    label: {
                        normal: {
                            show: true,
                            position: 'inside'
                        }
                    },
                }],
                animation:false,
            };
            myChart.setOption(option);
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.picA = myChart.getDataURL()
        }
        function echartsFunP(){
            var myChart = that.$echarts.init(document.getElementById('mainP'),'',{width:600,height:300})
            // 指定图表的配置项和数据
            // console.log(that.engName)
            var option = {
                // color: ['#f0f'],
                title:{
                    text:'工程师问题解决平均用时'
                },
                xAxis: {
                    type: 'category',
                    data: that.staffName
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {  
                        show: true,  
                        interval: 'auto',  
                        formatter: '{value}(h)'  
                    }, 
                },
                series: [{
                    data: that.probTimeAvg,
                    type: 'bar',
                    label: {
                        normal: {
                            show: true,
                            position: 'inside'
                        }
                    },
                }],
                animation:false,
            };
            myChart.setOption(option);
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.pic =  myChart.getDataURL()
        }
        },
        exportExcel(){
			 var that = this;
            console.log(that.pic)
            console.log(that.avgOrderNumber)
            var pict = {
                picture1:that.picA,
                picture2:that.pic,
                avgOrderNumber : that.avgOrderNumber,
                avgTime : that.avgTime,
                proTime : that.proTime,
            }
            let userN = sessionStorage.getItem('username')
            // that.$exportE('exportBranchKpi')
            exportOrderPrescription(pict).then((res) => {
                console.log(res)	
                window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })		
		}
        
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



