<template>
<!-- 故障分析 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;维修履历&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">客户名称</label>
                    <div class="layui-input-block">
                        <input type="text" name="name" lay-verify="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block">
                        <!-- <input type="text" name="serAddr" lay-verify="" autocomplete="off" class="layui-input"> -->
                        <select name="serAddr" lay-verify="">
                            <option value=""  id="address"></option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">机器编码</label>
                    <div class="layui-input-block">
                        <input type="text" name="bianma" lay-verify="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                
            </div>
            <div class="btn">
                <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome">查询</a>
                <a class="layui-btn layui-bg-blue" @click='$exportE("exportFaiure")'>导出</a>
            </div>
            </form>
            
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            
            <script type="text/html" id="barDemo">
                <a class="layui-btn layui-btn-xs" lay-event="history">查看历史</a>
            </script>
        </div>
        <div class="dis-n" id="history">
            <table id="test" lay-filter="test"></table>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {failureAnalysis,areaManage} from '../../../config/axiosUtil'


export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var username = sessionStorage.getItem('username')
            var unMask = true;
            layui.use(['table','form','laydate'], function(){
                var table = layui.table,laydate = layui.laydate,form = layui.form;
                //合同期限
                laydate.render({
                    elem:"#contractPeriod"
                })
                //日期范围
                laydate.render({
                    elem: '#startDate'
                });
                laydate.render({
                    elem: '#endDate'
                });
                
                form.render();
                table.render({
                    elem: '#demo'
                    ,height: 'full-280'
                    ,cellMinWidth: 120
                    ,url: that.$url+'failureAnalysis' //数据接口
                    ,where:{username}
                    ,page: true //开启分页
                    ,limit: 20
                    ,cols: [[ //表头
                        {field: 'id', title: '序号', type: 'numbers',width: 80,fixed: "left"}
                        ,{field: 'custName', title: '客户名称', width: 180,fixed:"left"}
                        ,{field: 'servicrArea', title: '服务区域', }
                        ,{field: 'machCode', title: '机器编码', }
                        ,{field: 'devName', title: '设备类型'}
                        ,{field: 'faultType', title: '故障类型'}
                        ,{field: 'downTime', title: '停机时间(分钟)' ,width:150}
                        ,{field: 'repairTime', title: '创建时间',templet:"<div>{{layui.util.toDateString(d.repairTime,'yyyy-MM-dd')}}</div>"}
                        ,{field: 'bz', title: '操作项',toolbar:'#barDemo'}
                    ]]
                    ,id: 'testReload'
                    ,done: function(res){
						console.log(res)
						console.log(that.limita)
						
					}
                });
                // 服务区域下拉内容
                that.$serviceArea(areaManage)
                //查看历史弹框
                table.on('tool(demo)', function(obj){
                    if(obj.event == 'history'){
                        layer.open({
                            type:1,
                            title: '查看历史',
                            content:$('#history'),
                            area:['100%', '100%'],
                            // maxmin: true
                        })
                    }
                    //查看历史的数据
                    table.render({
                        elem: '#test'
                        ,height: 'full-100'
                        ,cellMinWidth: 120
                        ,url: that.$url+'faultRecord?machCode='+obj.data.machCode //数据接口
                        ,page: true //开启分页
                        ,limit: 20
                        ,cols: [[ //表头
                            {field: 'woNumber', title: '工单号码', sort: true}
                            ,{field: 'machCode', title: '机器编码',sort: true}
                            ,{field: 'devName', title: '设备类型',sort: true}
                            ,{field: 'machCode', title: '设备型号',}
                            ,{field: 'esNumber', title: '设备序列号'}
                            ,{field: 'location', title: '设备位置'}
                            ,{field: 'repairMan', title: '报修人姓名'}
                            ,{field: 'repairTime', title: '报修时间',templet:"<div>{{layui.util.toDateString(d.repairTime,'yyyy-MM-dd')}}</div>"}
                            ,{field: 'enginnerName', title: '指定工程师'}
                            ,{field: 'probSolve', title: '问题解决时间',templet:"<div>{{layui.util.toDateString(d.probSolve,'yyyy-MM-dd')}}</div>"}
                            ,{field: 'faultType', title: '服务类型'}
                            ,{field: 'accidentType', title: '服务类别'}
                            ,{field: 'faultNo', title: '故障代码'}
                            ,{field: 'mainTenanceFeedback', title: '故障描述'}
                            ,{field: 'treatmentMeasure', title: '处理措施'}
                        ]]
                    })
                });
                //查询
                form.on('submit(formDome)', function(data){
                    $('.layui-icon-close').click();
                    var data = data.field;
                    table.reload('testReload', {
                        url: that.$url+'failureAnalysis' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.name,
                            machCode : data.bianma,
                            serviceArea : data.serAddr,
                            username
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                    return false;
                });
            });
        },
        //查询

    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



