<template>
<!-- 保养计划 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
		<div class="titleLab">&gt;&gt;保养计划&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
                <div class="layui-inline">
                    <label class="layui-form-label">客户名称</label>
                    <div class="layui-input-block">
                        <input type="text" name="custName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block">
                        <select name="serviceArea" lay-verify="" class="allinput">
                          <option value="" id="address">请选择</option>      
                        </select>
                    </div>
                </div>
				<div class="btn">
					<a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDemo">查询</a>
					<a class="layui-btn layui-bg-blue" v-if='addShow' @click='add()'>新增</a>
					<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
				</div>
            </form>
            
            </div>

            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>            
        </div>
		<!-- 新增保养计划 -->
		<div class="dis-n sdd">
			<form class="layui-form" action="">
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>机器编码：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='formV.machCode' @blur='selectA()' lay-verify="machCode" class="layui-input"  autocomplete="off">
						<!-- <input type="hidden"  class="layui-input"> -->
					</div>
				</div>
			</div>				
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>保养频率：</label>
					<div class="layui-input-inline">
						<select name="mainFrequency">
							<option value="">请选择</option>
							<option value="周">周</option>
							<option value="月">月</option>
							<option value="季度">季度</option>
							<option value="半年">半年</option>
							<option value="年">年</option>
						</select>						
					</div>
				</div>
			</div>		
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>合同编码：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='contractCode' lay-verify="" unselectable='on' readOnly class="layui-input"  autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>客户名称：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='custName' lay-verify="" unselectable='on' readOnly class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>联系人：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='repairMan' lay-verify="" unselectable='on' readOnly class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>联系电话：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='repairService' lay-verify="" unselectable='on' readOnly class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>责任工程师：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='responsibleEngineer' name="responsibleName" lay-verify=""  class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>后备工程师：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='reservEngineer' name="reserveEnginnerName" lay-verify=""  class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>		
		
			<div class="layui-form-item btn">
				<button type="reset" class="layui-btn dis-n" id="reset">重置</button>
				<a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDemo2">提交</a>
			</div>
			</form>
		</div>
		<!-- 修改 -->
		<div class="dis-n " id="revise">
			<form class="layui-form" action="" lay-filter="example">
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>机器编码：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='machCode' lay-verify="machCode" unselectable='on' readOnly class="layui-input"  autocomplete="off">
						<!-- <input type="hidden"  class="layui-input"> -->
					</div>
				</div>
			</div>				
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>保养频率：</label>
					<div class="layui-input-inline upWidth">
						<select name="mainFrequency">
							<option value="">请选择</option>
							<option value="周">周</option>
							<option value="月">月</option>
							<option value="季度">季度</option>
							<option value="半年">半年</option>
							<option value="年">年</option>
						</select>						
					</div>
				</div>
			</div>		
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>合同编码：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='contractCode' lay-verify="" unselectable='on' readOnly class="layui-input"  autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>客户名称：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='custName' lay-verify="" unselectable='on' readOnly class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>联系人：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='repairMan' lay-verify="" unselectable='on' readOnly class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>联系电话：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='repairService' lay-verify="" unselectable='on' readOnly class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>责任工程师：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='responsibleEngineer' lay-verify="" class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>后备工程师：</label>
					<div class="layui-input-inline upWidth">
						<input type="text" name='reservEngineer' lay-verify="" class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>		
		
			<div class="layui-form-item btn">
				<button type="reset" class="layui-btn dis-n" id="reset">重置</button>
				<a class="layui-btn layui-bg-blue" lay-submit lay-filter="formSub">提交</a>
			</div>
			</form>
		</div>
    </div>
</template>
<script>
import Index from '../../index'
import {maintenancePlan,maintenancePlanByMachCode,addMaintenance,areaManage,deleteMaintenance,updateMaintenance} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',	
			// 合同编码
			contractCode: '',
			custName: '',
			repairMan: '',				
			repairService: '',
			responsibleEngineer: '',
			reservEngineer: '',
			formV: {
				machCode: '',
				mainFrequency: '',
				responsibleName: '',  //责任工程师
				reserveEnginnerName: '',
			},
			addShow: false
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
		selectA(){
			var that = this					
			maintenancePlanByMachCode(that.formV)
			.then((res) => {
				console.log(res)
				if(res.data.code == 0){
					that.contractCode = res.data.data.contractNo
					that.custName = res.data.data.custArea
					that.repairMan = res.data.data.custLinkman
					that.repairService = res.data.data.linkmanPhone
					that.reservEngineer = res.data.data.reserveEnginner
					that.responsibleEngineer = res.data.data.responsibleEngineer
					// that.$layerMsg('查询成功')
				}else{
					that.$layerMsg(res.data.msg)
				}
				
			})
		},
        getData(){
			// var addressSer = sessionStorage.getItem('serviceAddress')
			// console.log(addressSer,"797788")
            var that = this;
			var username = sessionStorage.getItem("username")
			var uRole = sessionStorage.getItem('userRole')
			var unMask = true;
            layui.use(['form','table'], function(){
				var form = layui.form;
				var table = layui.table;
				table.render({
					elem: '#demo'
					,height: 'full-280'
					,cellMinWidth: 120
					,url: that.$url+'maintenancePlan' //数据接口
					,where:{username}
					,method: 'post'
					,page: true //开启分页
					,limit: 20
					,cols: [[ //表头
						{field: '', title: '序号', type:'numbers',minWidth:180,fixed: 'left'}
						,{field: 'custName', title: '客户名称',minWidth:180,fixed: "left"}
						,{field: 'compName', title: '服务区域',minWidth:180}
						,{field: 'contractCode', title: '合同号码',minWidth:180}
						,{field: 'machCode', title: '机器编码'}
						,{field: 'repairMan', title: '联系人'}
						,{field: 'repairService', title: '联系电话',}
						,{field: 'mainFrequency', title: '保养频率'}
						,{field: 'responsibleEngineer', title: '责任工程师'}
						,{field: 'reservEngineer', title: '后备工程师'}
						,{field: 'czx',fixed:'right', title: '操作项',align: 'center',width:120,
							templet: function (d) {
								return '<a class="layui-btn layui-btn-xs" lay-event="exit">修改</a>'
										+'<a class="layui-btn layui-btn-xs layui-btn-danger" lay-event="delete">删除</a>'
							}
						}
					]],
					id:'test'
					,done:function(res){
						that.custNum = res.count
						// console.log(res)
						if(uRole != '运维助理' && uRole != '驻现场人员' && uRole != '客服经理'){
							$("[data-field='czx']").css('display','none');
						}
					}
				})
				form.render();
				//查询
				form.on('submit(formDemo)', function(data){
					$('.layui-icon-close').click();
					var data = JSON.stringify(data.field)
					data = JSON.parse(data)
					var custName = data.custName
					var serviceArea = data.serviceArea
					var username = sessionStorage.getItem("username")
					// console.log(data)
					table.render({
						elem: '#demo'
						,height: 'full-280'
						,cellMinWidth: 120
						,url: that.$url+'maintenancePlan' //数据接口
						,method: 'post'
						,where: {custName,serviceArea,username}
						,page: true //开启分页
						,limit: 20
						,cols: [[ //表头
							{field: '', title: '序号', type:'numbers',minWidth:180,fixed: "left"}
							,{field: 'custName', title: '客户名称',minWidth:180,fixed: "left"}
							,{field: 'compName', title: '服务区域',minWidth:180}
							,{field: 'contractCode', title: '合同号码',minWidth:180}
							,{field: 'machCode', title: '机器编码'}
							,{field: 'repairMan', title: '联系人'}
							,{field: 'repairService', title: '联系电话',}
							,{field: 'mainFrequency', title: '保养频率'}
							,{field: 'responsibleEngineer', title: '责任工程师'}
							,{field: 'reservEngineer', title: '后备工程师'}
							,{field: 'czx',fixed:'right', title: '操作项',align: 'center',width:120,
								templet: function (d) {
									return '<a class="layui-btn layui-btn-xs" lay-event="exit">修改</a>'
											+'<a class="layui-btn layui-btn-xs layui-btn-danger" lay-event="delete">删除</a>'
								}
							}
						]]
						,id: 'test'
						,done:function(res){
							that.custNum = res.count
							// console.log(res)
							if(uRole != '运维助理' && uRole != '驻现场人员' && uRole != '客服经理'){
								$("[data-field='czx']").css('display','none');
							}
						}
					})
					return false;
				});
				form.on('submit(formDemo2)', function(data){
					console.log(data)
					that.formV.mainFrequency = data.field.mainFrequency
					that.formV.responsibleName = data.field.responsibleName
					that.formV.reserveEnginnerName = data.field.reserveEnginnerName
					addMaintenance(that.formV)
					.then((res) => {
						if(res.data.code == 0){
							that.$layerClose('test')
							that.$layerMsg('添加成功')								
						}else{								
							that.$layerMsg(res.data.msg)
						}
					})
					// that.$layerClose('test')
					// $("#reset").click();
					return false;
				})
				
				// 查询服务区域下拉内容
				that.$serviceArea(areaManage)
				
				table.on('tool(demo)',function(obj){
					if(obj.event == 'exit'){
						layer.open({
                            type:1,
                            title: '修改',
                            content:$('#revise'),
                            area:['60%', '70%'],
                            cancel: function(index, layero){ 
                                
                            }  
                        })
						
						form.val('example', {
                            "machCode": obj.data.machCode  // 机器编码
                            ,"mainFrequency": obj.data.mainFrequency  //保养频率
                            ,"contractCode": obj.data.contractCode      //合同编码
                            ,"custName": obj.data.custName   //客户名称
                            ,"repairMan": obj.data.repairMan      //联系人
                            ,"repairService": obj.data.repairService  //联系人电话
                            ,"responsibleEngineer": obj.data.responsibleEngineer  //责任工程师
                            ,"reservEngineer": obj.data.reservEngineer  //后备工程师
                        })
					}else if(obj.event == 'delete'){
						//删除
                        var machCode = {machCode:obj.data.machCode};
                        layer.confirm("确认删除？",function(index){
                            deleteMaintenance(machCode).then((res) => {
                                console.log(res.data.msg)
                                table.reload('test')
                                layer.msg(res.data.msg)
                            })
                            .catch((error) => {				
                            })
                            layer.close(index)
                        })
					}
				})
				form.on('submit(formSub)', function(data){
					var sendData = {
						"machCode": data.field.machCode  // 机器编码
						,"mainFrequency": data.field.mainFrequency  //保养频率
						,"contractCode": data.field.contractCode      //合同编码
						,"custName": data.field.custName   //客户名称
						,"repairMan": data.field.repairMan      //联系人
						,"repairService": data.field.repairService  //联系人电话
						,"responsibleName": data.field.responsibleEngineer  //责任工程师
						,"reserveEnginnerName": data.field.reservEngineer  //后备工程师
					}
					updateMaintenance(sendData).then((res) => {
                        if(res.data.code == 0){
                            layer.closeAll();
                            table.reload('test')
                            layer.msg(res.data.msg)
                        }else if(res.data.code == 1){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }
                    })
                    .catch((error) => {				
                    })
				})
			})			
        },
		exportExcel(){
			var that = this
			that.$exportE('exportmaintenanceMapper')			
		},
		add(){
			var that = this;	
			that.flag = 'add'
			layui.use(['form','table','layer','util'], function(){
				var table = layui.table, layer = layui.layer;
				$("#reset").click();
				var form = layui.form;				
				layer.open({
					type:1,
					title: '新增',
					area: ['60%','70%'],
					content: $('.sdd')
				})							
			})
		},
		showA(){
			var that = this
			var arr = [that.$admin.ywzl,that.$admin.zxcry,that.$admin.kfzg,that.$admin.admin]
			console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
			}
    	},
	},
	created(){
		this.showA()
	},
	destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.sdd .layui-input-inline{
	width: 340px;
}
</style>



