<template>
    <div class="addWindow">
        <!-- <Index/> -->
        <div class="body">
            <a href="javascript:;" @click="back()" class="close"><img src="../../../../static/images/close.svg"></a>
            <div class="addWin">
                <div class='choseWin'>
                    <button class='chosebtn' :class="{boxShadow:isBoxShadow1}" @click="breakdownFun()" v-if="accident">事故报修</button>
                    <button class='chosebtn' :class="{boxShadow:isBoxShadow2}" id="fwbut" @click="demandFun()">服务需求</button>
                </div>
                <!-- 故障报修 -->
                <div class="breakdown" v-if="breakdown">
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>机器编码</label>
                        <div class="layui-input-inline">
                            <input type="text"  class="layui-input" @blur='selectA()' v-model="equipBreak.machCode" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>客户名称</label>
                        <div class="layui-input-inline">
                            <input type="text" unselectable='on' readonly  class="layui-input" v-model="equipBreak.custName" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>设备类型</label>
                        <div class="layui-input-inline">
                            <input type="text" unselectable='on' readonly  class="layui-input" v-model="equipBreak.equipName" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>设备型号</label>
                        <div class="layui-input-inline">
                            <input type="text" unselectable='on' readonly  class="layui-input" v-model="equipBreak.equipType" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>部门</label>
                        <div class="layui-input-inline">
                            <input type="text" unselectable='on' readonly  class="layui-input" v-model="equipBreak.department" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>设备位置</label>
                        <div class="layui-input-inline">
                            <input type="text" unselectable='on' readonly class="layui-input" v-model="equipBreak.location" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>客户地址</label>
                        <div class="layui-input-inline">
                            <input type="text" unselectable='on' readonly class="layui-input" v-model="equipBreak.address" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>报修人/联系人</label>
                        <div class="layui-input-inline">
                            <input type="text" class="layui-input" v-model="formV.repairMan" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>报修人联系电话</label>
                        <div class="layui-input-inline">
                            <input type="text" class="layui-input" v-model="formV.repairService" autocomplete="off">
                        </div>
                    </div>
                    <!-- <div class='divW'>
                        <label class='winLabel'>故障类型</label>
                        <div class="layui-input-inline">
							<select name="" id="" v-model="formV.faultType" class="allinput">
                                <option value=''>请选择</option>
								<option value="a">事故类</option>
								<option value="b">事件类</option>
								<option value="c">需求类</option>
							</select>
                            
                        </div>
                    </div> -->
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>服务类别</label>
                        <div class="layui-input-inline">
                            <input type="radio" name="" class="" @click='wac("a")' v-model="formV.accidentType" value='a'><label>硬件故障</label>
                            <input type="radio" name="" class="" @click='wac("b")' v-model="formV.accidentType" value='b'><label>软件故障</label>
                            <input type="radio" name="" class="" @click='wac("c")' v-model="formV.accidentType" value='c'><label>耗材断供</label>
                        </div>
                    </div>
                    <div class='divW' v-if='flagF'>
                        <label class='winLabel'><span>*</span>耗材型号</label>
                        <div class="layui-input-inline">
                            <input type="text" class="layui-input" v-model="formV.materialModel" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW' v-if='flagF'>
                        <label class='winLabel'><span>*</span>耗材数量</label>
                        <div class="layui-input-inline">
                            <input type="text" class="layui-input" v-model="formV.materialNumber" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel' style="vertical-align: top;">故障描述</label>
                        <div class="layui-input-inline">
                            <!-- <input type="text" v-model="formV.describe"  class="layui-input" autocomplete="off"> -->
                            <textarea v-model="formV.describe" autocomplete="off" class="textWid"></textarea>
                        </div>
                    </div>
                   <!-- <div class='divW'>
                        <label class='winLabel'>保修时间</label>
                        <div class="layui-input-inline">
                            <input type="text" class="layui-input" v-model="equipBreak.department">
                        </div>
                    </div> -->
                   
                </div>
                <!-- 服务需求 -->
                <div class="demand" v-if="demand">
                    <!-- <div class='divW'>
                        <label class='winLabel'>工单号</label>
                        <div class="layui-input-inline">
                            <input type="text" readonly="value"  class="layui-input" v-model="equipBreak.department">
                        </div>
                    </div> -->
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>服务区域</label>
                        <div class="layui-input-inline">
							<select name="" id="" v-model="formVa.serviceArea" class="allinput">
								<option value="" id="">请选择</option>
								<option :value="item.compName" v-for="item in serArea" v-if="zbkf">{{item.compName}}</option> 
								<option :value="item" v-for="item in serArea" v-if="ywzl">{{item}}</option> 
							</select>                                
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>客户名称</label>
                        <div class="layui-input-inline">
							<input type="text" v-model="formVa.custName" @keyup="custNameScreen()" @blur="blurHid()" class="layui-input" autocomplete="off">     
                            <div class="nameData" v-show="isShow">
                                <div>请选择</div>
                                <div v-for="(item,index) in keyupData" :key="index" @click="namecust(item.custName)">{{item.custName}}</div>
                            </div>                   
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>报修人/联系人</label>
                        <div class="layui-input-inline">
                            <input type="text" v-model="formVa.repairMan" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel'><span>*</span>报修人联系电话</label>
                        <div class="layui-input-inline">
                            <input type="text" v-model="formVa.repairService" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                    <div class='divW' >
                        <label class='winLabel'><span>*</span>服务类型</label>
                        <div class="layui-input-inline">
                            <input type="radio" name="" v-model="formVa.faultType" @click='wac2("b")' value='b' class="" v-if="khHide"><label v-if="khHide">事件类</label>
                            <input type="radio" name="" v-model="formVa.faultType" @click='wac2("c")' value='c' class=""><label>需求类</label>
                        </div>
                    </div>
                    <div class='divW' v-if='flag3'>
                        <label class='winLabel'><span>*</span>服务类别</label>
                        <div class="layui-input-inline">
                            <div class="layui-input-inline">
                            <input type="radio" name="" v-model="formVa.accidentType" value='a' class=""><label>保养</label>
                            <input type="radio" name="" v-model="formVa.accidentType" value='b' class=""><label>送货</label>
                            <input type="radio" name="" v-model="formVa.accidentType" value='c' class=""><label>抄表</label>
                            <input type="radio" name="" v-model="formVa.accidentType" value='d' class=""><label>送文件</label>
                            <input type="radio" name="" v-model="formVa.accidentType" value='e' class=""><label>其他</label>
                        </div>
                        </div>
                    </div>
                    <div class='divW' v-if='flag4'>
                        <label class='winLabel'><span>*</span>服务类别</label>
                        <div class="layui-input-inline">
                            <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("a")' value='a' class=""><label>设备变动</label>
                            <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("b")' value='b' class=""><label>耗材补充</label>
                            <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("c")' value='c' class=""><label>安装实施</label>
                            <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("d")' value='d' class=""><label>客户培训</label>
                        </div>                        
                    </div>
					
                    <div class='divW' v-if='flag1'>
                        <label class='winLabel'><span>*</span>设备变动</label>
                        <div class="layui-input-inline">
                            <input type="radio" name="" v-model="formVa.macd" value='a' class=""><label>加机</label>
                            <input type="radio" name="" v-model="formVa.macd" value='b' class=""><label>变更</label>
                            <input type="radio" name="" v-model="formVa.macd" value='c' class=""><label>撤除</label>
                            <input type="radio" name="" v-model="formVa.macd" value='d' class=""><label>移动</label>
                        </div>
                    </div>
                    <div class='divW' v-if='flag2'>
                    	<label class='winLabel'><span>*</span>耗材型号</label>
                    	<div class="layui-input-inline">
                    		<input type="text" class="layui-input" v-model="formVa.materialModel" autocomplete="off">
                    	</div>
                    </div>
					
                    <div class='divW' v-if='flag2'>
                    	<label class='winLabel'><span>*</span>耗材数量</label>
                    	<div class="layui-input-inline">
                    		<input type="text" class="layui-input" v-model="formVa.materialNumber" autocomplete="off">
                    	</div>
                    </div>
                    <div class='divW'>
                        <label class='winLabel' style="vertical-align: top;">需求描述</label>
                        <div class="layui-input-inline">
                            <!-- <input type="text" v-model="formV.describe"  class="layui-input" autocomplete="off"> -->
                            <textarea v-model="formVa.describe" autocomplete="off" class="textWid"></textarea>
                        </div>
                    </div>
                    
                </div> 
				<div class='submitNow'>
					<button @click='addSub()'>{{ subV }}</button>
				</div>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {maintenancePlanByMachCode,doNewOrder,noMachCodedoNewOrder,areaManage,customerInfo} from '../../../config/axiosUtil'
export default {
    name:'AddWindow',
    components: {
        Index
    },
    data(){
        return{
            // serverType:'事故类',
			subV: '立即报修',
            breakdown:true,
            demand:false,
            equipBreak:{
                machCode:'',//机器编码
                custName:'',//客户名称
                equipName:'',//设备类型
                equipType:'',//设备型号
				department: '', //部门                
                location:'',//设备位置
                address:'',//客户地址
            },
			formV: {
				machCode: '',
				repairMan: '',
				repairService: '',
				// faultType: '',
				accidentType: '',
				materialModel:'',// 耗材型号 
				materialNumber: '', // 耗材数量
				username: window.sessionStorage.getItem('username'),
                describe:''
			},
			formVa: {				
				custName: '',
				serviceArea: '',
				repairMan: '',
				repairService: '',
				faultType: '',
				accidentType: '',
				accidentType2: '',
				macd: '',
				materialModel:'',// 耗材型号 
				materialNumber: '', // 耗材数量
				username: window.sessionStorage.getItem('username'),
                describe:''
			},
			flagF: '',
			flag: 'formV',
			flag1: '',
			flag2: '',
			flag3: '',
			flag4: '',
            serArea:[],
            khHide: true,
            accident: true,
            isBoxShadow1: true,
            isBoxShadow2: false,
            zbkf:true,
            ywzl:false,
            keyupData:'',
            isShow: false,
        }
    },
    mounted() {
        //客户权限只有需求类
        var role = sessionStorage.getItem("userRole")
        var custA = sessionStorage.getItem('serviceArea')
        if(role == '客户'){
            this.khHide = false;
            this.wac2("c")
            this.formVa.faultType = 'c'
            this.formVa.serviceArea = custA
        }else if(role == '总部客服'){
            this.khHide = false;
            this.wac2("c")
            this.formVa.faultType = 'c'
        }else if(role == '运维助理'){
            this.accident = false;
            this.breakdown = false;
            this.demand = true
            $("#fwbut").click()
        }

    },
    methods: {
		wac(value){
			if(value == 'c'){					
				this.flagF = true				
			}else{
				this.flagF = false
				this.formV.materialModel = ''
				this.formV.materialNumber = ''
			}
		},	
		wac2(value){
			// alert(value)
			if(value == 'b'){					
				this.flag3 = true	
				this.flag1 = false
				this.flag2 = false
				this.flag4 = false		
				this.formVa.accidentType2 = ''
				this.formVa.macd = ''
				this.formVa.materialModel = ''
				this.formVa.materialNumber	 = ''
			}else if(value == 'c'){
				this.flag3 = false	
				this.flag1 = false
				this.flag2 = false
				this.flag4 = true
				this.formVa.accidentType = ''
				this.formVa.macd = ''
				this.formVa.materialModel = ''
				this.formVa.materialNumber	 = ''
			}
		},		
		wac3(value){
			// alert(value)
			if(value == 'a'){					
				this.flag1 = true	
				this.flag2 = false		
				this.formVa.materialModel = ''
				this.formVa.materialNumber = ''	
			}else if(value == 'b'){
				this.flag1 = false	
				this.flag2 = true
				this.formVa.macd = ''
			}else{
				this.flag1 = false	
				this.flag2 = false
				this.formVa.materialModel = ''
				this.formVa.materialNumber = ''
				this.formVa.macd = ''
			}
		},
        // 输入机器编码带出信息	
		selectA(){
			// alert(this.formV.username)
			var that = this					
			maintenancePlanByMachCode(that.equipBreak)
			.then((res) => {
				console.log(res)
				if(res.data.code == 0){
					that.equipBreak.custName = res.data.data.custArea
					that.equipBreak.equipName = res.data.data.devName
					that.equipBreak.equipType = res.data.data.unitType
					that.equipBreak.department = res.data.data.department
					that.equipBreak.location = res.data.data.location
					that.equipBreak.address = res.data.data.custAddress
					that.formV.machCode = that.equipBreak.machCode
				}else{
					that.$layerMsg(res.data.msg)
				}
				
			})
		},
        // 提交
		addSub(){
			var that = this
			if(that.flag == 'formV'){      //事故报修
                // if(!(/^1\d{10}$/.test(that.formV.repairService))){
                //     that.$layerMsg('手机号格式不正确')
                // }else{
                    doNewOrder(that.formV)
                    .then((res) => {
                    	if(res.data.code == 0){
                    		that.$layerMsg(res.data.msg)
                    		that.$router.push({path:'/home/workMsg',params:{user: '1'}})
                            location.reload()
                    		// that.$router.push({path:'/home/workMsg'})
                    		// that.$router.go(0)
                    	}else{
                    		that.$layerMsg(res.data.msg)
                    	}
                    })
                    .catch((res) => {
                    	that.$layerMsg('请求出错')
                    })
                // }
                // console.log(that.formV)
				
			}else if(that.flag == 'formVa'){      //服务需求
                console.log(that.formVa)
                // if(!(/^1\d{10}$/.test(that.formVa.repairService))){
                //     that.$layerMsg('手机号格式不正确')
                // }else{
                    noMachCodedoNewOrder(that.formVa)
                    .then((res) => {
                        if(res.data.code == 0){
                            that.$layerMsg(res.data.msg)
                            that.$router.push({path:'/home/workMsg',params:{user: '1'}})
                            location.reload()
                            // that.$router.push({path:'/home/workMsg'})
                            // that.$router.go(0)
                        }else{
                            that.$layerMsg(res.data.msg)
                        }
                    })
                    .catch((res) => {
                        that.$layerMsg('请求出错')
                    })
                // }
			}
		},
        breakdownFun(){
			// alert(this.ac)
            var that = this;
            that.breakdown = true;
            that.demand = false;
			that.flag = 'formV'
			that.subV = '立即保修'
            that.isBoxShadow1 = true
            that.isBoxShadow2 = false
        },
        //点击服务需求
        demandFun(){
            var that = this;
            that.demand = true;
            that.breakdown = false;
			that.flag = 'formVa'
			that.subV = '需求提交'
            that.isBoxShadow1 = false
            that.isBoxShadow2 = true
            var userN = {username:sessionStorage.getItem('username')}
            var serviceA = sessionStorage.getItem('serviceArea')
            // 服务区域下拉内容
			// that.$serviceArea(areaManage)
            var uRole = sessionStorage.getItem("userRole")
            var serareaName = {username:sessionStorage.getItem('serviceArea')}
            // console.log(custA)
            if(uRole == '总部客服' || uRole == '运维总监' ){
                that.zbkf = true
                that.ywzl = false
                areaManage().then((res) => {
                    var data = res.data.data;
				    that.serArea = res.data.data
                })
                .catch((error) => {				
                });
            }else if(uRole == '客户'){
                that.zbkf = false
                that.ywzl = true
                that.serArea = serareaName
                console.log(that.serArea)
            }else{
                that.zbkf = false
                that.ywzl = true
                if(serviceA == '广州乐派数码科技有限公司' || serviceA == '行业客户部' || serviceA == '系统推进部'){
                    that.serArea = ['广州乐派数码科技有限公司','行业客户部','系统推进部']
                    
                }else{
                    that.serArea = serareaName
                }
                
                console.log(that.serArea)
                
            }	
            // areaManage(userN).then((res) => {
            //     var data = res.data.data;
			// 	that.serArea = res.data.data
            // })
            // .catch((error) => {
            // })
        },
		prop(){
			// var a = this.$route.query.ab
			// alert(a)
		},
        back(){
            this.$router.back(-1)
            // this.$router.push({path:'/home/workMsg'})
            // location.reload()
        },
        //客户名称筛选
        custNameScreen(){
            var that = this;
            that.isShow = true;
            var custNames ={
                custName:that.formVa.custName
                } ;
            customerInfo(custNames).then((res)=>{
                console.log(res)
                that.keyupData = res.data.data
                console.log(that.keyupData)
            }).catch((res) => {
                // that.$layerMsg('请求出错')
            })
        },
        namecust(inx){
            console.log(inx)
            this.formVa.custName = inx
            this.isShow = false;
        },
        blurHid(){
            var that = this;
            setTimeout(() => {
                that.isShow = false;
            }, 300);
        },
    },
	created(){
		this.prop()
	}
}
</script>
<style lang="">
    .addWin{
        width:80%;
        height:87%;
        padding:3% 10%;
        background:#fff;
        overflow: auto;
    }
    .chosebtn{
        width: 200px;
        height: 38px;
        line-height: 38px;
        margin-left: 20px;
        background-color: #009dbb;
        border: none;
        color: #fff;
        border-radius: 5px;
        
    }
    .boxShadow{box-shadow: 3px 3px 6px #999;}
    .breakdown{
        margin-top: 50px;
    }
    .demand{
        margin-top: 50px;
    }
    .winLabel{
        display: inline-block;
        width: 130px;
        height: 38px;
        line-height: 38px;
        text-align: right;
        padding: 0 10px;
    }
    .submitNow{
        margin: 50px 180px;

    }
    .submitNow button{
        background-color: #009db1;
        border: none;
        width: 130px;
        height: 35px;
        line-height: 35px;
        color: #fff;
        font-size: 16px;
    }
    .close{
        position: absolute;
        right: 50px;
        top: 20px;
        background: #fff !important;
    }
    .close img{
        width: 30px;
        height: auto;
    }
    .divW{
        margin-bottom: 15px;
    }
    .divW input[type=text],.divW select{
        width: 300px;
    }
    .textWid{
        width: 300px;
        height: 80px;
    }
    .nameData{
        position: absolute;
        z-index: 999;
        background: #fff;
        width: 300px;
        border: 1px #ddd solid;
    }
    .nameData>div{
        height: 30px;
        line-height: 30px;
    }
    .nameData>div:hover{
        background: #009fe8;
        cursor: pointer;
    }
</style>



