<script>
  import {permissions} from '../config/axiosUtil'
export default {
  name: 'Index',
  data () {
    return {
        msg: '',
        items: '',
        name: '',
        assistant: false,
        client: false,
        majordomo: false,
        operations: false,
        serviceHome: false,
		customerShow: '',
    }
  },
  mounted () {
      this.getData();
    //   if(this.name == undefined){
    //       this.$router.push({path:'/'})
    //     }
  },
  methods : {
    getData(){
        var that = this;
    	var name = window.sessionStorage.getItem('username');
    	var roleName = sessionStorage.getItem('userRoleA');
        // console.log(roleName)
    	var v = {
    		username : name
    	}
    	permissions(v)
    	.then((res) => {
    		this.items = res.data.data
			this.name = res.data.name
    		// console.log(this.items)
    	})
        //运维助理ywzl、住现场人员zxcry、客服经理kfjl
        if(roleName == that.$admin.ywzl ||roleName == that.$admin.zxcry ||roleName == that.$admin.kfjl){
            // this.$router.push({path:'/home/assistant'})
            that.assistant = true;
        }else if(roleName == that.$admin.kh){           //客户
            // this.$router.push({path:'/home/client'})
			that.customerShow = true
			// that.items.homepage = false
            that.client = true;
        }else if(roleName == that.$admin.zbkf){         //总部客服
            // this.$router.push({path:'/home/service'})
            that.serviceHome = true;
        }else if(roleName == that.$admin.ywzj ||roleName == that.$admin.admin){     //运维总监ywzj、超级管理员admin
            // this.$router.push({path:'/home/majordomo'})
            that.majordomo = true;
        }else if(roleName == that.$admin.ywjl ||roleName == that.$admin.jszg){      //运维经理ywjl、技术主管jszg
            // this.$router.push({path:'/home/operations'})
            that.operations = true;
        }
    },
		exit(){
			var that = this
			layui.use(['form','table'], function(){
				var form = layui.form;
				var table = layui.table;
				layer.confirm('确定退出登录么？', {
					btn: ['确定','取消'] //按钮
					}, function(index){
						// sessionStorage.removeItem('username')
						// sessionStorage.removeItem('userRole')
                        sessionStorage.clear();
                        // console.log(sessionStorage.getItem("username"))
                        // console.log(sessionStorage.getItem("userRole"))
						that.$router.push({path: '/'})
						layer.closeAll();
					}, function(){
						
				});
			})
			

		}
  },
  created (){
    
    
  }
}
</script>

