<template>
<!-- 坐席客服kpi -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;坐席客服KPI&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">客服姓名</label>
                    <div class="layui-input-block">
                        <input type="text" name="name" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">起始时间</label>
                    <div class="layui-input-block">
                        <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">终止时间</label>
                    <div class="layui-input-block">
                        <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
            </div>
            <div class="btn">
                <a class="layui-btn layui-bg-blue"  lay-submit lay-filter="formDome" >查询</a>
                <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出详细报表</a>
            </div>
            </form>
        </div>
        <div class="tab">
            <table id="demo" lay-filter="demo"></table>
        </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'

export default {
    name: "Service",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','form','laydate'], function () {
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                //日期
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                form.render()
                // 详细报表
                table.render({
                    elem: '#demo'
                    , height: 'full-300'
                    , cellMinWidth: 150
                    , url: that.$url+'seatServiceSummary' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width:80}
                        , {field: 'staffName', title: '客服姓名'}
                        , {field: 'chiefSendTimeRate', title: '受理时间达标率',
                            templet:function(d){
                                return intercept(d.chiefSendTimeRate)
                            }
                        }
                        , {field: 'orderNum', title: '工单数量'}
                        , {field: 'custComplaints', title: '客户投诉（次）'}
                    ]]
                    ,id: 'testReload'
                });
                //概率保留整数
                function intercept(data){
                    var datas = data.split('%')[0]
                    return Math.round(datas)+'%'
                }
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'seatServiceSummary' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            staffName : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
        },
        exportExcel(){
			var that = this
			that.$exportE('exportSeatService')		
		}
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



