<template>
    <!-- 合同概况 -->
    <div id="contract">
        <!-- <Index/> -->
        <div class="body">
		<div class="titleLab">&gt;&gt;合同概况&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域：</label>
                        <div class="layui-input-block">
                            <!-- <input type="text" name="area" v-model='v.childService' required  lay-verify="required" placeholder="" autocomplete="off" class="layui-input"> -->
							<select name="address" lay-verify="">
                                <option value=""  id="address"></option>
                            </select>
                        </div>
                    </div>
                </div>
				<div class="btn">
					<a class="layui-btn layui-bg-blue" lay-filter="formDome" lay-submit="">查询</a>
					<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
				</div>
            </form>
            
            <div class="labnum">
                <label>合同总数：&nbsp;&nbsp;{{ item.count1 }}&nbsp;&nbsp;</label>
                <label>即将到期合同：<span>&nbsp;&nbsp;{{ item.count2 }}&nbsp;&nbsp;</span></label>
                <label>已到期合同：&nbsp;&nbsp;<span>{{ item.count3 }}</span>&nbsp;&nbsp;</label>
                <label>续期合同：&nbsp;&nbsp;{{ item.count4 }}&nbsp;&nbsp;</label>
            </div>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="test"></table>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {contractSituation} from '../../../config/axiosUtil'

export default {
    name: "Contract",
    components: {
        Index
    },
	data () {
		return {
			v : {
				childService : '',
				timeOut: '',
				dueTo: ''
			},
			item: {
				count1: '1',
				count2: '2',
				count3: '3',
				count4: '4'
			}			
		}
	},
    mounted() {
        this.getData();
    },
    methods: {
		// 获取合同概况数据
        getData(){
			var that = this;
			var uname = sessionStorage.getItem("username")
            layui.use(['table','form','util'], function(){
				var table = layui.table,form = layui.form;
				table.render({
					elem: '#demo'
					,height: 'full-320'
					,cellMinWidth: 120
					,url: that.$url+'contractSituation' //数据接口
					,where:{username:uname}
					,page: true //开启分页
					,cols: [[ //表头
						{field: 'id', title: '序号', sort: true,type:'numbers',width:80}
						,{field: 'custName', title: '客户名称'}
						,{field: 'mainService', title: '服务区域'}
						,{field: 'city', title: '合同名称'}
						,{field: 'contractNo', title: '合同编码',}
						,{field: 'contractType', title: '合同类型'}
						,{field: 'score', title: '合同性质'}
						// ,{field: 'bxr', title: '登记时间'}
						// ,{field: 'handlingDepartment', title: '经办部门'}
						// ,{field: 'agent', title: '经办人'}
						// ,{field: 'contractHoldman', title: '合同保管人'}
						,{field: 'startDate', title: '签约日期',templet:"<div>{{layui.util.toDateString(d.startDate,'yyyy-MM-dd')}}</div>"}
						,{field: 'endDate', title: '到期时间',templet:"<div>{{layui.util.toDateString(d.endDate,'yyyy-MM-dd')}}</div>"}
						,{field: 'dueDays', title: '离到期天数'}
						,{field: 'contractDeadline', title: '期限(月)',
							templet:function(d){
								var time = Math.round(d.contractDeadline);
								return "<div>"+ time +"</div>"
							}
						}
						,{field: 'dueDays', title: '合同到期预警', 
							templet: function(d){
								return getdueDays(d.dueDays)
							}
						}
						,{field: 'bz', title: '外包所服务客户'}
						// ,{field: 'bz', title: '合同标的金额'}
						// ,{field: 'bz', title: '合同执行情况'}
						// ,{field: 'bz', title: '合同终止情况'}
						,{field: 'bz', title: '提前终止日'}
						// ,{field: 'bz', title: '提前终止情况说明'}
						// ,{field: 'bz', title: '移交归档日期'}
						// ,{field: 'bz', title: '接交人'}
						,{field: 'bz', title: '备注'}
					]]
					,id: 'testReload'
					,parseData: function(res){
                        // console.log(res)
						that.item.count1 = res.count;
						that.item.count2 = res.count2;
						that.item.count3 = res.count3;
						that.item.count4 = res.count4;
                        return {
                            "code": res.code, //解析接口状态
                            "count": res.count, //解析数据长度
                            "data": res.data //解析数据列表
                        };
                    }
				});
				
				//服务区下拉内容
				contractSituation().then((res) => {
					console.log(res)
					var data = res.data.data;
					var serverArea = []
					for(var i=0; i<data.length; i++){
						if(serverArea.indexOf(data[i].mainService) == -1){
							serverArea.push(data[i].mainService);
						}
					}
					for(var j=0; j<serverArea.length; j++){
						console.log(serverArea[j])
						$('#address').after('<option value=\''+ serverArea[j] +'\'  v-model="v.childService">'+ serverArea[j] +'</option>');
					}
					form.render()
				})
				.catch((error) => {
					// console.log(error)
				}) 
				// 查询
                form.on('submit(formDome)',function(data){
					$('.layui-icon-close').click();
                    var data = data.field;
                    // console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'contractSituation' ,//数据接口
						method: 'post'
                        ,where: {
                            childService : data.address, //服务区域
							username : uname
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                }) 
			});
			function getdueDays(data){
				console.log(data);
				if(data < '185'){
					return '<span style="color: #f00">'+ data +'</span>'
				}else{
					return '<span>'+ data +'</span>'
				}
			};
			// function getTimeRount
			
        },
		// 导出合同
		exportExcel(){
			var that = this
			that.$exportE('exportContractSituation')	
			// window.open(this.$url+'/exportContractSituation?custName='+this.v.childService)
		}
    },
	destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#contract{
    width:85%;
    
}
.labnum>label>span{
	color: #f00;
	font-size: 14px;
    vertical-align: middle;
}
</style>



