<template>
<!-- 运维总监登陆后首页 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <div class="up">
                <div class="bigpic" ><img src="../../../../static/images/xuwei.png" style="width: 100%;height: 398px;" ref="reference"  id="chart"/>
                    <!-- <div class="bigpic-font"><p>持续改善、优质运维、客户满意</p></div> -->
                </div>
                <div class="left-o">
                    <!--月度报告-->
                    <p>月度报告</p>
                    <div class="pie" :style="{'height':height}">
                        <img src="../../../../static/images/pic.png" height="" width=""/>
                    </div>
                </div>
            </div>
            <div class="layui-clear" style="margin-top: 15px;width:97%;margin-left:2%;">
                <!--合同管理-->
                <dl class="contract-three">
                    <dd class="title">合同管理</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link :to="{name: 'ContractMsg',params:{woStatus: 'a'}}">合同总数</router-link></dd>
                            <dd><span class="content">{{htzs}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractMsg',params:{woStatus: 'b'}}">已到期</router-link></dd>
                            <dd><span class="content">{{ydqht}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractMsg',params:{woStatus: 'c'}}">一年内到期</router-link></dd>
                            <dd><span class="content">{{ynndq}}</span></dd>
                        </dl>
                    </dd>
                </dl>
                <!--工单概况-->
                <dl class="contract-three">
                    <dd class="title">工单概况</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'a'}}">本月工单</router-link></dd>
                            <dd><span class="content">{{bygd}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'b'}}">未完成</router-link></dd>
                            <dd><span class="content">{{wwc}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'c'}}">超时工单</router-link></dd>
                            <dd><span class="content">{{csgd}}</span></dd>
                        </dl>
                    </dd>
                </dl>
                <!--客户满意度-->
                <dl class="contract-three">
                    <dd class="title">客户满意度</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'a'}}">用户评价</router-link></dd>
                            <dd><span class="content">{{ yhpj }}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'b'}}">客户投诉</router-link></dd>
                            <dd><span class="content">{{khts}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'c'}}">客户表扬</router-link></dd>
                            <dd><span class="content">{{khby}}</span></dd>
                        </dl>
                    </dd>
                </dl>
            </div>
            
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {majordomo} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            ynndq:'',//一年内到期合同
            htzs:'',//合同总数
            khts:'',//客户投诉
            khby:'',//客户表扬
            ydqht:'',//已到期合同
            bygd:'',//本月工单
            csgd:'',//超时工单
            wwc:'',//未完成
            yhpj:'',//用户评价
            height: '',  //div高度
            lineHeight: '',
        }
    },
    mounted() {
        this.getData();
        // this.reflashPage();
        var that = this;
        var heightCss = $('#chart').outerHeight();
        this.height = heightCss - '79' +'px';
        this.lineHeight = heightCss - '79' +'px';
        console.log(heightCss)
        // console.log(height)
        // let scrollHight = window.getComputedStyle(that.$refs.reference).height;
        // that.height = scrollHight - '79' +'px';
        // that.lineHeight = scrollHight - '79' +'px';
        // console.log(scrollHight,"11111")
        // let aa = that.$refs.reference;
        // console.log(aa,"aa")
        // var scrollHight = this.$refs.reference.style;
        
        // console.log(scrollHight)
        // console.log(this.height)
        // $(window).load(function() {
        //     var boxH = $('#chart').outerHeight();
        //     that.height = boxH - '79' +'px';
        //     that.lineHeight = boxH - '79' +'px';
        //     console.log(that.lineHeight,"7777777")
        // });
    },
    created() {
        
    },
    methods:{
        reflashPage(){
            let NewPage = '_empty' + '?time=' + new Date().getTime()/500;
            this.$router.push(NewPage);
            this.$router.go(-1);
        },
        getData(){
            var that = this;
            majordomo().then((res) => {
                that.ynndq = res.data.一年内到期合同;
                that.htzs = res.data.合同总数;
                that.khts = res.data.客户投诉;
                that.khby = res.data.客户表扬;
                that.ydqht = res.data.已到期合同;
                that.bygd = res.data.本月工单;
                that.csgd = res.data.超时工单;
                that.wwc = res.data.未完成;
                that.yhpj = res.data.用户评价;
            })
            .catch((error) => {				
            })
        }
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.subordinate dd a{
    text-decoration: underline;
}
</style>



