<template>
    <!-- 员工信息 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
			<div class="titleLab">&gt;&gt;员工信息&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">				
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block allinput">
                        <select name="branceName" lay-verify="">
							<option value="" id="address">请选择</option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">姓名</label>
					<div class="layui-input-block allinput">
						<input type="text" name="name" lay-verify="name" class="layui-input" autocomplete="off">
					</div>                   
                </div>
                
                <div class="layui-inline">
                    <label class="layui-form-label">角色</label>
                    <div class="layui-input-block allinput">
						<select name="role" lay-verify="role" lay-search="">
							<option value="">请选择</option>
							<option value="运维经理">运维经理</option>
							<option value="技术主管">技术主管</option>
							<option value="总部客服">总部客服</option>
							<option value="工程师">工程师</option>		
							<option value="驻现场人员">驻现场人员</option>								
							<option value="运维助理">运维助理</option>								
							<option value="客服经理">客服经理</option>								
							<option value="运维总监">运维总监</option>	
						</select>
					</div>
                </div>
				<div class="btn">
					<!-- <button class="layui-btn layui-bg-blue" lay-submit lay-filter="formDemo" @click='selectAll()'>查询</button> -->
					<a class="layui-btn layui-bg-blue" lay-filter="formDome" lay-submit="">查询</a>
					<a class="layui-btn layui-bg-blue" v-if='addShow' @click='add()'>新增</a>
					<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
				</div>
            </form>
            
            <div class="labnum">
                <label>总数：&nbsp;&nbsp;{{number.count}}&nbsp;&nbsp;</label>
            </div>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <script type="text/html" id="barDemo">
                <!-- <a class="layui-btn layui-btn-xs" lay-event="edit">编辑</a>
                <a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="del">删除</a> -->
            </script>
        </div>
		<!-- 新增/修改 -->
		<div id="increased" class="dis-n sdd">			
				<!-- <div class="layui-form" action=""> -->
					<div class="layui-form-item">
						<div class="layui-inline" style="margin-top: 20px">
						    <label class="layui-form-label"><span>*</span>员工编号：</label>
							<div class="layui-input-block">
								<input type="text" v-model='formV.staffId' placeholder="员工编号例:XW0000" lay-verify="staffId" class="layui-input" autocomplete="off">
							</div>                   
						</div>
					</div>
					<div class="layui-form-item">						
						<div class="layui-inline">
								<label class="layui-form-label"><span>*</span>姓名：</label>
								<div class="layui-input-inline">
										<input type="text" v-model="formV.staffName" lay-verify="staffName" class="layui-input" autocomplete="off">
								</div>
						</div>
					</div>
					
					<div class="layui-form-item">
							<div class="layui-inline">
									<label class="layui-form-label"><span>*</span>电话：</label>
									<div class="layui-input-inline">
											<input type="text" v-model="formV.phoneNumber" lay-verify="newLink" class="layui-input" autocomplete="off">
									</div>
							</div>
					</div>
					<div class="layui-form-item">
							<div class="layui-inline">
									<label class="layui-form-label"><span>*</span>服务区域：</label>
									<div class="layui-input-block allinput">
										<select v-model='formV.serviceArea' lay-search="" class="allinput" id="sel">
											<option value="" id="serArea">请选择</option>
											<option :value="item.compName" v-for="item in serArea">{{item.compName}}</option>
										</select>
									</div>
							</div>
					</div>
					<div class="layui-form-item">
							<div class="layui-inline">
									<label class="layui-form-label"><span>*</span>角色：</label>									
									<div class="layui-input-block allinput">
										<select v-model="formV.role" lay-search="" class="allinput">
											<option value="">请选择</option>
											<option value="运维经理">运维经理</option>
											<option value="技术主管">技术主管</option>
											<option value="总部客服">总部客服</option>
											<option value="工程师">工程师</option>		
											<option value="驻现场人员">驻现场人员</option>								
											<option value="运维助理">运维助理</option>								
											<option value="客服经理">客服经理</option>								
											<option value="运维总监">运维总监</option>								
										</select>
									</div>
							</div>
					</div>	
					<!-- <div class="layui-form-item">
						<div class="layui-inline">
							<label class="layui-form-label"><span>*</span>员工编号：</label>
							<div class="layui-input-inline">
								<input type="text" v-model="formV.staffId" lay-verify="" class="layui-input">
							</div>
						</div>
					</div> -->
					<div class="layui-form-item">
						<div class="layui-inline">
							<label class="layui-form-label"><span>*</span>涉密资质：</label>									
							<div class="layui-input-block selHeight">
								<label>是</label><input type="radio" v-model="formV.secret" name='a' @click='wac("true")' value='true'/>
								<label>否</label><input type="radio" v-model="formV.secret" name='a' @click='wac("false")' value="false"/>
							</div>
						</div>
					</div>	
					<div class="layui-form-item" v-if="issecret">
						<div class="layui-inline">
							<label class="layui-form-label"><span>*</span>涉密等级：</label>									
							<div class="layui-input-block selHeight">
								<label>秘密</label><input type="radio" v-model="formV.secretLevel" name='b'  value='1'/>
								<label>机密</label><input type="radio" v-model="formV.secretLevel" name='b'  value="2"/>
								<label>绝密</label><input type="radio" v-model="formV.secretLevel" name='b'  value="3"/>
							</div>
						</div>
					</div>		
						
					<div class="layui-form-item btn">
							<button class="layui-btn layui-bg-blue" @click="subMit(flag)">提交</button>
							<button class="layui-btn layui-bg-blue dis-n" @click='reset()'>重置</button>
					</div>
				<!-- </div> -->
		</div>
    </div>
</template>
<script>
import Index from '../../index'
import {deleteStaff,doAddManager,updateStaff,userManageInfo,areaManage} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
			formV:{
				staffId : '',
				staffName : '',
				serviceArea : '',
				role : '',
				phoneNumber : '',
				secret: false,
				secretLevel: '',
				// secretClass: '',						
			},
			issecret: '',
			// issecretLevel: '',
			// issecretClass: '',
			item:'',
			flag : '1',		
			number:{
				count: ''
				
			},
			addShow: false,
			serArea:[],
			limita: 0,		
        }
    },
    mounted() {
        this.getData()
    },
	watch: {			
	},
    methods:{
		wac(value){
			if(value == 'true'){					
				this.issecret = true
				this.formV.secretLevel = 1
			}else{
				this.issecret = false
				this.formV.secretLevel = false
			}
			console.log(this.formV.secret)
			console.log(this.formV.secretLevel)
		},
        getData() {
            var that = this;
			var uname = sessionStorage.getItem("username")
			var uRole = sessionStorage.getItem("userRole")
			var unMask = true;
            layui.use(['table','form'], function(){
                var table = layui.table,form = layui.form;
                form.render();
                table.render({
                    elem: '#demo'
                    ,height: 'full-320'
                    ,cellMinWidth: 180
                    ,url: that.$url+'userManageInfo' ,//数据接口
					method: 'post'
					,where:{username:uname}
                    ,page: true //开启分页
					,limit: 20
                    ,cols: [[ //表头
                        {field: 'staffId', title: '员工编号', }
                        ,{field: 'compName', title: '服务区域', minWidth:277}
                        ,{field: 'name', title: '姓名'}
                        ,{field: 'phone', title: '电话'}
                        ,{field: 'post', title: '角色'}
                        ,{field: 'secret', title: '是否涉密'}
                        ,{field: 'secretLevel', title: '涉密等级'},
                        ,{field: 'createDate', title: '创建时间',
							templet:function(d){
								if(d.createDate == null){
									return ''
								}else{
									var insDate = layui.util.toDateString(d.createDate,'yyyy-MM-dd')
									return "<div>"+insDate+"</div>"
								}
							}
							// "<div>{{layui.util.toDateString(d.createDate,'yyyy-MM-dd')}}</div>"
						}
                        ,{field: 'czx',fixed: 'right', title: '操作项',width: 130,
							templet: function (d) {
								var arr2 = ''
								return that.isShow(d);
							}
						}
                    ]]
					,id: 'test'
					,done: function(res){
						that.number.count = res.count;
						console.log(res)
						if(uRole != '运维助理' && uRole != '驻现场人员' && uRole != '总部客服' && uRole != '客服经理'){
							$("[data-field='czx']").css('display','none');
						}
					}
                });
				// 查询服务区域下拉内容
				that.$serviceArea(areaManage)
				// 查询
				form.on('submit(formDome)',function(data){
                    var data = data.field;
                    // console.log(data)
					$('.layui-icon-close').click();
                    table.reload('test', {
                        url: that.$url+'userManageInfo' ,//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            name : data.name,//员工姓名
                            branceName : data.branceName,//服务区域
                            role : data.role, //角色 branceName,name,role
							username : uname
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })

				table.on('tool(demo)', function(obj){
					var data = obj.data
					// console.log(data)
					if(obj.event == 'del'){
						var v = {
							staffId : data.staffId
						}
						layer.confirm('确定删除员工信息吗?', function(index){
							deleteStaff(v)
							.then((res) => {
								if(res.data.code == 0){
									layer.msg('删除成功', {icon: 1});
									obj.del();
									layer.close(index);
									that.getData()
								}else{
									layer.msg('删除失败', {icon: 2});
									// that.getData()
								}
							})
							.catch((error) => {
								// layer.msg('我的天呐，网炸了！')
							})						
						});
					}else if(obj.event === 'edit'){
						// console.log(data)
						layer.open({
							type: 1,
							title: '编辑',
							area: ['60%','70%'],
							content: $('.sdd')
						});	
						var userN = {username:sessionStorage.getItem('username')}
						// console.log(userN)
						areaManage(userN).then((res) => {
							var data = res.data.data;
							that.serArea = res.data.data
						})
						.catch((error) => {
						})
						that.flag = 'edit'
						that.formV.staffId = data.staffId
						that.formV.staffName = data.name
						that.formV.serviceArea= data.compName
						// console.log(that.formV.serviceArea)
						// console.log(data.compName)
						// console.log("广州乐派数码科技有限公司")
						$("#sel").val = data.compName
						
						that.formV.role = data.post
						that.formV.phoneNumber = data.phone	
						if(data.secret === '是'){
							that.formV.secret = true
							that.issecret = true							
						}else{							
							that.formV.secret = false
							that.issecret = false
						}
						if(data.secretLevel == '秘密'){
							that.formV.secretLevel = 1		
						}else if(data.secretLevel == '机密'){
							that.formV.secretLevel = 2	
						}else if(data.secretLevel == '绝密'){
							that.formV.secretLevel = 3
						}else{
							that.formV.secretLevel = false
						}
						
						this.flag = 'edit'
						
											
					}
				})
            });
        },
		add(){
			var that = this;
			that.flag = 'add'
			that.formV.staffId = ''
			that.formV.staffName = ''
			that.formV.serviceArea = ''
			that.formV.role = ''
			that.formV.phoneNumber = ''			
			that.formV.secret = false
			that.issecret = false
			that.formV.secretLevel = true
			// that.issecretLevel = true
			// that.formV.secretClass = true	
			var userN = {username:sessionStorage.getItem('username')}
						// console.log(userN)
						areaManage(userN).then((res) => {
							var data = res.data.data;
							that.serArea = res.data.data
						})
						.catch((error) => {
						})
			layui.use(['form','table','layer'], function(){
				var table = layui.table, layer = layui.layer;
				var form = layui.form;			
				layer.open({
					type: 1,
					title: '添加',
					area: ['60%','70%'],
					// shade: 0,
					// shadeClose: true,
					content: $('.sdd')
				});	
				
			})	
			return false;
		},
		
		// 修改新增提交
		subMit(str){
			var that = this;
			// console.log(this.formV)
			if(str == 'edit'){
				// if(!(/^1\d{10}$/.test(that.formV.phoneNumber))){
				// 	that.$layerMsg('手机号不正确')
				// 	that.formV.phoneNumber = ''
				// }else{
					updateStaff(this.formV).then((res) => {
						if(res.data.code == 0){
							that.$layerClose('test')
							that.$layerMsg('修改成功')
						}else if(res.data.code == 1){
							// alert(1)
							that.$layerMsg(res.data.msg)
						}						
					}).catch((error) => {
						// layer.msg('我的天呐，网炸了！')
					})
				// }
				
			}else if(str == 'add'){		
				console.log(that.formV)
				if(that.formV.staffId == ''){
					that.$layerMsg('员工编号不能为空')
				}else if(!(/^\XW\d{4}/.test(that.formV.staffId))){
					that.$layerMsg('员工编号格式不正确')
					that.formV.staffId = ''
				}
				// else if(!(/^1\d{10}$/.test(that.formV.phoneNumber))){
				// 	that.$layerMsg('手机号不正确')
				// 	that.formV.phoneNumber = ''
				// }
				else{
					doAddManager(this.formV).then((res) => {
						if(res.data.code == 0){
							that.$layerClose('test')
							that.$layerMsg('添加成功')
							that.getData()
						}else if(res.data.code == 1){
							that.$layerMsg(res.data.msg)
						}						
					}).catch((error) => {
						// layer.msg('我的天呐，网炸了！')
					})
				}
				
			}
			return false;
		},
		reset(){
			var that = this
			for(var k in that.formV){
				that.formV[k] = ''
			}
		},
		exportExcel(){
			var that = this;
			that.$exportE('exportStaff')
		},	
		showA(){
			var that = this
			var arr = [that.$admin.ywzl,that.$admin.zxcry,that.$admin.kfzg,that.$admin.zbkf,that.$admin.admin]
			console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
			}
		},	
		isShow(){
			var that = this			
			var arr2 = that.$admin
			var admin = ''
			if(arr2.zbkf == that.$userRole || arr2.admin == that.$userRole || arr2.zxcry == that.$userRole|| arr2.kfzg == that.$userRole|| arr2.ywzl == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="edit">编辑</a><a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="del">删除</a>'					
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
			}
			return admin;
			
		},
    },
	created(){
		this.showA()
    },
	destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.sdd .layui-form-item{
	width: 80%;
	margin: 0 auto;
}
.sdd .layui-form-item .layui-inline .layui-input-inline{
	width: 300px;
}
.sdd .layui-form-item .layui-input-block{
	width: 300px;
}
.sdd .layui-form-item .layui-input-block select{
	width: 300px !important;
}
.btn{
	margin-bottom: 20px !important;
}
</style>


