<template>
<!-- 反馈与意见 -->
    <div id="feedback">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;反馈与建议&lt;&lt;</div>
            <div class="feed" v-for="(text,index) in datas" :key="index">
                <div class="info">
                    <label>客户名称</label><span>{{text.custName}}</span>
                    <label>联系人</label><span>{{text.id}}</span>
                    <label>联系电话</label><span>{{text.custPhone}}</span>
                    <label>时间</label><span>{{text.feedbackTime}}</span>
                </div>
                <div class="feedCon">
                    <label>反馈内容</label>
                    <div class="evalCon">{{text.content}}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {selectAllAdvice} from '../../../config/axiosUtil'

export default {
    name: "Feedback",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            datas:[]
        }
    },
    mounted() {
       this.getData();
    },
    methods: {
        getData(){
            var that = this;
             selectAllAdvice().then((res) => {
                var data = res.data.data;
                // console.log(data);
                // for(var i = 0; i < data.length; i++){
                //     console.log(data[i].feedbackTime)
                // }
                that.datas = data;
                console.log(that.datas)
            })
            .catch((error) => {				
            });
        }
    },
}
</script>
<style scoped>
    #feedback{
        width: 85%;
    }
    .feed{
        width: 86%;
        margin: 20px auto;
        background: #fff;
        padding: 20px;
        border-radius: 5px;
    }
    .feed label{
        margin-left: 50px;
        font-size: 16px;
        color: #555;
    }
    .info span{
        color: #009953;
        font-size: 16px;
        margin-left: 10px;
    }
    .feedCon{
        margin-top: 10px;
        /*padding-bottom: 10px;
        border-bottom: 1px solid #999;*/
    }
    .evalCon{
        width:86%;
        height: 100px;
        background: #fff;
        overflow: auto;
        border: solid 1px #999;
        padding: 2%;
        margin: 10px auto;
        color: #009953;
        font-size: 16px;
    }
</style>


