<template>
    <!-- 工程师在岗状态 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
		<div class="titleLab">&gt;&gt;在岗状态&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
                <div class="layui-inline">
                    <label class="layui-form-label">职位</label>
                    <div class="layui-input-block">
                        <select name="post" lay-verify="" class="allinput">
                            <option value="" id="address">请选择</option>
                            <option value="运维助理">运维助理</option>
                            <option value="驻现场人员">驻现场人员</option>
                            <option value="客服经理">客服经理</option>
                            <option value="总部客服">总部客服</option>
                            <option value="运维总监">运维总监</option>
                            <option value="运维经理">运维经理</option>
                            <option value="技术主管">技术主管</option>
                            <option value="工程师">工程师</option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">姓名</label>
                    <div class="layui-input-block">
                        <input type="text" name="name"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
				<div class="btn">
					<a class="layui-btn layui-bg-blue" lay-submit="" lay-filter='formDemo'>查询</a>
					<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
				</div>
            </form>
            </div>

            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <script type="text/html" id="barDemo">
                <!-- <a class="layui-btn layui-btn-xs" lay-event="edit">修改</a> -->
                <!-- <a class="layui-btn layui-btn-xs layui-btn-danger" lay-event="del">删除</a> -->
            </script>
        </div>
		<!-- 修改在岗状态 -->
		<div class="dis-n sdd2">
			<!-- <form class="layui-form" action=""> -->
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>员工编号：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='formV.id' unselectable='on' readonly="readonly" lay-verify="machCode" class="layui-input"  autocomplete="off">
						<!-- <input type="hidden"  class="layui-input"> -->
					</div>
				</div>
			</div>				
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>姓名：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='formVa.name' unselectable='on' readonly="readonly" lay-verify="" class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>职位：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='formVa.post' unselectable='on' readonly="readonly" lay-verify="" class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>工作状态：</label>
					<div class="layui-input-inline">
						<select v-model='formV.workStatus' class="allinput">				
							<option value="在岗">在岗</option>
							<option value="休假">休假</option>
							<option value="培训">培训</option>							
						</select>						
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>电话：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='formVa.phone' unselectable='on' readonly="readonly" lay-verify="" class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>
			<div class="layui-form-item">
				<div class="layui-inline">
					<label class="layui-form-label addInput"><span>*</span>备注：</label>
					<div class="layui-input-inline">
						<input type="text" v-model='formV.remark'  lay-verify="" class="layui-input" autocomplete="off">
					</div>
				</div>
			</div>					
		
			<div class="layui-form-item btn">
				<!-- <button type="reset" class="layui-btn dis-n" id="reset">重置</button> -->
				<a class="layui-btn layui-bg-blue" @click='subMit()'>提交</a>
			</div>
			<!-- </form> -->
		</div>
    </div>
</template>
<script>
import Index from '../../index'
import {deletaEngineer,doUpdateEngineer} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
			formV: {
				id: '',
				workStatus: '',
				remark: '',
			},
			formVa: {			
				name: '',
				post: '',
				phone: '',
				// remark: '',				
			},
			flag: '',
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
			var uname = sessionStorage.getItem("username")
			var uRole = sessionStorage.getItem("userRole")
            layui.use(['table','form'], function(){
                var table = layui.table,form = layui.form;
                
				form.render()
				//表格数据
                table.render({
                    elem: '#demo'
					,id: 'testReload'
					,method: 'post'
                    ,height: 'full-280'
                    ,cellMinWidth: 180
                    ,url: that.$url+'engineerWorkingStatus' //数据接口
					,where:{username:uname}
                    ,page: true //开启分页
					,limit: 20
                    ,cols: [[ //表头
                        {field: 'staffId', title: '员工编号', }
                        ,{field: 'name', title: '姓名'}
                        ,{field: 'post', title: '职位',}
                        ,{field: 'workCond', title: '工作状态'}
                        ,{field: 'phone', title: '电话',}
                        ,{field: 'remark', title: '备注'}
                        ,{field: 'czx', title: '操作项',minWidth:80,fixed:'right',
							templet: function (d) {
								var arr2 = ''
								return that.isShow(d);
							}
						}
						
                    ]]
					,done:function(res){
						// console.log(res)
						if(uRole != '运维助理'){
							$("[data-field='czx']").css('display','none');
						}
					}
                });

				form.on('submit(formDemo)',function(data){
                    var data = data.field;
                    // console.log(data)
					$('.layui-icon-close').click();                    
                    table.reload('testReload', {
                        url: that.$url+'engineerWorkingStatus' ,//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            name : data.name,//客户名称
                            post : data.post,//服务区域
                            username : uname
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
				// 操作栏
				table.on('tool(demo)', function(obj){
					var data = obj.data
					// console.log(data)
					// 删除
					if(obj.event == 'del'){
						that.formV.id = data.staffId
						deletaEngineer(that.formV)
						.then((res) => {
							if(res.data.code == 0){			
								that.$layerClose('test')
								that.$layerMsg('删除成功')					
							}else{
								that.$layerMsg(res.data.msg)
							}
						})
					// 修改
					}else if(obj.event == 'edit'){						
						that.flag = 'edit'
						that.formV.id = data.staffId
						that.formV.workStatus = data.workCond
						that.formVa.name = data.name
						that.formVa.post = data.post
						that.formVa.phone = data.phone
						that.formV.remark = data.remark
						// alert(that.formV.workStatus)
						that.b = data.name
						that.c = data.post
						that.d = data.phone
						that.e = data.remark
						// console.log(that.formV.workStatus)
						layui.use(['form','table','layer','util'], function(){
							var table = layui.table, layer = layui.layer;
							var form = layui.form;				
							layer.open({
								type:1,
								title: '修改',
								area: ['700px','500px'],
								content: $('.sdd2')
							})							
						})
					}
				})
            });
        },
		exportExcel(){
			var that = this
			that.$exportE('exportEngineers')			
		},
		subMit(){
			var that = this
			if(that.flag == 'edit'){
				// console.log(that.formV)
				doUpdateEngineer(that.formV)
				.then((res) => {
					if(res.data.code == 0){
						that.$layerClose('test')
						that.$layerMsg('修改成功')		
						that.getData()			
					}else{
						that.$layerMsg(res.data.msg)
					}	
				})
				.catch((error) => {
					console.log(res)
				})
			}
		},
		isShow(){
			var that = this			
			var arr2 = that.$admin
			var admin = ''
			if(arr2.ywzl == that.$userRole || arr2.admin == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="edit">修改</a>'
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
			}
			return admin;
			
		},
    },
	destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



