<template>
<!-- 客服登陆后首页 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <div class="up">
                <div class="bigpic"><img src="../../../../static/images/xuwei.png" width="100%" height="400px"/>
                    <!--  <div class="bigpic-font"><p>持续改善、优质运维、客户满意</p></div> -->
                </div>
                <div class="left-o">
                    <!--待受理客户事故及需求数量-->
                    <div class="bghbpink" style="height:200px;">
                        <p class="describe" style="margin-top:65px;"><router-link :to="{name:'WorkMsg',params:{woStatus:'d'}}">待受理客户事故及需求数量</router-link></p>
                        <p class="content">{{dsl}}</p>
                    </div>
                    <!--工单生成-->
                    <div class="bghbgreen" style="height:200px;margin-top:10px;">
                        <p><router-link to="/home/workMsg/WorkMsgAdd">工单生成</router-link></p>
                    </div>
                </div>
            </div>
            <div class="layui-clear contractD">
                <!--工单概况-->
                <dl class="contract-two">
                    <dd class="title">工单概况</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link rel="stylesheet" :to="{name:'WorkMsg',params:{woStatus:'a'}}">本月工单</router-link></dd>
                            <dd><span class="content">{{bygd}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link rel="stylesheet" :to="{name:'WorkMsg',params:{woStatus:'b'}}">未完成</router-link></dd>
                            <dd><span class="content">{{wwc}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link rel="stylesheet" :to="{name:'WorkMsg',params:{woStatus:'c'}}">超时工单</router-link></dd>
                            <dd><span class="content">{{csgd}}</span></dd>
                        </dl>
                    </dd>
                </dl>
                <!--客户满意度-->
                <dl class="contract-two">
                    <dd class="title">客户满意度</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'a'}}">用户评价</router-link></dd>
                            <dd><span class="content">{{ yhpj }}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'b'}}">客户投诉</router-link></dd>
                            <dd><span class="content">{{khts}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'c'}}">客户表扬</router-link></dd>
                            <dd><span class="content">{{khby}}</span></dd>
                        </dl>
                    </dd>
                </dl>
            </div>
            
        </div>
        
    </div>
</template>
<script>
import Index from '../../index'
import {headquartersService} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            yhpj:'',//客户评价
            khby:'',//客户表扬
            khts:'',//客户投诉
            dsl:'',//待受理客户事故及需求数量
            wwc:'',//未完成
            bygd:'',//本月工单
            csgd:'',//超时工单
            
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var userN = sessionStorage.getItem('username');
            var userName = {username : userN}
            headquartersService(userName).then((res) => {
                that.yhpj = res.data.客户评价;
                that.khby = res.data.客户表扬;
                that.khts = res.data.客户投诉;
                that.dsl = res.data.待受理客户事故及需求数量;
                that.wwc = res.data.未完成;
                that.bygd = res.data.本月工单;
                that.csgd = res.data.超时工单;
            })
            .catch((error) => {				
            })
        },
        
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.subordinate dd a{
    text-decoration: underline;
}
    
</style>



