<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <!-- 详细报表 -->
            <div class="titleLab">&gt;&gt;分公司KPI&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">起始时间</label>
                        <div class="layui-input-block">
                            <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">终止时间</label>
                        <div class="layui-input-block">
                            <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="btn">
                    <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome">查询</a>
                    <a class="layui-btn layui-bg-blue" style="width: 120px;" @click=aaaa()>导出详细报表</a>
                </div>
                </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <div></div>
            <div id="main" style="width: 600px;height:400px;" v-show="isShow"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {exportBranchKpi,areaManage,exportCustomerKpiSummary1} from '../../../config/axiosUtil'
export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            responseTimeRate: '',
            arrTimeRate:'',
            probSolveRate:'',
            pic: '',
            isShow: false,
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','form','laydate'], function () {
                var table = layui.table,laydate = layui.laydate,form = layui.form;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                //日期
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                form.render()
                // 详细报表
                table.render({
                    elem: '#demo'
                    , height: 'full-300'
                    , cellMinWidth: 150
                    , url: that.$url+'branchKpiSummary' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO',  type:'numbers',width: 80}
                        ,{field: 'custName', title: '客户名称', }
                        , {field: 'responseTimeRate', title: '电话响应时间达标率'}
                        , {field: 'arrTimeRate', title: '到达时间达标率'}
                        , {field: 'probSolveRate', title: '问题解决时间达标率',width:180}
                        , {field: 'custScore', title: '客户满意度',width:150}
                    ]]
                    ,id: 'testReload'
                    ,done:function(res){
                        if(res.count == 1){
                            
                        }else{
                            that.isShow = false
                        }
                        
                    }
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    $('.layui-icon-close').click();
                    table.reload('testReload', {
                        url: that.$url+'branchKpiSummary' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            serviceArea : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                        ,done:function(res){
                            console.log(res)
                            if(data.name){
                                that.isShow = true
                                that.responseTimeRate = res.avgResponseTimeRate
                                that.arrTimeRate = res.avgArrTimeRate;
                                that.probSolveRate = res.avgProbTimeRate
                                echartsFun()
                            }
                        }
                    });
                })

                
            });
            function echartsFun(){
            var myChart = that.$echarts.init(document.getElementById('main'),'',{width:600,height:300})
            // 指定图表的配置项和数据
            console.log(that.responseTimeRate)
            var option = {
                title: {
                    text: '分公司月KPI汇总'
                },
                tooltip: {
                    // trigger: 'item',
                    // formatter: "{a}<br/>{b}:{c}%"
                    formatter: function (params,ticket,callback) {
                        console.log(params);
                        console.log(ticket);
                        console.log(callback);
                        // var res = params[1]+   '<br/>'+"XX人数"+' : ' + params[2];
                        return res;
                    }
                },  //提示框
                legend: {               //图例
                    
                },
                xAxis: {            //x轴数据
                    type: 'category'
                },
                yAxis: [  
                    {  
                        type: 'value',  
                        axisLabel: {  
                            show: true,  
                            interval: 'auto',  
                            formatter: '{value} %'  
                            },  
                        show: true,
                        boundaryGap: true
                    }  
                ],            //y轴数据
                series: [        //图标类型)(柱状图)
                    {
                        type: 'bar',
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                fontSize: 14,
                            }
                        },
                    },
                    {
                        type: 'bar',
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                fontSize: 14,
                            }
                        },
                    },
                ],
                dataset: {
                    source: [
                        ['product', '达标率目标', '当月实际达标率'],
                        ['电话响应时间', '95', that.responseTimeRate],
                        ['到达现场时间', '95', that.arrTimeRate],
                        ['问题解决时间', '95', that.probSolveRate],
                        // ['设备正常运转率', '95', 0]
                    ]
                }, 
                //关闭动画，否则有些统计图表只有背景（如扇形统计图）
                animation:false,
            };

            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
            
            //获取echarts图的base64编码，为png格式
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.pic = {picture : myChart.getDataURL()}
        }
        },
        
        aaaa(){
            var that = this;
            let userN = sessionStorage.getItem('username')
            console.log(that.pic)
            // that.$exportE('exportBranchKpi')
                exportBranchKpi(that.pic).then((res) => {
                    // console.log(res)	
                    window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
                }).catch((error) => {
                    // layer.msg('我的天呐，网炸了！')
                })
        }
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



