<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;密码修改&lt;&lt;</div>
            <form class="layui-form">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">原密码</label>
                        <div class="layui-input-inline">
                            <input type="password" name="oldPassword" lay-verify="required" class="layui-input"  autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">新密码</label>
                        <div class="layui-input-inline">
                            <input type="password" name="newPassword" lay-verify="required" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">确认密码</label>
                        <div class="layui-input-inline">
                            <input type="password" name="surePassword" lay-verify="required" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item mar-left">
                    <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="preservation">提交</a>
                    <button type="reset" class="layui-btn reset">重置</button>
                </div>
            </form>
            
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {upDatePassword} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            layui.use(['form','layer'],function(){
                var form = layui.form, layer = layui.layer;
                form.on('submit(preservation)',function(data){
                    console.log(data)
                    // var username = sessionStorage.getItem('username');
                    // console.log(username)
                    // var oldPassword = data.field.oldPassword;
                    // var newPassword = data.field.newPassword;
                    // var surePassword = data.field.surePassword;
                    var datapwd = {
                        username : sessionStorage.getItem('username'),
                        oldPassword : data.field.oldPassword,
                        newPassword : data.field.newPassword,
                        surePassword : data.field.surePassword
                    }
                    console.log(datapwd)
                    upDatePassword(datapwd).then((res) => {	
                        console.log(res.data);
                        var datas = res.data;
                        if(datas.code == 1){
                            layer.msg(datas.msg);
                            $('.reset').click();
                        }else if(datas.code == 0){
                            layer.msg(datas.msg);
                            $('.reset').click();
                        }
                    })
                    .catch((error) => {				
                    })
                })
            })
        }
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



