<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">   
        <div class="titleLab">&gt;&gt;PC端权限分配&lt;&lt;</div>   
			<form class="layui-form">
				<div class="layui-form-item">
					<div class="layui-inline">
						<label class="layui-form-label">角色</label>
						<div class="layui-input-inline">
							<select name="role" id='rr' class='layui-form' lay-filter="role">
								<option value=""></option>
								<!-- <option value="客户">客户</option> -->
								<option value="运维总监">运维总监</option>
								<option value="运维经理">运维经理</option>
								<option value="技术主管">技术主管</option>
								<option value="客服经理">客服经理</option>
								<option value="总部客服">总部客服</option>
								<option value="运维助理">运维助理</option>
							</select>
						</div>
					</div>
					<a class="layui-btn layui-bg-blue" @click='subMit()'>保存</a>
				</div>
			</form>            
            <div class="menuSet">
                <div class="title">菜单管理</div>
                <div class="menuCon">
                <div class="menuContent">
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.homepage'><label>首页</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" @click="homePage(formV.workerOrder)" v-model='formV.workerOrder'><label>工单概况</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" @click='homePage(formV.contract)' v-model='formV.contract'><label>合同概况</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" @click='homePage(formV.equipment)' v-model='formV.equipment'><label>设备概况</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" @click='homePage(formV.atWork)' v-model='formV.atWork'><label>在岗人数</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" @click='homePage(formV.satisfaction)' v-model='formV.satisfaction'><label>客户满意度</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.basicMsg'><label>基本信息</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.custmorMsg'><label>客户信息</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.userManage'><label>用户管理</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.calendar'><label>工作日历</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.custManage'><label>客户管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.contractMsg'><label>合同管理</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.contractSatis'><label>客户满意度</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.feedback'><label>反馈与建议</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.serveManage'><label>服务管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.workMsg'><label>工单管理</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.faultAnalysis'><label>故障分析</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.equipAndProperty'><label>设备与资产管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.equipmentManage'><label>设备管理</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.assets'><label>资产管理</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.maintainManage'><label>保养管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.plan'><label>保养计划</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.implement'><label>保养执行</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv" style="width: 235px;">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.reportManage'><label>报表管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit" style="height: 230px">
                                <input type="checkbox" v-model='formV.kpiMang'><label>kpi管理</label>  
                                <div class="menukpi">
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.engineer'><label>工程师KPI报表</label>  
                                    </div>
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.customerMsgr'><label>客户KPI报表</label>  
                                    </div>
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.branchOffice'><label>分公司KPI</label>  
                                    </div>
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.headquarters'><label>总公司运维KPI</label>  
                                    </div>
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.service'><label>坐席客服KPI</label>  
                                    </div>
                                </div>
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.orderAnalysis'><label>工单分析表</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.repair'><label>维修报表</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.operationRate'><label>设备运转正常率</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.maintain'><label>保养报表</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.equipmentReq'><label>设备管理报表</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.contractForm'><label>运维合同报表</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv" style="border-right: none;">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.onGuard'><label>在岗管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.engineerWork'><label>在岗状态</label>  
                            </div>
                        </div>
                    </div>
                    <!-- <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.satisfaction'><label>耗材管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.satisfaction'><label>耗材页面</label>  
                            </div>
                        </div>
                    </div>
                    <div class="floatDiv">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.satisfaction'><label>账单管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.satisfaction'><label>账单页面</label>  
                            </div>
                        </div>
                    </div> -->
                   <div class="floatDiv" style="width: 235px;border-right: none;">
                        <div class="menuTit">
                            <input type="checkbox" v-model='formV.system'><label>系统管理</label>  
                        </div>
                        <div class="menuChild">
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.area'><label>区域管理</label>  
                            </div>
                            <div class="menuTit" style="height: 115px">
                                <input type="checkbox" v-model='formV.jurisdiction'><label>权限分配</label> 
                                <div class="menukpi">
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.pc'><label>PC端权限分配</label>  
                                    </div>
                                 <!--   <div class="menuTit">
                                        <input type="checkbox" v-model='formV.moble'><label>移动端权限分配</label>  
                                    </div> -->
                                    <div class="menuTit">
                                        <input type="checkbox" v-model='formV.account'><label>账户管理</label>  
                                    </div>
                                </div> 
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.amend'><label>密码修改</label>  
                            </div>
                            <div class="menuTit">
                                <input type="checkbox" v-model='formV.parameter'><label>参数设定</label>  
                            </div>
                        </div>
                    </div>
					 
                </div>    
                </div>
            </div>
			
        </div>		
    </div>
</template>
<script>
import Index from '../../index'
import {judgeRole,upDatePermission} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
// 			formV: {
// 				username: '',
// 				homepage: '',
// 				workerOrder: '',
// 				equipment: '',
// 				contract: '',
// 				atWork: '',
// 				satisfaction: '',
// 				basicMsg: '',
// 				custmorMsg: '',
// 				userManage: '',
// 				calendar: '',
// 				custManage: '',
// 				contractMsg: '',
// 				contractSatis: '',
// 				feedback: '',
// 				serveManage: '',
// 				workMsg: '',
// 				faultAnalysis: '',
// 				equipAndProperty: '',
// 				equipmentManage: '',
// 				assets: '',
// 				maintainManage: '',
// 				plan: '',
// 				implement: '',
// 				reportManage: '',
// 				kpiMang: '',
// 				engineer: '',
// 				customerMsgr: '',
// 				branchOffice: '',
// 				headquarters: '',
// 				service: '',
// 				orderAnalysis: '',
// 				repair: '',
// 				operationRate: '',
// 				maintain: '',
// 				equipmentReq: '',
// 				contractForm: '',
// 				onGuard: '',
// 				engineerWork: '',
// 				system: '',
// 				area: '',
// 				jurisdiction: '',
// 				pc: '',
// 				moble: '',
// 				account: '',
// 				amend: '',
// 				parameter: '',
// 			}
			formV: {
				role: '',
			}
        }
    },
    mounted() {
        this.getData()
    },
	watch: {
		'formV.homepage':function(value){
			if(value == true){
				this.formV.workerOrder = true
				this.formV.equipment = true
				this.formV.contract = true
				this.formV.atWork = true
				this.formV.satisfaction = true
			}else{
				this.formV.workerOrder = false
				this.formV.equipment = false
				this.formV.contract = false
				this.formV.atWork = false
				this.formV.satisfaction = false
			}
		},
		'formV.basicMsg':function(value){
			if(value == true){
				this.formV.custmorMsg = true
				this.formV.userManage = true
				this.formV.calendar = true				
			}else{
				this.formV.custmorMsg = false
				this.formV.userManage = false
				this.formV.calendar = false
			}
		},
		'formV.custManage':function(value){
			if(value == true){
				this.formV.contractMsg = true
				this.formV.contractSatis = true
				this.formV.feedback = true				
			}else{
				this.formV.contractMsg = false
				this.formV.contractSatis = false
				this.formV.feedback = false		
			}
		},
		'formV.serveManage':function(value){
			if(value == true){
				this.formV.workMsg = true
				this.formV.faultAnalysis = true						
			}else{
				this.formV.workMsg = false
				this.formV.faultAnalysis = false
			}
		},
		'formV.equipAndProperty':function(value){
			if(value == true){
				this.formV.equipmentManage = true
				this.formV.assets = true			
			}else{
				this.formV.equipmentManage = false
				this.formV.assets = false		
			}
		},
		'formV.maintainManage':function(value){
			if(value == true){
				this.formV.plan = true
				this.formV.implement = true			
			}else{
				this.formV.plan = false
				this.formV.implement = false	
			}
		},
		'formV.reportManage':function(value){
			if(value == true){
				this.formV.kpiMang = true
				this.formV.orderAnalysis = true
				this.formV.repair = true	
				this.formV.operationRate = true
				this.formV.maintain = true
				this.formV.equipmentReq = true
				this.formV.contractForm = true
			}else{
				this.formV.kpiMang = false
				this.formV.orderAnalysis = false
				this.formV.repair = false	
				this.formV.operationRate = false
				this.formV.maintain = false
				this.formV.equipmentReq = false
				this.formV.contractForm = false
			}
		},		
		'formV.onGuard':function(value){
			if(value == true){
				this.formV.engineerWork = true							
			}else{
				this.formV.engineerWork = false
			}
		},
		'formV.system':function(value){
			if(value == true){
				this.formV.area = true
				this.formV.jurisdiction = true
				this.formV.amend = true	
				this.formV.parameter = true			
			}else{
				this.formV.area = false
				this.formV.jurisdiction = false
				this.formV.amend = false	
				this.formV.parameter = false		
			}
		},
		'formV.kpiMang':function(value){
			if(value == true){
				this.formV.engineer = true
				this.formV.customerMsgr = true
				this.formV.branchOffice = true	
				this.formV.headquarters = true		
				this.formV.service = true		
			}else{				
				this.formV.engineer = false
				this.formV.customerMsgr = false
				this.formV.branchOffice = false	
				this.formV.headquarters = false		
				this.formV.service = false				
			}
		},
		'formV.jurisdiction':function(value){
			if(value == true){
				this.formV.pc = true
				this.formV.moble = true
				this.formV.account = true			
			}else{
				this.formV.pc = false                                                                                                                                                                                                                    
				this.formV.moble = false
				this.formV.account = false				
			}
		}
		
	},
    methods:{
		homePage(str){
			var that = this			
			if(str == false){				
				that.formV.homepage = true
			}
		},
        getData(){
            var that = this;
            layui.use(['layer','form'], function () {
                var layer = layui.layer, form = layui.form;
                form.render();
				form.on('select(role)', function (data) {//aihao为下拉框的lay-filter
					that.formV.role = data.value
					var v = {
						role:that.formV.role
					}
					sessionStorage.setItem('role',that.formV.role)
					judgeRole(v)
					.then((res) => {
						if(res.data.code == 0){
							that.formV = res.data.data
						}else{
							that.$layerMsg(res.data.msg)
						}
					})
				});
            })
        },		
		subMit(){
			var that = this
			that.formV.role = sessionStorage.getItem('role')
			// console.log(that.formV)
			upDatePermission(that.formV)
			.then((res) => {
				if(res.data.code == 0 ){
					sessionStorage.removeItem('userN')
					that.$layerMsg(res.data.msg)
					that.formV = ''
					that.$router.go(0)
					// that.$router.push({path: '/home/serviceHome'})
					// that.$router.push({path: '/home/pcU'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.menuSet{
    width: 96%;
    margin-left: 2%;
    background: #fff;
}
.floatDiv{
    float: left;
    width: 185px;
    border-right: solid #4cbff6 1px;
}
.menuCon{
    overflow-x: auto;
    height: 350px;
}
.menuContent{
    width: 165%;
    height: 390px;
}
.menuTit{
    width: 95%;
    margin-left: 5%;
    height: 38px;
    line-height: 38px;
    font-size: 18px;
}
.menuChild{
    margin-left: 15%;
}
.menuChild>.menuTit>label{
    font-size: 16px;
    
}
.menukpi{
    margin-left: 15%;
}
.menukpi label{
    font-size: 16px;
}
</style>



