import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Login from '@/components/login'
import Assistant from '@/components/content/home/assistant'
import Client from '@/components/content/home/client'
import Majordomo from '@/components/content/home/majordomo'
import Operations from '@/components/content/home/operations'
import ServiceHome from '@/components/content/home/service'
import WorkerOrder from '@/components/content/homePage/workerOrder'
import Contract from '@/components/content/homePage/contract'
import AtWork from '@/components/content/homePage/atWork'
import Satisfaction from '@/components/content/homePage/satisfaction'
import Equipment from '@/components/content/homePage/equipment'
import CustmorMsg from '@/components/content/basicMsg/custmorMsg'
import Calendar from '@/components/content/basicMsg/calendar'
import UserManage from '@/components/content/basicMsg/userManage'
import ContractMsg from '@/components/content/custManage/contractMsg'
import ContractSatis from '@/components/content/custManage/contractSatis'
import Feedback from '@/components/content/custManage/feedback'
import WorkMsg from '@/components/content/serveManage/workMsg'
import WorkMsgAdd from '@/components/content/serveManage/workMsgAdd'
import FaultAnalysis from '@/components/content/serveManage/faultAnalysis'
import EquipmentManage from '@/components/content/equipAndProperty/equipmentManage'
import Assets from '@/components/content/equipAndProperty/assets'
import Plan from '@/components/content/maintainManage/plan'
import Implement from '@/components/content/maintainManage/implement'
import Engineer from '@/components/content/reportManage/engineer'
import CustomerMsgr from '@/components/content/reportManage/customerMsgr'
import BranchOffice from '@/components/content/reportManage/branchOffice'
import Headquarters from '@/components/content/reportManage/headquarters'
import Service from '@/components/content/reportManage/service'
import OrderAnalysis from '@/components/content/reportManage/orderAnalysis'
import Repair from '@/components/content/reportManage/repair'
import OperationRate from '@/components/content/reportManage/operationRate'
import Maintain from '@/components/content/reportManage/maintain'
import EquipmentReq from '@/components/content/reportManage/equipmentReq'
import ContractForm from '@/components/content/reportManage/contractForm'
import OrderStat from '@/components/content/reportManage/orderStat'
import orderPrescription from '@/components/content/reportManage/orderPrescription'
import EngineerWork from '@/components/content/onGuard/engineerWork'
import Area from '@/components/content/system/area'
import Jurisdiction from '@/components/content/system/jurisdiction'
import Pc from '@/components/content/system/pc'
import Moble from '@/components/content/system/moble'
import Account from '@/components/content/system/account'
import Amend from '@/components/content/system/amend'
import Parameter from '@/components/content/system/parameter'
import pcU from '@/components/content/system/pcU'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
		{
			path: '/home',   //运维助理首页
			name: 'Home',
			component: Home,
			children:[
				{
					path: '/home/pcU',   //运维助理首页
					name: 'pcU',
					component: pcU
				},
				{
					path: '/home/assistant',   //运维助理首页
					name: 'Assistant',
					component: Assistant
				},
				{
					path: '/home/client',   //客户端登陆后首页
					name: 'Client',
					component: Client
				},
				{
					path: '/home/majordomo',   //运维助理首页
					name: 'Majordomo',
					component: Majordomo
				},
				{
					path: '/home/operations',   //运维助理首页
					name: 'Operations',
					component: Operations
				},
				{
					path: '/home/serviceHome',   //运维助理首页
					name: 'ServiceHome',
					component: ServiceHome
				},
				
				{
					path: '/home/workerOrder',  //工单概况
					name: 'WorkerOrder',
					component: WorkerOrder
				},
				{
					path: '/home/contract',  //合同概况
					name: 'Contract',
					component: Contract
				},
				{
					path: '/home/atWork',  //在岗状态
					name: 'AtWork',
					component: AtWork
				},
				{
					path: '/home/satisfaction',  //客户满意度
					name: 'Satisfaction',
					component: Satisfaction
				},
				{
					path: '/home/custmorMsg',  //客户信息
					name: 'CustmorMsg',
					component: CustmorMsg
				},
				{
					path: '/home/equipment',  //设备概况
					name: 'Equipment',
					component: Equipment
				},
				{
					path: '/home/calendar',  //工作日历
					name: 'Calendar',
					component: Calendar
				},
				{
					path: '/home/userManage',  //员工信息
					name: 'UserManage',
					component: UserManage
				},
				{
					path: '/home/contractMsg',  //合同管理
					name: 'ContractMsg',
					component: ContractMsg
				},
				{
					path: '/home/contractSatis',  //客户满意度管理
					name: 'ContractSatis',
					component: ContractSatis
				},
				{
					path: '/home/feedback',  //反馈与建议
					name: 'Feedback',
					component: Feedback
				},
				{
					path: '/home/workMsg',  //工单管理
					name: 'WorkMsg',
					component: WorkMsg,
					children:[
						{
							path: 'workMsgAdd',  //新增工单
							name: 'WorkMsgAdd',
							component: WorkMsgAdd,
						},
					]
				},
				{
					path: '/home/faultAnalysis',  //故障分析
					name: 'FaultAnalysis',
					component: FaultAnalysis
				},
				{
					path: '/home/equipmentManage',  //设备管理
					name: 'EquipmentManage',
					component: EquipmentManage
				},
				{
					path: '/home/assets',  //资产管理
					name: 'Assets',
					component: Assets
				},
				{
					path: '/home/plan',  //保养计划
					name: 'Plan',
					component: Plan
				},
				{
					path: '/home/implement',  //保养执行
					name: 'Implement',
					component: Implement
				},
				{
					path: '/home/engineer',  //工程师KPI报表
					name: 'Engineer',
					component: Engineer
				},
				{
					path: '/home/customerMsgr',  //客户KPI报表
					name: 'CustomerMsgr',
					component: CustomerMsgr
				},
				{
					path: '/home/branchOffice',  //分公司KPI
					name: 'BranchOffice',
					component: BranchOffice
				},
				{
					path: '/home/headquarters',  //总公司运维KPI
					name: 'Headquarters',
					component: Headquarters
				},
				{
					path: '/home/service',  //坐席客服KPI
					name: 'Service',
					component: Service
				},
				{
					path: '/home/orderAnalysis',  //工单分析表
					name: 'OrderAnalysis',
					component: OrderAnalysis
				},
				{
					path: '/home/repair',  //维修报表
					name: 'Repair',
					component: Repair
				},
				{
					path: '/home/operationRate',  //设备运转正常率
					name: 'OperationRate',
					component: OperationRate
				},
				{
					path: '/home/maintain',  //保养报表
					name: 'Maintain',
					component: Maintain
				},
				{
					path: '/home/equipmentReq',  //设备管理报表
					name: 'EquipmentReq',
					component: EquipmentReq
				},
				{
					path: '/home/orderStat',  //工单统计
					name: 'OrderStat',
					component: OrderStat
				},
				{
					path: '/home/orderPrescription',  //工单时效表
					name: 'orderPrescription',
					component: orderPrescription
				},
				{
					path: '/home/contractForm',  //运维合同报表
					name: 'ContractForm',
					component: ContractForm
				},
				{
					path: '/home/engineerWork',  //在岗状态
					name: 'EngineerWork',
					component: EngineerWork
				},
				{
					path: '/home/area',  //区域管理
					name: 'Area',
					component: Area
				},
				{
					path: '/home/jurisdiction',  //权限分配
					name: 'Jurisdiction',
					component: Jurisdiction
				},
				{
					path: '/home/pc',  //PC端权限分配
					name: 'Pc',
					component: Pc
				},
				{
					path: '/home/moble',  //移动端权限分配
					name: 'Moble',
					component: Moble
				},
				{
					path: '/home/account',  //账户管理
					name: 'Account',
					component: Account
				},
				{
					path: '/home/amend',  //密码修改
					name: 'Amend',
					component: Amend
				},
				{
					path: '/home/parameter',  //参数设定
					name: 'Parameter',
					component: Parameter
				}
			]
		},
    
    
  ]
})



