                        layer.confirm("确认删除工单？",function(index){
                            deleteOrder(worlNum).then((res) => {
                                console.log(res.data.msg)
                                table.reload('testReload')
                                layer.msg(res.data.msg)
                            })
                            .catch((error) => {				
                            })
                            layer.close(index)
                        })
                    }else if(obj.event == 'reset'){
                        //撤除工单
                        var worlNum = {woNumber:obj.data.woNumber};
                        layer.confirm("确认撤除工单？",function(index){
                            resetOrder(worlNum).then((res) => {
                                console.log(res.data.msg)
                                table.reload('testReload')
                                layer.msg(res.data.msg)
                            })
                            .catch((error) => {				
                            })
                            layer.close(index)
                        })
                    }else if(obj.event == 'exit'){
                        
                        //修改
                        console.log(obj.data)
                        if(obj.data.faultType == '事故类'){
                            layer.open({
                                type:1,
                                title: '修改事故单',
                                content:$('.shigu'),
                                area:['60%', '70%'],
                                cancel: function(index, layero){ 

                                }  
                            })
                            var serviceType = obj.data.serviceType
                            if(serviceType != null){
                                 var arr = serviceType.split('：');
                                 var hcxh = arr[0]
                                 var hcsl = arr[1]
                            }
                            // var arr = serviceType.split('：');
                            // var hcxh = serviceType.substring(0,1);
                            // console.log(hcxh)
                            // var hcsl = serviceType.substring(2,3)
                            if(obj.data.accidentType == '硬件故障'){
                                that.hcdg = false
                            }else if(obj.data.accidentType == '软件故障'){
                                that.hcdg = false
                            }else if(obj.data.accidentType == '耗材断供'){
                                that.hcdg = true
                            }
                            form.val('exampleSG', {
                                "woNumber": obj.data.woNumber  // 工单号码
                                ,"machCode": obj.data.machCode  //机器编码
                                ,"custName": obj.data.custName      //客户名称
                                ,"devName": obj.data.devName   //设备类型
                                ,"unitType": obj.data.unitType      //设备型号
                                ,"department": obj.data.department      //部门
                                ,"location": obj.data.location  //设备位置
                                ,"custAddr": obj.data.custAddr  //客户地址
                                ,"repairMan": obj.data.repairMan  //报修人
                                ,"repairPhone": obj.data.repairPhone  //报修人联系电话
                                ,"accidentType": obj.data.accidentType  //服务类别
                                ,"hcxh": hcxh //耗材型号
                                ,"hcsl": hcsl  //耗材数量
                                ,"describe": obj.data.describe   //故障描述                                              
                            })
                            
                            form.on('radio',function(data){
                                console.log(data)
                                if(data.value == '硬件故障'){
                                    that.hcdg = false
                                }else if(data.value == '软件故障'){
                                    that.hcdg = false
                                }else if(data.value == '耗材断供'){
                                    that.hcdg = true
                                }
                            })
                        }else if(obj.data.faultType == '事件类'){

                            layer.open({
                                type:1,
                                title: '修改事件单',
                                content:$('.shijian'),
                                area:['60%', '70%'],
                                cancel: function(index, layero){ 
                                    
                                }  
                            })
                            that.serviceArea = obj.data.serviceArea
                            form.val('exampleSJ', {
                                "woNumber": obj.data.woNumber  // 工单号码
                                ,"serviceArea": obj.data.serviceArea  //服务区域
                                ,"custName": obj.data.custName      //客户名称
                                ,"repairMan": obj.data.repairMan   //报修人
                                ,"repairPhone": obj.data.repairPhone      //报修人联系电话
                                ,"faultType": obj.data.faultType      //服务类型
                                ,"accidentType": obj.data.accidentType  //服务类别
                                ,"describe": obj.data.describe //需求描述
                            })

                        }else{
                            layer.open({
                                type:1,
                                title: '修改需求单',
                                content:$('.xuqiu'),
                                area:['60%', '70%'],
                                cancel: function(index, layero){ 
                                    
                                },
                                success: function(layero, index){
                                    // console.log(layero, index);
                                }
                            })
                            // console.log(that.serArea)
                            var serviceType = obj.data.serviceType
                            if(serviceType != null){
                                 var arr = serviceType.split('：');
                                 var hcxh = arr[0]
                                 var hcsl = arr[1]
                            }
                            // var hcxh = serviceType.substring(0,1);
                            // console.log(hcxh)
                            // var hcsl = serviceType.substring(2,3)
                            // var arr = serviceType.split('：');
                            if(obj.data.accidentType == '设备变动'){
                                that.sbbd = true
                                    that.haocai = false
                            }else if(obj.data.accidentType == '耗材补充'){
                                that.haocai = true
                                    that.sbbd = false
                            }else{
                                that.haocai = false
                                    that.sbbd = false
                            }
                            form.val('exampleXQ', {
                                "woNumber": obj.data.woNumber  // 工单号码
                                ,"serviceArea": obj.data.serviceArea  //服务区域
                                ,"custName": obj.data.custName      //客户名称
                                ,"repairMan": obj.data.repairMan   //报修人
                                ,"repairPhone": obj.data.repairPhone      //报修人联系电话
                                ,"faultType": obj.data.faultType      //服务类型
                                ,"accidentType": obj.data.accidentType  //服务类别
                                ,"macd": obj.data.macd  //设备变动
                                ,"hcxh": hcxh //耗材型号
                                ,"hcsl": hcsl  //耗材数量
                                ,"describe": obj.data.describe //需求描述
                            })
                            form.on('radio(encrypt)',function(data){
                                console.log(data)
                                if(data.value == '设备变动'){
                                    that.sbbd = true
                                    that.haocai = false
                                }else if(data.value == '耗材补充'){
                                    that.haocai = true
                                    that.sbbd = false
                                }else{
                                    that.haocai = false
                                    that.sbbd = false
                                }
                            })
                        }
                    }else if(obj.event == 'setSign'){
                        //查看图片
                        var data = obj.data;
                        
                        // that.picture = data.picture
                        console.log(data.picture.picture1)
                        if(data.picture.picture1 == null){
                            
                        }else if(data.picture.picture2 == null && data.picture.picture3 == null){
                            layer.open({  
                                type: 1,  
                                offset: 'auto',
                                area:['70%', '70%'],
                                shadeClose:true,
                                scrollbar: false,
                                title: "图片预览", //不显示标题  
                                content:$('.setSign'),
                            }); 
                            var pictures = []
                            pictures.push(data.picture.picture1)
                            that.picture = pictures
                        }else if(data.picture.picture3 == null){
                            layer.open({  
                                type: 1,  
                                offset: 'auto',
                                area:['70%', '70%'],
                                shadeClose:true,
                                scrollbar: false,
                                title: "图片预览", //不显示标题  
                                content:$('.setSign'),
                            }); 
                            var pictures = []
                            pictures.push(data.picture.picture1)
                            pictures.push(data.picture.picture2)
                            that.picture = pictures
                        }else{
                            layer.open({  
                                type: 1,  
                                offset: 'auto',
                                area:['800px', '400px'],
                                shadeClose:true,
                                scrollbar: false,
                                title: "图片预览", //不显示标题  
                                content:$('.setSign'),
                            }); 
                            var pictures = []
                            pictures.push(data.picture.picture1)
                            pictures.push(data.picture.picture2)
                            pictures.push(data.picture.picture3)
                            that.picture = pictures
                        }
                    }
                })
                // 事故类修改
                form.on('submit(shigu)', function(data){
                    var data = data.field;
                    console.log(data)
                    var sendData = {
                        "woNumber": data.woNumber  // 工单号码
                        ,"machCode": data.machCode  //机器编码
                        ,"repairMan": data.repairMan  //报修人
                        ,"repairService": data.repairPhone  //报修人联系电话
                        ,"accidentType": data.accidentType  //服务类别
                        ,"materialModel": data.hcxh //耗材型号
                        ,"materialNumber": data.hcsl  //耗材数量
                        ,"describe": data.describe   //故障描述  
                    }
                    updateOrder(sendData).then((res) => {
                        if(res.data.code == 0){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }else if(res.data.code == 1){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }
                    })
                    .catch((error) => {				
                    })
                    return false;
                });
                // 事件类修改
                form.on('submit(shijian)', function(data){
                    var data = data.field;
                    console.log(data)
                    var sendData = {
                        "woNumber": data.woNumber  // 工单号码
                        ,"serviceArea": data.serviceArea  //服务区域
                        ,"machCode": data.machCode  //机器编码
                        ,"custName": data.custName      //客户名称
                        ,"repairMan": data.repairMan  //报修人
                        ,"repairPhone": data.repairPhone  //报修人联系电话
                        ,"accidentType": data.accidentType  //服务类别
                        ,"materialModel": data.hcxh //耗材型号
                        ,"materialNumber": data.hcsl  //耗材数量
                        ,"describe": data.describe   //故障描述  
                    }
                    updateOrder(sendData).then((res) => {
                        if(res.data.code == 0){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }else if(res.data.code == 1){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }
                    })
                    .catch((error) => {				
                    })
                    return false;
                });
                // 需求类修改
                form.on('submit(xuqiu)', function(data){
                    var data = data.field;
                    console.log(data)
                    var sendData = {
                        "woNumber": data.woNumber  // 工单号码
                        ,"serviceArea": data.serviceArea  //服务区域
                        ,"custName": data.custName      //客户名称
                        ,"repairMan": data.repairMan   //报修人
                        ,"repairService": data.repairPhone      //报修人联系电话
                        ,"faultType": '需求类'      //服务类型
                        ,"accidentType": data.accidentType  //服务类别
                        ,"macd": data.macd  //设备变动
                        ,"hcxh": data.hcxh //耗材型号
                        ,"hcsl": data.hcsl  //耗材数量
                        ,"describe": data.describe //需求描述
                    }
                    updateOrder(sendData).then((res) => {
                        if(res.data.code == 0){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }else if(res.data.code == 1){
                            layer.closeAll();
                            table.reload('testReload')
                            layer.msg(res.data.msg)
                        }
                    })
                    .catch((error) => {				
                    })
                    return false;
                });
                //受理派单
                form.on('submit(send)', function(data){
                    var data = data.field;
                    var sendRes = {woNumber:data.woNumber,engineerID:data.engineer,username:uname}
                    // if(data.engineer == ''){
                    //     layer.msg('请先指派工程师')
                    // }else{
                        console.log(sendRes)
                        layer.confirm("确认派单么?",function(index){
                            sendOrder(sendRes).then((res) => {
                                if(res.data.code == 0){
                                    layer.closeAll();
                                    table.reload('testReload')
                                    layer.msg(res.data.msg)
                                }else if(res.data.code == 1){
                                    layer.closeAll();
                                    table.reload('testReload')
                                    layer.msg(res.data.msg)
                                }
                            })
                            .catch((error) => {				
                            })
                        })
                    // }
                    
                    return false;
                });
                // 重新指派
                form.on('submit(sendAgain)', function(data){
                    var data = data.field;
                    var sendRes = {woNumber:data.woNumber,engineerID:data.engineer,username:uname}
                    // if(data.engineer == ''){
                    //     layer.msg('请先指派工程师')
                    // }else{
                        layer.confirm("确认派单么?",function(index){
                            // sendOrderOnce(sendRes).then((res) => {
                            sendOrder(sendRes).then((res) => {
                                if(res.data.code == 0){
                                    layer.closeAll();
                                    table.reload('testReload')
                                    layer.msg(res.data.msg)
                                }else if(res.data.code == 1){
                                    layer.closeAll();
                                    table.reload('testReload')
                                    layer.msg(res.data.msg)
                                }
                            })
                            .catch((error) => {				
                            })
                        })
                    // }
                    
                    return false;
                });
                form.on('submit(formDome2)', function(data){
                    // console.log(data)
                    var woStatus = ''
                    $('.layui-icon-close').click();
                    data = data.field;
                    // var data = data.field;
                    var fromState = '';
                    // var a = data.a, b = data.b, c = data.c;
                    // console.log(data,"574")
                    // console.log(a,b,c)
                    if(data.a){
						var a = data.a
					}else{
						var a = ''
					}
					if(data.b){
						var b = data.b
					}else{
						var b = ''
					}
					if(data.c){
						var c = data.c
					}else{
						var c = ''
					}
					// console.log(a)
                    if(data.from == 1){
                        fromState = '网络'
                    }else if(data.from == 2){
                        fromState = '电话'
                    }else{
                        fromState = ''
                    };
                    form.render()
                    //查询
                    table.reload('testReload', {
                        url: that.$url+'orderManage' ,//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.name,
                            enginnerName : data.enginnerName,
                            serviceArea : data.serviceArea,
                            machCode : data.bianma,
                            startDate : data.startTime,
                            endDate : data.endTime,
                            rapairType : fromState,
                            a,b,c,woStatus,
                            username:uname,
                            ProcessingState : data.ProcessingState
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                    return false;
                });
            });
        },
		exportE(){
			var that = this
			that.$exportE('exportOrder')
        },
		showA(){
			var that = this
			var arr = [that.$admin.ywzl,that.$admin.zxcry,that.$admin.kfzg,that.$admin.zbkf,that.$admin.admin]
			// console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
		    }
        },
        //新增
        // add(){
        //     this.$router.push({path:'/workMsg/workMsgAdd'})
        // },
        // dropDown(){
        //     // if(){}
        //     $('.dropDown').css('display','block')
        // },
        // dropUp(){
        //     $('.dropDown').css('display','none')

        // }
		
    },
	created(){
		// alert(1)
	},
	beforeCreate(){
		$('.layui-icon-close').click();
	},
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.wininput{
    width: 300px;
}
.dropDownE{
    width: 278px;
    height: 38px;
    border: 1px solid #aaa;
    padding: 0 10px;
    line-height: 38px;
}
.dropDownRE{
    width: 278px;
    height: 38px;
    border: 1px solid #aaa;
    padding: 0 10px;
    line-height: 38px;
}
</style>



