<template>
<!-- 保养执行 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <div class="titleLab">&gt;&gt;保养执行&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
                <div class="layui-inline">
                	<label class="layui-form-label">客户名称</label>
                	<div class="layui-input-block">
                		<input type="text" name="custName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                	</div>
                </div>
                <div class="layui-inline">
                	<label class="layui-form-label">服务区域</label>
                	<div class="layui-input-block">
                		<select name="serviceArea" lay-verify="" class="allinput">
                		<option value="" id="address">请选择</option>      
                		</select>
                	</div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">工程师名称</label>
                    <div class="layui-input-block">
                        <input type="text" name="enginnerName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
				<div class="btn">
					<a class="layui-btn layui-bg-blue" lay-filter="formDemo" lay-submit>查询</a>
					<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
				</div>
            </form>
            
            <!-- <div class="labnum">
                <label>完成率：&nbsp;&nbsp;{{number.count}}&nbsp;&nbsp;</label>
            </div> -->
            </div>

            <div class="tab">
                <table id="demo" lay-filter="test"></table>
            </div>
            
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {maintenancePerformer,areaManage} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            number : {
                count: ''
            }
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var username = sessionStorage.getItem("username")
            var role = sessionStorage.getItem("userRole")
            var unMask = true;
            layui.use(['table','form'], function(){
                var table = layui.table,form = layui.form;
                //查询
                table.render({
                    elem: '#demo'
                    ,height: 'full-260'
                    ,cellMinWidth: 120
                    ,url: that.$url+'maintenancePerformer' //数据接口
                    ,where:{username}
					,method: 'post'
                    ,page: true //开启分页
                    ,limit: 20
                    ,cols: [[ //表头
                        {field: 'id', title: '序号', type:'numbers',width:80,fixed:"left"}
                        ,{field: 'custName', title: '客户名称',minWidth:180,fixed: "left"}
                        ,{field: 'compName', title: '服务区域',minWidth:180}
                        ,{field: 'contractCode', title: '合同号码',minWidth:180}
                        ,{field: 'machCode', title: '机器编码'}
                        ,{field: 'lastTime', title: '保养完成时间',
                            templet:function(d){
                                console.log(d)
                                if(d.lastTime == null){
									return ''
								}else{
									var insDate = layui.util.toDateString(d.lastTime,'yyyy-MM-dd')
									return "<div>"+insDate+"</div>"
								}
                            }
                            // "<div>{{layui.util.toDateString(d.lastTime,'yyyy-MM-dd')}}</div>"
                            
                        }
                        ,{field: 'mainFrequency', title: '保养频率',}
                        ,{field: 'maintenanceState', title: '保养执行状态',
                                templet:function(d){
                                    console.log(d)
                                    if(d.maintenanceState == 0){
                                        return '<span>未完成</span>'
                                    }else if(d.maintenanceState == 1){
                                        return '<span>完成</span>'
                                    }
                                }    
                            }
						,{field: 'coverage', title: '覆盖率(%)'}                        
                        ,{field: 'bwReader', title: '黑白读数',
                            templet: function(d){
                                if(d.device.bwReader == null){
                                    return ''
                                }else{
                                    return d.device.bwReader
                                }
                            }
                        }
                        ,{field: 'coReader', title: '彩色读数',
                            templet: function(d){
                                if(d.device.colorReader == null){
                                    return ''
                                }else{
                                    return d.device.colorReader
                                }
                            }
                        }
                        ,{field: 'materialModel', title: '耗材型号'}
                        ,{field: 'materialNumber', title: '耗材数量'}
                        ,{field: 'responsibleEngineer', title: '责任工程师'}
                        ,{field: 'reservEngineer', title: '后备工程师'}
                        ,{field: 'remark', title: '备注'}
                    ]]
                    ,done:function(res){
                        that.number.count = res.count;
                        console.log(role)
                        if(role != '总部客服' && role != '运维助理'){
                            $("[data-field='coverage']").css('display','none');
                            $("[data-field='bwReader']").css('display','none');
                            $("[data-field='coReader']").css('display','none');
                            $("[data-field='materialModel']").css('display','none');
                            $("[data-field='materialNumber']").css('display','none');
                        }
                        
					}
                });
                form.on('submit(formDemo)', function(data){
                    $('.layui-icon-close').click();
                    var data = JSON.stringify(data.field)
					data = JSON.parse(data)
					var custName = data.custName
					var serviceArea = data.serviceArea
					var enginnerName = data.enginnerName
                    
					table.render({
						elem: '#demo'
						,height: 'full-260'
						,cellMinWidth: 120
						,url: that.$url+'maintenancePerformer' //数据接口
						,method: 'post'
						,where: {custName,serviceArea,enginnerName,username}
						,page: true //开启分页
                        ,limit: 20
						,cols: [[ //表头
							{field: 'id', title: '序号', type:'numbers',width:80,fixed:"left"}
							,{field: 'custName', title: '客户名称',minWidth:180,fixed:"left"}
							,{field: 'compName', title: '服务区域',minWidth:180}
							,{field: 'contractCode', title: '合同号码',minWidth:180}
							,{field: 'machCode', title: '机器编码'}
							,{field: 'lastTime', title: '保养完成时间',templet:"<div>{{layui.util.toDateString(d.lastTime,'yyyy-MM-dd')}}</div>"}
							,{field: 'mainFrequency', title: '保养频率',}
							,{field: 'maintenanceState', title: '保养执行状态',
                                templet:function(d){
                                    console.log(d)
                                    if(d.maintenanceState == 0){
                                        return '<span>未完成</span>'
                                    }else if(d.maintenanceState == 1){
                                        return '<span>完成</span>'
                                    }
                                }    
                            }
							,{field: 'coverage', title: '覆盖率(%)'}
							,{field: 'bwReader', title: '黑白读数',
                                templet: function(d){
                                    if(d.device.bwReader == null){
                                        return ''
                                    }else{
                                        return d.device.bwReader
                                    }
                                }
                            }
							,{field: 'coReader', title: '彩色读数',
                                templet: function(d){
                                    if(d.device.colorReader == null){
                                        return ''
                                    }else{
                                        return d.device.colorReader
                                    }
                                }
                            }
							,{field: 'materialModel', title: '耗材型号'}
							,{field: 'materialNumber', title: '耗材数量'}
							,{field: 'responsibleEngineer', title: '责任工程师'}
							,{field: 'reservEngineer', title: '后备工程师'}
							,{field: 'remark', title: '备注'}
						]]
					});
                    return false;
                });
                form.render();
                
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                
            });
        },
		exportExcel(){
			var that = this
			that.$exportE('exportmaintenancePerformer')		
		}
    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



