<script>
// 设备管理
import Index from '../../index'
import {doAddEquipment,deviceChange,downQrCodeAll,changeEquipment,equipmentInfo,areaManage} from '../../../config/axiosUtil'

export default {
    name: "EquipmentManage",
    components: {
        Index
    },
    data(){
        return{
			flag: '',
			issecret: '',
			// 新增修改数据
			formV: {		
				ID: '',
				assetAttr: '',
				assetNumber: '',
				holdDepartment: '',
				holdMan: '',
				assetClass: '',
				assetStatus:'',
				specifications: '',
				// devName: '',
				machCode: '',
				deviceType: '',
				unitType: '',
				esNumber: '',
				deviceBrand: '',
				outputSpec: '',
				deviceBound: ''
			},
			// 变动数据
			formVa:{
				username: '',
				machCode: '',
				changeType: '',
				serviceArea: '',
				linkman:'',
				linkmanTel:'',
				location: '',
				department: '',
				serviceLevel: '',
				responsibleEngineer: '',
				reserveEnginner: '',
				IP: '',
				bwReader: '',
				colorReader: '',
				installedTime: '',
				secret: '',
				secretLevel: ''
			},
			number: {
				count: '',
			},
			xunwei:true,
			xunweis:true,
			addShow: false,
			limita:0,
			
		}
    },
    mounted() {
        var Status = this.$route.params.woStatus
        this.getData(Status)
    },
    methods:{
		wac(value){
			if(value == 'true'){					
				this.issecret = true				
			}else{
				this.issecret = false	
				this.formVa.secretLevel = ''			
			}
		},
        getData(woStatus){

            var that = this;
			var uname = sessionStorage.getItem('username')
			var role = sessionStorage.getItem("userRole")
			var serviceA = sessionStorage.getItem("serviceArea")
			var vbb = ''
			console.log(that.showA())
			if(that.showA()){
				vbb = '#toolbarDemo'
			}else{
				vbb = ''
			}
			var unMask = true;
            layui.use(['table','form','laydate','util'], function(){
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                // 日期
                laydate.render({
                    elem: '#startDate'
                });
                laydate.render({
                    elem: '#endDate'
                });
				form.render();
                table.render({
                    elem: '#demo'
					// ,method: 'post'
                    ,height: 'full-320'
                    ,cellMinWidth: 120,
					toolbar: vbb
                    ,url: that.$url+'equipmentInfo' //数据接口
					,where:{username:uname,woStatus}
                    ,page: true //开启分页
					,limit: 20
                    ,cols: [[ //表头
                        {field: 'check',type: 'checkbox', fixed: 'left'}
                        ,{field: 'id', title: '序号', type:'numbers',width:80,fixed: 'left'}
                        ,{field: 'custArea', title: '客户名称',minWidth:180,fixed: 'left'}
                        ,{field: 'serviceArea', title: '服务区域',minWidth:180}
                        ,{field: 'contractNo', title: '合同编码',}
                        ,{field: 'deviceType', title: '设备类型',}
                        ,{field: 'machCode', title: '机器编码',}
                        ,{field: 'esNumber', title: '设备序列号',}
                        ,{field: 'unitType', title: '设备型号',}
                        ,{field: 'location', title: '设备位置'}
                        ,{field: 'deviceBrand', title: '设备品牌'}
                        ,{field: 'ip', title: '设备IP'}
                        ,{field: 'installedTime', title: '安装日期',
							templet:function(d){
								if(d.installedTime == ''){
									return ''
								}else{
									var insDate = layui.util.toDateString(d.installedTime,'yyyy-MM-dd')
									return "<div>"+insDate+"</div>"
								}
							}
								// "<div>{{layui.util.toDateString(d.installedTime,'yyyy-MM-dd')}}</div>"
						}
                        // ,{field: '', title: '服务年限'}
                        ,{field: 'assetAttr', title: '资产属性'}
						,{field: 'assetStatus', title: '资产状态'}
                        ,{field: 'assetNumber', title: '资产编码'}
                        ,{field: 'secret', title: '是否涉密设备'}
                        // ,{field: '', title: '备注'}
                        ,{ field: 'czx',fixed: 'right', title: '操作项',width:300,
							templet: function (d) {
								var arr2 = ''
								return that.isShow(d);
							}
						}
                    ]]
					,id: 'test'
					,done:function(res){
						console.log(res)
						var data = res.data;
						if(role == '客户'){
                            $("[data-field='assetNumber']").css('display','none');
							$("[data-field='czx']").css('display','none');
							$("[data-field='check']").css('display','none');
                        }else if(role == '总部客服' || role == '运维总监' || role == '运维经理' || role == '技术主管'){
							$("[data-field='czx']").css('display','none');
							$("[data-field='check']").css('display','none');
						}
					}
                });
				// 服务区域下拉内容
				that.$serviceArea(areaManage)
                
				table.on('tool(demo)',function(obj){					
					var data = obj.data
					if(obj.event == 'view'){
						var machCode = data.machCode
						that.$exportEa('qrCode?machCode='+machCode)
					}else if(obj.event == 'exportQode'){
						//导出二维码
						var machCode = data.machCode
						window.open(that.$url+'downQrCode?machCode='+machCode+'&'+'username='+uname)
					// 修改
					}else if(obj.event == 'edit'){
						console.log(data)
						that.flag = 'edit'
						that.formV.ID = data.id
						that.formV.assetAttr = data.assetAttr
						that.formV.assetNumber = data.assetNumber
						that.formV.assetStatus = data.assetStatus
						that.formV.holdDepartment = data.holdDepartment
						that.formV.holdMan = data.holdMan
						that.formV.assetClass = data.assetClass
						that.formV.specifications = data.specifications
						// that.formV.devName = data.devName
						that.formV.machCode = data.machCode
						that.formV.deviceType = data.deviceType
						that.formV.unitType = data.unitType
						that.formV.esNumber = data.esNumber
						that.formV.deviceBrand = data.deviceBrand
						that.formV.outputSpec = data.outputSpec
						that.formV.deviceBound = data.deviceBound
						// if(){}
						// that.xunweis = true
						layer.open({
							type:1,
							title: '修改',
							area: ['700px','500px'],
							content: $('.sdd')
						})	
					//设备变动
					}else if(obj.event == 'change'){
							console.log(data)
							that.flag = 'change'
							that.formVa.machCode = data.machCode
							that.formVa.changeType = data.changeType
							that.formVa.serviceArea = data.serviceArea
							that.formVa.linkman = data.custLinkman
							that.formVa.linkmanTel = data.linkmanPhone
							that.formVa.location = data.location
							that.formVa.department = data.department
							that.formVa.serviceLevel = data.serviceLevel
							that.formVa.responsibleEngineer = data.responsibleEngineer
							that.formVa.reserveEnginner = data.reserveEnginner
							that.formVa.IP = data.ip
							that.formVa.bwReader = data.bwReader
							that.formVa.colorReader = data.colorReader
							that.formVa.installedTime = data.installedTime
							// if(data.installedTime == null){
							// 	that.formVa.installedTime = ''
							// }else{
							// 	that.formVa.installedTime = that.$date(data.installedTime)
							// }
							
							
							if(data.secret == '是'){
								that.formVa.secret = true
								that.issecret = true
							}else if(data.secret == '否'){
								that.formVa.secret = false
								that.issecret = false
							}
							if(data.secretLevel == '秘密'){								
								that.formVa.secretLevel = '1'
							}else if(data.secretLevel == '机密'){								
								that.formVa.secretLevel = '2'
							}else if(data.secretLevel == '绝密'){								
								that.formVa.secretLevel = '3'
							}else{
								that.formVa.secretLevel = data.secretLevel		
							}
							// this.resOne = false;							
							// this.resTwo = false;							
							// this.resThree = false;	
							// this.showImg = true;
							// this.showImg1 = true;
							// this.showImg2 = true;				
							//变动
							layer.open({
								type: 1,
								title: '变动',
								content: $('#windows'),
								area: ['900px', '450px'],
							});
						}
					
				})			
				//批量导出二维码
			  table.on('toolbar(demo)', function(obj){
				var checkStatus = table.checkStatus(obj.config.id);
				if(obj.event == 'getCheckData'){
					var data = checkStatus.data;
					data = JSON.stringify(data)
					data = JSON.parse(data)
					var arr = []
					for(var i = 0;i<data.length;i++){
						arr.push(data[i].machCode)
					}
					arr = arr.join(',')
					arr = JSON.stringify(arr)
					var v = {
						machCodeArr: arr
					}
					console.log(arr)
					console.log(v)
					if(arr === '""'){
						console.log("jhjh")
						layer.msg('请选择导出的信息!')
					}else{
						downQrCodeAll(v)
						.then((res) => {
							window.open(that.$url+'downQrCodeAll?machCodeArr='+arr+'&'+'username='+uname)
						})
					}
				}
			  });	 
            });
        },
		//查询
		selectAll(){
			$('.layui-icon-close').click();
			var that = this
			var vbb = ''
			var role = sessionStorage.getItem("userRole")
			if(that.showA()){
				vbb = '#toolbarDemo'
			}else{
				vbb = ''
			}
			layui.use(['table','form','laydate'], function(){
				var table = layui.table,form = layui.form,laydate = layui.laydate;
				form.on('submit(formDemo)', function(data){
					// layer.msg(JSON.stringify(data.field));
					var custName = data.field.custName
					var serviceArea = data.field.serviceArea
					var assetAscription = data.field.assetAscription
					var machCode = data.field.macNum
					var username = sessionStorage.getItem("username")
					var woStatus = ''
					table.render({
						elem: '#demo'
						,height: 'full-320'
						,cellMinWidth: 120
						,toolbar: vbb
						,url: that.$url+'equipmentInfo' //数据接口
						,method: 'post'
						,where: {custName,serviceArea,assetAscription,machCode,username,woStatus}
						,page: true //开启分页
						,limit: 20
						,cols: [[ //表头
							{type: 'checkbox', fixed: 'left'}
							,{field: 'id', title: '序号', type:'numbers',width:80,fixed: 'left'}
							,{field: 'custArea', title: '客户名称',minWidth: 180,fixed: 'left'}
							,{field: 'serviceArea', title: '服务区域',minWidth: 180}
							,{field: 'contractNo', title: '合同编码',}
							,{field: 'deviceType', title: '设备类型',}
							,{field: 'machCode', title: '机器编码',}
							,{field: 'esNumber', title: '设备序列号',}
							,{field: 'unitType', title: '设备型号',}
							,{field: 'location', title: '设备位置'}
							,{field: 'deviceBrand', title: '设备品牌'}
							,{field: 'installedTime', title: '安装日期',
								templet:function(d){
									if(d.installedTime == ''){
										return ''
									}else{
										var insDate = layui.util.toDateString(d.installedTime,'yyyy-MM-dd')
										return "<div>"+insDate+"</div>"
									}
								}
							}
							// ,{field: '', title: '服务年限'}
							,{field: 'assetAttr', title: '资产属性'}
							,{field: 'assetStatus', title: '资产状态'}
							,{field: 'assetNumber', title: '资产编码'}
							,{field: 'secret', title: '是否涉密设备'}
							// ,{field: '', title: '备注'}
							,{field: 'czx', fixed: 'right', title: '操作项',width:300,
								templet: function (d) {
									var arr2 = ''
									return that.isShow(d);
								}
							}
						]],
						id: 'test'
						,done:function(res){
							console.log(res,role)
							var data = res.data;
							if(role == '客户'){
								$("[data-field='assetNumber']").css('display','none');
								$("[data-field='czx']").css('display','none');
							}else if(role == '总部客服' || role == '运维总监' || role == '运维经理' || role == '技术主管'){
								$("[data-field='czx']").css('display','none');
							}
						}
					});
					return false;
				});
			})
		},
		//新增
		add(){
			var that = this
			that.flag = 'add'			
					
			var al = Object.keys(that.formV).length
			for(var a in that.formV){
				that.formV[a] = ''
				that.formV.assetAttr = ''
				that.formV.ID = ''
				that.xunwei = false
				that.xunweis = false	
			}
			layui.use(['table','form','laydate'], function(){
				var table = layui.table, layer = layui.layer;
				var form = layui.form;				
				layer.open({
					type:1,
					title: '新增',
					area: ['700px','500px'],
					content: $('.sdd')
				})	
			})

		},
		// 新增/修改/变动提交
		subMit(){
			var that = this
			if(that.flag == 'add'){
				// console.log(that.formV)
				doAddEquipment(that.formV)
				.then((res) => {
					if(res.data.code == 0){
						that.$layerClose('test')
						that.$layerMsg('新增成功')								
					}else{								
						that.$layerMsg(res.data.msg)
					}
				})
				.catch((error) => {					
				})
			// 修改
			}else if(that.flag == 'edit'){
				if(that.formV.assetAttr != '迅维'){
					that.formV.holdMan = ''
					that.formV.holdDepartment = ''
					that.formV.assetNumber = ''
					that.formV.assetStatus = ''
				}
				deviceChange(that.formV)
				.then((res) => {
					if(res.data.code == 0){
						that.$layerClose('test')
						that.$layerMsg('修改成功')								
					}else{								
						that.$layerMsg(res.data.msg)
					}
				})
				.catch((error) => {					
				})
			// 变动
			}else if(that.flag == 'change'){
				// console.log(that.formVa)
				that.formVa.username = sessionStorage.getItem('username')
					changeEquipment(that.formVa)
					.then((res) => {
						if(res.data.code == 0){
							that.$layerClose('test')
							that.$layerMsg('变动成功')	
							// this.resOne = false;							
							// this.resTwo = false;							
							// this.resThree = false;	
							// this.showImg = true;
							// this.showImg1 = true;
							// this.showImg2 = true;					
						}else{								
							that.$layerMsg(res.data.msg)
						}
					})
					.catch((error) => {
						console.log(error)
					})
				
			}
		},
		exportExcel(){
			var that = this;
			that.$exportE('exportEquipment')	
		},
		showA(){
			var that = this
			var arr = [that.$admin.ywzl,that.$admin.zxcry,that.$admin.kfzg,that.$admin.admin]
			console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
					return true
				}
			}
		},
		isShow(){
			var that = this			
			var arr2 = that.$admin
			console.log(arr2)
			var admin = ''
			if(arr2.admin == that.$userRole || arr2.zxcry == that.$userRole|| arr2.kfzg == that.$userRole|| arr2.ywzl == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="view">查看二维码</a><a class="layui-btn layui-btn-xs" lay-event="exportQode">导出二维码</a><a class="layui-btn layui-btn-xs" lay-event="change">变动</a><a class="layui-btn layui-btn-xs" lay-event="edit">修改</a>'
				// console.log(that.$noAdmin)
			}
			// else if(arr2.zbkf == that.$userRole){
			// 	admin = '<a class="layui-btn layui-btn-xs" lay-event="view">查看二维码</a><a class="layui-btn layui-btn-xs" lay-event="exportQode">导出二维码</a>'
			// }
			else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
				// console.log(that.$noAdmin)
			}
			return admin;
			
		},
		// 点击增加后备工程师文本框
		// showEng(){
		// 	this.resOne = true;
		// 	this.showImg = false;
		// },
		// showEng1(){
		// 	this.resTwo = true;
		// 	this.showImg1 = false;
		// },
		// showEng2(){
		// 	this.resThree = true;
		// 	this.showImg2 = false;
		// },
    },
	created(){
		this.showA()
    },
	watch:{
        newValue(val, oldVal){
            console.log("b.c: "+val, oldVal);
			console.log(this.flag)
			if(this.flag == 'edit'){
				if(val == '迅维'){
					this.xunwei = true;
					this.xunweis = true;
					
				}else if(val == '客户'){
					this.xunwei = false;
					this.xunweis = false;
				}else if(val == '其他'){
					this.xunwei = false
					this.xunweis = false
				}else{
					this.xunwei = false
					this.xunweis = false
				}
			}else{
				this.xunweis = false
				if(val == '迅维'){
					this.xunwei = true;
					
				}else if(val == '客户'){
					this.xunwei = false;
					this.formV.holdMan = ''
					this.formV.holdDepartment = ''
					this.formV.assetNumber = ''
				}else if(val == '其他'){
					this.xunwei = false
					this.formV.holdMan = ''
					this.formV.holdDepartment = ''
					this.formV.assetNumber = ''
				}else{
					this.xunwei = false
				}
			}
			
        }
    },
	computed: {
    　　newValue() {
    　　　　return this.formV.assetAttr
    　　}
    },
	destroyed() {
        $('.layui-icon-close').click();
		// sessionStorage.removeItem('serviceAddress')
	},

}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.winLabel{
display: inline-block;
width: 130px;
height: 38px;
line-height: 38px;
text-align: right;
padding: 0 10px;
}
.submitNow{
	margin: 30px 180px;
}
.submitNow button{
	width: 150px;
	height: 38px;
	background: #1E9FFF;
    border: none;
    color: #fff;
    font-size: 16px;
}
.divW{
	margin-bottom: 15px;
}
.divW input[type=text],.divW select{
	width: 380px;
	height: 38px;
}
</style>



