<template>
<!-- 运维经理首页 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <div class="up">
                <div class="bigpic"><img src="../../../../static/images/xuwei.png" width="100%" height="350px" id="chart"/>
                    <!-- <div class="bigpic-font"><p>持续改善、优质运维、客户满意</p></div> -->
                </div>
                <div class="rightdiv">
                    <!--待受理客户事故及需求数量-->
                    <div class="bghbpink dslsg">
                        <p class="describe"><router-link :to="{name:'WorkMsg',params:{woStatus:'c'}}">超时工单</router-link></p>
                        <p class="content">{{csgd}}</p>
                    </div>
                    <!--合同管理-->
                    <div class="htgl">
                        <div class="title">合同管理</div>
                        <div class="subordinate">
                            <ul>
                                <li><router-link :to="{name: 'ContractMsg',params:{woStatus: 'a'}}">合同总数</router-link></li>
                                <li><span>{{htzs}}</span></li>
                            </ul>
                            <ul>
                                <li><router-link :to="{name: 'ContractMsg',params:{woStatus: 'b'}}">已到期</router-link></li>
                                <li><span>{{ydqht}}</span></li>
                            </ul>
                            <ul>
                                <li><router-link :to="{name: 'ContractMsg',params:{woStatus: 'c'}}">一年内到期</router-link></li>
                                <li><span>{{ynndq}}</span></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="layui-clear contractD">
                <!--工单概况-->
                <dl class="contract-two">
                    <dd class="title">工单概况</dd>
                    <dd class="subordinate customerBox">
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'a'}}">本月工单</router-link></dd>
                            <dd><span class="content">{{bygd}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'b'}}">未完成</router-link></dd>
                            <dd><span class="content">{{wwc}}</span></dd>
                        </dl>

                    </dd>
                </dl>
                <!--客户满意度-->
                <dl class="contract-two">
                    <dd class="title">客户满意度</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'a'}}">用户评价</router-link></dd>
                            <dd><span class="content">{{ yhpj }}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'b'}}">客户投诉</router-link></dd>
                            <dd><span class="content">{{khts}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'ContractSatis',params:{woStatus: 'c'}}">客户表扬</router-link></dd>
                            <dd><span class="content">{{khby}}</span></dd>
                        </dl>
                    </dd>
                </dl>
            </div>
            
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {operations} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            ynndq:'',//一年内到期合同
            htzs:'',//合同总数
            khts:'',//客户投诉
            khby:'',//客户表扬
            ydqht:'',//已到期合同
            bygd:'',//本月工单
            csgd:'',//超时工单
            wwc:'',//未完成
            yhpj:'',//用户评价
        }
    },
    mounted() {
        this.getData()
        var that = this;
        var heightCss = $('#chart').outerHeight();
        this.height = heightCss - '79' +'px';
        this.lineHeight = heightCss - '79' +'px';
        console.log(heightCss)
    },
    methods:{
        getData(){
            var that = this;
            var userN = sessionStorage.getItem('username');
            var userName = {username : userN}
            operations(userName).then((res) => {
                that.ynndq = res.data.一年内到期;
                that.htzs = res.data.合同总数;
                that.khts = res.data.客户投诉;
                that.khby = res.data.客户表扬;
                that.ydqht = res.data.已到期;
                that.bygd = res.data.本月工单;
                that.csgd = res.data.超时工单;
                that.wwc = res.data.未完成;
                that.yhpj = res.data.用户评价;
            })
            .catch((error) => {				
            })
        }
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.rightdiv{
    float: left;
    /* background: #aaa; */
    width: 31.5%;
    margin-left: 2%;
}
.dslsg{
    background-size: 100% auto;
    height: 14rem;
    overflow: hidden;
}
.describe{
    margin-top: 50px;
}
.dslsg p,.dslsg p a{
    color: #fff;
    text-align: center;
    font-size: 18px;
}
.htgl .title{
    text-align: center;
}
.htgl .subordinate{
    margin-top: 40px;
    overflow: hidden;
}
.htgl .subordinate ul{
    float: left;
    margin-left: 10%;
    text-align: center;
}
.htgl .subordinate ul li{
    font-size: 18px;
    color: #999;
}
.htgl .subordinate ul li:last-child{
    margin-top: 20px;
    color: #4cbff6;
    font-size: 18px;
}
.subordinate dd a{
    text-decoration: underline;
}
.subordinate li a{
    text-decoration: underline;
}
/* @media screen and (min-width:1280px) and (max-width:1440px){
  .dslsg{height: 15rem;}
} */
/* @media screen and (min-width:1441px) and (max-width:1920px){
  .dslsg{height: 20rem;}
  .htgl .subordinate ul{margin-left: 14%;}
  .describe{margin-top: 80px}
} */

</style>



