<template>
<!-- 运维助理首页 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <div class="upleft">
                <div class="bigpicA">
                    <img src="../../../../static/images/xuwei.png" width="100%" height="350px"/>
                    <!-- <div class="bigpic-font"><p>持续改善、优质运维、客户满意</p></div> -->
                </div>
                <div class="leftA">
                    
                    <!--工单生成-->
                    <div class="bgred bg-fontA">
                        <!-- <span><router-link to="/home/workMsg/WorkMsgAdd">工单生成</router-link></span> -->
                        <span><a href="javascript:;" @click="windUp">工单生成</a></span>
                    </div>
                    <!--账单管理-->
                    <div class="bghbgreen bg-fontA">
                        <span>账单管理</span>
                    </div>
                    <!--耗材管理-->
                    <div class="bghbpink bg-fontA">
                        <span>耗材管理</span>
                    </div>
                    <!--设备管理 首页下的设备概况-->
                    <div class="bghblue bg-fontA">
                        <span><router-link to="/home/equipmentManage">设备概况</router-link></span>
                    </div>
                    <!--零件订购-->
                    <!--<div class="bghblue bg-fontA">
                        <span>零件订购</span>
                    </div>-->
                </div>
            </div>
            <div class="upright">
                <!--工单概况-->
                <dl class="contract-two" style="width: 97%;height: 260px;">
                    <dd class="title">工单概况</dd>
                    <dd class="subordinate">
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'a'}}">本月工单</router-link></dd>
                            <dd><span class="content">{{bygd}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'b'}}">未完成</router-link></dd>
                            <dd><span class="content">{{wwc}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'c'}}">超时工单</router-link></dd>
                            <dd><span class="content">{{csgd}}</span></dd>
                        </dl>
                    </dd>
                </dl>
                <!--客户满意度-->
                <dl class="contract-two" style="width: 97%;height: 255px;margin: 20px 0;">
                    <dd class="title">客户管理</dd>
                    <dd class="subordinate customerBox">
                        <dl>
                            <dd><router-link to="/home/contractMsg">合同管理</router-link></dd>
                        </dl>
                        <dl>
                            <dd><router-link to="/home/contractSatis">客户满意度</router-link></dd>
                        </dl>
                        
                    </dd>
                </dl>
                <div class="bghblue bg-fontA" style="width: 100%;clear: both;margin-top: -10px;">
                    <div style="margin-top: 60px;" @click="windj()"><a href="javascript:;" style="color: #f5f5f5;margin-top: 60px;">待订零件及耗材</a></div>
                    <span style="line-height: 0;">{{hcsl}}</span>
                </div>
            </div>
            
        </div>
        <!-- 工单生成 -->
        <div class="windUp dis-n">
            <div class="demand">
                <!-- <div class='divW'>
                    <label class='winLabel'>工单号</label>
                    <div class="layui-input-inline">
                        <input type="text" readonly="value"  class="layui-input" v-model="equipBreak.department">
                    </div>
                </div> -->
                <div class='divW'>
                    <label class='winLabel'><span>*</span>服务区域</label>
                    <div class="layui-input-inline">
                        <select name="" id="" v-model="formVa.serviceArea" class="allinput">
                            <option value="" id="address">请选择</option>
                            <!-- <option :value="item.compName" v-for="item in serArea">{{item.compName}}</option> -->
                        </select>                                
                    </div>
                </div>
                <div class='divW'>
                    <label class='winLabel'><span>*</span>客户名称</label>
                    <div class="layui-input-inline">
                        <input type="text" v-model="formVa.custName" class="layui-input" @keyup="custNameScreen()" @blur="blurHid()" autocomplete="off">     
                        <div class="nameData" v-show="isShow">
                            <div>请选择</div>
                            <div v-for="(item,index) in keyupData" :key="index" @click="namecust(item.custName)">{{item.custName}}</div>
                        </div>                   
                    </div>
                </div>
                <div class='divW'>
                    <label class='winLabel'><span>*</span>报修人</label>
                    <div class="layui-input-inline">
                        <input type="text" v-model="formVa.repairMan" class="layui-input" autocomplete="off">
                    </div>
                </div>
                <div class='divW'>
                    <label class='winLabel'><span>*</span>报修人联系电话</label>
                    <div class="layui-input-inline">
                        <input type="text" v-model="formVa.repairService" class="layui-input" autocomplete="off">
                    </div>
                </div>
                <div class='divW'>
                    <label class='winLabel'><span>*</span>服务类型</label>
                    <div class="layui-input-inline">
                        <input type="radio" name="" v-model="formVa.faultType" @click='wac2("b")' value='b' class=""><label>事件类</label>
                        <input type="radio" name="" v-model="formVa.faultType" @click='wac2("c")' value='c' class=""><label>需求类</label>
                    </div>
                </div>
                <div class='divW' v-if='flag3'>
                    <label class='winLabel'><span>*</span>服务类别</label>
                    <div class="layui-input-inline">
                        <div class="layui-input-inline">
                        <input type="radio" name="" v-model="formVa.accidentType" value='a' class=""><label>保养</label>
                        <input type="radio" name="" v-model="formVa.accidentType" value='b' class=""><label>送货</label>
                        <input type="radio" name="" v-model="formVa.accidentType" value='c' class=""><label>抄表</label>
                        <input type="radio" name="" v-model="formVa.accidentType" value='d' class=""><label>送文件</label>
                        <input type="radio" name="" v-model="formVa.accidentType" value='e' class=""><label>其他</label>
                    </div>
                    </div>
                </div>
                <div class='divW' v-if='flag4'>
                    <label class='winLabel'><span>*</span>服务类别</label>
                    <div class="layui-input-inline">
                        <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("a")' value='a' class=""><label>设备变动</label>
                        <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("b")' value='b' class=""><label>耗材补充</label>
                        <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("c")' value='c' class=""><label>安装实施</label>
                        <input type="radio" name="" v-model="formVa.accidentType2" @click='wac3("d")' value='d' class=""><label>客户培训</label>
                    </div>                        
                </div>
                
                <div class='divW' v-if='flag1'>
                    <label class='winLabel'><span>*</span>设备变动</label>
                    <div class="layui-input-inline">
                        <input type="radio" name="" v-model="formVa.macd" value='a' class=""><label>加机</label>
                        <input type="radio" name="" v-model="formVa.macd" value='b' class=""><label>变更</label>
                        <input type="radio" name="" v-model="formVa.macd" value='c' class=""><label>撤销</label>
                        <input type="radio" name="" v-model="formVa.macd" value='d' class=""><label>移动</label>
                    </div>
                </div>
                <div class='divW' v-if='flag2'>
                    <label class='winLabel'><span>*</span>耗材型号</label>
                    <div class="layui-input-inline">
                        <input type="text" class="layui-input" v-model="formVa.materialModel" autocomplete="off">
                    </div>
                </div>
                
                <div class='divW' v-if='flag2'>
                    <label class='winLabel'><span>*</span>耗材数量</label>
                    <div class="layui-input-inline">
                        <input type="text" class="layui-input" v-model="formVa.materialNumber" autocomplete="off">
                    </div>
                </div>
                <!-- <div class='divW'>
                    <label class='winLabel'>报修时间</label>
                    <div class="layui-input-inline">
                        <input type="text"  class="layui-input">
                    </div>
                </div> -->
                <div class='divW'>
                    <label class='winLabel' style="vertical-align: top;">需求描述</label>
                    <div class="layui-input-inline">
                        <!-- <input type="text" v-model="formV.describe"  class="layui-input" autocomplete="off"> -->
                        <textarea v-model="formVa.describe" autocomplete="off" class="textWid"></textarea>
                    </div>
                </div>
                
            </div> 
            <div class='submitNow'>
                <button @click='addSub()' v-if='refer'>提交需求</button>
                <button v-else>提交需求</button>
            </div>
        </div>
        <!-- 零件订购 -->
        <div class="windj dis-n">
            <!-- <table id="demo" lay-filter="demo"></table> -->
            <div class="datafor">
                <table id="test" lay-filter="test" ></table>
                <!-- <div v-for=items in datafor"><span>工单号：{{items.woNumber}}</span><span>客户名称：{{items.custName}}</span>{{items.partsTypeNumber}}</div> -->
            </div>
        </div>
    </div>
        
</template>
<script>
import Index from '../../index'
import {assistant,noMachCodedoNewOrder,getPartAndNumber,areaManage,customerInfo} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            khts:'',
            khby:'',
            wwc:'',
            bygd:'',
            yhpj:'',
            bygd:'',
            csgd:'',
            hcsl:'',
            formVa: {				
				custName: '',
				serviceArea: '',
				repairMan: '',
				repairService: '',
				faultType: '',
				accidentType: '',
				accidentType2: '',
				macd: '',
				materialModel:'',// 耗材型号 
				materialNumber: '', // 耗材数量
				username: window.sessionStorage.getItem('username'),
                describe:''
			},
			flag1: '',
			flag2: '',
			flag3: '',
			flag4: '',
            datafor:'',
            serArea:[],
            keyupData:'',
            isShow: false,
            refer: true
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var userN = sessionStorage.getItem('username');
            var userName = {username : userN}
            assistant(userName).then((res) => {
                that.khts = res.data.客户投诉;
                that.khby = res.data.客户表扬;
                that.wwc = res.data.未完成;
                that.bygd = res.data.本月工单;
                that.yhpj = res.data.用户评价;
                that.csgd = res.data.超时工单;
                that.hcsl = res.data.待定零件和耗材;
            })
            .catch((error) => {				
            })
            // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
        },
        //客户名称筛选
        custNameScreen(){
            var that = this;
            that.isShow = true;
            var custNames ={
                custName:that.formVa.custName
                } ;
            customerInfo(custNames).then((res)=>{
                console.log(res)
                that.keyupData = res.data.data
                console.log(that.keyupData)
            }).catch((res) => {
                // that.$layerMsg('请求出错')
            })
        },
        namecust(inx){
            console.log(inx)
            this.formVa.custName = inx
            this.isShow = false;
        },
        blurHid(){
            var that = this;
            setTimeout(() => {
                that.isShow = false;
            }, 300);

        },
        windUp(){
            layui.use('layer',function(){
                var layer = layui.layer;
                var index = layer.open({
                    type: 1,
                    title: '工单生成',
                    area: ['700px','500px'],
                    content: $('.windUp')
                });	
            })
            var userN = {username:sessionStorage.getItem('username')}
            areaManage(userN).then((res) => {
                var data = res.data.data;
                console.log(data)
				this.serArea = res.data.data
            })
            .catch((error) => {
            })
        },
        wac2(value){
			// alert(value)
			if(value == 'b'){					
				this.flag3 = true	
				this.flag1 = false
				this.flag2 = false
				this.flag4 = false		
				this.formVa.accidentType2 = ''
				this.formVa.macd = ''
				this.formVa.materialModel = ''
				this.formVa.materialNumber	 = ''
			}else if(value == 'c'){
				this.flag3 = false	
				this.flag1 = false
				this.flag2 = false
				this.flag4 = true
				this.formVa.accidentType = ''
				this.formVa.macd = ''
				this.formVa.materialModel = ''
				this.formVa.materialNumber	 = ''
			}
		},	
        wac3(value){
			// alert(value)
			if(value == 'a'){					
				this.flag1 = true	
				this.flag2 = false		
				this.formVa.materialModel = ''
				this.formVa.materialNumber = ''	
			}else if(value == 'b'){
				this.flag1 = false	
				this.flag2 = true
				this.formVa.macd = ''
			}else{
				this.flag1 = false	
				this.flag2 = false
				this.formVa.materialModel = ''
				this.formVa.materialNumber = ''
				this.formVa.macd = ''
			}
		},
        // 工单提交
        addSub(){
			var that = this
            that.refer = false
				noMachCodedoNewOrder(that.formVa)
				.then((res) => {
					if(res.data.code == 0){
                        that.$layerClose()
						that.$layerMsg(res.data.msg)
						// that.$router.go(0)
                        that.refer = true
                        that.$router.push({path:'/home/workMsg',params:{user: '1'}})
                        // custName= '';
                        // serviceArea= '';
                        // repairMan= '';
                        // repairService= '';
                        // faultType= '';
                        // accidentType= '';
                        // accidentType2= '';
                        // macd= '';
                        // materialModel='';// 耗材型号 
                        // materialNumber= ''; // 耗材数量
                        // describe=''
					}else{
                        // that.$layerClose()
						that.$layerMsg(res.data.msg)
                        that.refer = true
                        
					}
				})
				.catch((res) => {
                    that.$layerClose()
					that.$layerMsg('请求出错')
				})
		},
        windj(){
            var that = this;
            layui.use(['layer','table'],function(){
                var layer = layui.layer,table = layui.table;
                var indexs = layer.open({
                    type: 1,
                    title: '待订零件及耗材',
                    area: ['1000px','450px'],
                    content: $('.windj')
                });	
                var username = sessionStorage.getItem('username');

                // var userName = {username : username};
                table.render({
						elem: '#test'
						,height: 350
                        ,width: 1000
						,cellMinWidth: 150
						,url: that.$url+'getPartAndNumber' //数据接口
                        ,where:{username : username}
						,method: 'post'
						,where: {username}
						// ,page: true //开启分页
						,cols: [[ //表头
							,{field: 'custName', title: '客户名称',sort: true,}
							,{field: 'woNumber', title: '工单号码',sort: true,}
							,{field: 'machCode', title: '机器编码',sort: true,}
							,{field: 'enginner', title: '工程师姓名',sort: true,}
							,{field: 'serviceType', title: '耗材名称及数量',sort: true,}
							,{field: 'partsTypeNumber', title: '零件名称及数量',}
						]]
                        ,id: 'testReload'
                        ,parseData: function(res){ //将原始数据解析成 table 组件所规定的数据
                            console.log(res)
                            return {
                                "code": res.code, //解析接口状态
                                // "msg": res.message, //解析提示文本
                                // "count": res.total, //解析数据长度
                                "data": res.待定零件及耗材 //解析数据列表
                            };
                        }
				});
            })
        },
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.addWin{
        width:80%;
        height:87%;
        padding:3% 10%;
        background:#fff;
        overflow: auto;
    }
    .divW{
        margin-top: 15px;
    }
    .divW .layui-input-inline,.divW select{
        width: 300px;
    }
    .divW textarea{
        width: 100%;
        height: 65px;
    }
    .winLabel{
        display: inline-block;
        width: 130px;
        height: 38px;
        line-height: 38px;
        text-align: right;
        padding: 0 10px;
    }
    .submitNow{
        margin: 20px 180px;
    }
    .submitNow button{
        width: 120px;
        height: 38px;
        line-height: 38px;
        border: none;
        background: #009db1;
        color: #fff;
    }
    .datafor{
        width: 100%;
        height: 100%;
    }
    .datafor div{
        font-size: 18px;
        margin-left: 30px;
        margin-top: 20px;
    }
    .datafor div span{
        margin: 0 10px;
    }
    .upright dd a{
        text-decoration: underline;
    }
    .nameData{
        position: absolute;
        z-index: 9999999;
        background: #fff;
        width: 300px;
        border: 1px #ddd solid;
        
    }
    .nameData>div{
        height: 30px;
        line-height: 30px;
        position: relative;
    }
    .nameData>div:hover{
        background: #009fe8;
        cursor: pointer;
    }
    
</style>



