<template>
    <!-- 客户满意度管理 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;客户满意度管理&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">客户名称</label>
                    <div class="layui-input-block allinput">
                        <input type="text" name="name"   lay-verify="" placeholder="" class="layui-input" autocomplete="off">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block">
                        <!-- <input type="text" name="serAddr"   lay-verify="" placeholder="" autocomplete="off" class="layui-input"> -->
                        <select name="serAddr" lay-verify="">
                            <option value=""  id="address"></option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">服务评价</label>
                    <div class="layui-input-block">
                        <select name="custSat" lay-verify="">
                            <option value="">请选择</option>
                            <option value="5">非常满意</option>
                            <option value="4">满意</option>
                            <option value="3">一般</option>
                            <option value="2">不满意</option>
                            <option value="1">非常不满意</option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">开始日期</label>
                    <div class="layui-input-inline" style="margin-right: 0">
                        <input type="text" class="layui-input" name="startDate" id="startDate" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">结束日期</label>
                    <div class="layui-input-inline">
                        <input type="text" class="layui-input" name="endDate" id="endDate" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
            </div>
            <div class="btn">
                <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome" id="select">查询</a>
                <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
            </div>
            </form>
            
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <script type="text/html" id="barDemo">
                <a class="layui-btn layui-btn-xs" lay-event="evaluate">评价</a>
            </script>
        </div>
        <!-- 评价 -->
        <div class="appraise dis-n">
            <form class="layui-form" lay-filter="example">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">工单号</label>
                        <div class="layui-input-inline">
                            <input type="text" name="woNumber" class="layui-input" unselectable='on' readonly autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <label class="layui-form-label">星级好评</label>
                    <div><div id="test6"></div></div>
                </div>
                <div class="layui-form-item">
                    <label class="layui-form-label">评价内容</label>
                    <div class="layui-input-block">
                        <textarea placeholder="请输入内容" class="layui-textarea" v-model="textarea"></textarea>
                    </div>
                </div>
                <div class="layui-form-item btn">
                    <button class="layui-btn layui-bg-blue" lay-submit="" lay-filter="evalSub">提交</button>
                    <button type="reset" class="layui-btn dis-n" id="reset">重置</button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {doAddCustSatisfa,customerSatisfactionManager,areaManage} from '../../../config/axiosUtil'

export default {
    name: "ContractSatis",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            starValue:'',
            textarea:''
        }
    },
    mounted() {
        var Status = this.$route.params.woStatus
		this.getData(Status)
    },
    methods:{
        getData(woStatus){
            var that = this;
            var uname = sessionStorage.getItem('username');
            var uRole = sessionStorage.getItem("userRole")
            var unMask = true
            layui.use(['form','laydate','table','rate','layer'], function(){
                var form = layui.form,laydate = layui.laydate,table = layui.table,rate = layui.rate,layer = layui.layer;
                
                laydate.render({
                    elem: '#startDate'
                });
                laydate.render({
                    elem: '#endDate'
                });
                form.render();
                table.render({
                    elem: '#demo'
                    ,height: 'full-320'
                    ,cellMinWidth: 150
                    ,url: that.$url+'customerSatisfactionManager'  //数据接口
                    ,where:{username:uname,woStatus}
                    ,cols: [[ //表头
                        {field: '', title: '序号', type:'numbers',width:80,fixed:"left"}
                        ,{field: 'custName', title: '客户名称',minWidth:220,fixed:"left"}
                        ,{field: 'woNumber', title: '工单号码',minWidth: 160}
                        ,{field: 'serviceArea', title: '服务区域',minWidth: 180}
                        ,{field: 'repairMan', title: '报修人'}
                        ,{field: 'repairPhone', title: '报修电话',}
                        ,{field: 'enginnerName', title: '维修工程师'}
                        ,{field: 'custScore', title: '星评得分',width:100}
                        ,{field: 'custEva', title: '评价内容'}
                        ,{field: 'czx',fixed: 'right', title: '操作项',width:80,
                            templet: function (d) {
								var arr2 = ''
                                console.log(d.custScore)
                                if(d.custScore == null){
                                    return that.isShow(d);
                                }else{
                                    return ''
                                }
								
							}
                        }
                    ]]
                    ,id: 'testReload'
                    ,page: true //开启分页
                    ,limit: 20
                    ,done: function(res){
						console.log(res.data)
                        if(uRole != '客户' && uRole != '总部客服'){
                            $("[data-field='czx']").css('display','none');
                        }
					}
                });
                // 服务区域下拉内容
                that.$serviceArea(areaManage)
                // 点击评价弹出窗口
                table.on('tool(demo)',function(obj){
                    if(obj.event == 'evaluate'){
                        // 点星评价
                        rate.render({
                            elem: '#test6'
                            ,value: 0
                            ,text: true
                            ,setText: function(value){ //自定义文本的回调
                                var arrs = {
                                    '1': '非常不满意'
                                    ,'2': '不满意'
                                    ,'3': '一般'
                                    ,'4': '满意'
                                    ,'5': '非常满意'
                                };
                                this.span.text(arrs[value] || ( value + "星"));
                                console.log(value)
                                that.starValue = value;
                            }
                        })
                        that.textarea = '';
                        var woNumber = obj.data.woNumber;
                        layer.open({
                            type: 1,
                            title: '评价',
                            area: ['700px','400px'],
                            content: $('.appraise')
                        });
                        form.val('example',{
                            "woNumber": woNumber  // 工单号码
                        })
                    }
                })
                //监听提交
                form.on('submit(evalSub)', function(data){
                    var starValue = that.starValue;
                    var textarea = that.textarea;
                    var woNumber = data.field.woNumber;
                    if(starValue == 0){
                        layer.msg("请进行评选")
                    }else if(textarea == ''){
                        layer.msg("请填入评价内容")
                    }else{
                        var evalContent = {woNumber:woNumber,custScore:starValue,custEva:textarea}
                        doAddCustSatisfa(evalContent).then((res) => {
                            if(res.data.code == 0){
                                $("#reset").click();
                                layer.closeAll();
                                layer.msg(res.data.data);
                                table.reload('testReload');
                            }else if(res.data.code == 1){
                                $("#reset").click();
                                layer.closeAll();
                                table.reload('testReload');
                                layer.msg(res.data.data);
                            }
                        })
                        .catch((error) => {				
                        })
                    }
                    return false;
                });
                // 查询
                form.on('submit(formDome)',function(data){
                    // console.log(data);
                    $('.layui-icon-close').click();
                    var data = data.field;
                    var woStatus = ''
                    table.reload('testReload', {
                        url: that.$url+'customerSatisfactionManager' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.name,//客户名称
                            serviceArea : data.serAddr,//服务区域
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            custSat : data.custSat, //客户评价
                            username : uname,
                            woStatus
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
        },
        exportExcel(){
			var that = this
			that.$exportE('exportSatFactionManager')
        },	
        isShow(){
			var that = this			
			var arr2 = that.$admin
			var admin = ''
			if(arr2.kh == that.$userRole || arr2.zbkf == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="evaluate">评价</a>'					
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
			}
			return admin;
					
		},
        closeStar(){
            that.starValue = '';
            that.textarea = '';
        }
    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



