<template>
<!-- 维修报表 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;维修报表&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">起始时间</label>
                        <div class="layui-input-inline">
                            <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">终止时间</label>
                        <div class="layui-input-inline">
                            <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="btn">
                        <a class="layui-btn layui-bg-blue"  lay-submit lay-filter="formDome">查询</a>
                        <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                    </div>
                </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <div id="main" style="width: 1000px;height:400px;"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,exportRepairReport} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            hcgd:'',
            echarsType:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','laydate','form'], function () {
                var table = layui.table,laydate = layui.laydate,form = layui.form;
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-280'
                    , cellMinWidth: 140
                    , url: that.$url+'repairReport' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width: 80}
                        ,{field: 'woNumber', title: '服务单号',minWidth: 160}
                        ,{field: 'custName', title: '客户名称',minWidth: 160}
                        ,{field: 'unitType', title: '设备型号'}
                        ,{field: 'deviceBrand', title: '设备品牌'}
                        , {field: 'esNumber', title: '设备序列号'}
                        , {field: 'machCode', title: '机器编码'}
                        , {field: 'serviceArea', title: '服务区域'}
                        , {field: 'department', title: '部门'}
                        , {field: 'location', title: '设备位置',minWidth:180}
                        , {field: 'repairTime', title: '报修时间',minWidth:160}
                        , {field: 'probTime', title: '完成时间',minWidth:160}
                        , {field: 'bwReader', title: '黑白读数'}
                        , {field: 'coReader', title: '彩色读数'}
                        , {field: 'accidentType', title: '故障现象分类'}
                        ,{field: 'maintenanceFeedback', title: '处理措施'}
                        , {field: 'faultNo', title: '故障代码'}
                        , {field: 'remark', title: '故障描述'}
                        // , {field: 'post', title: '处理描述1'}
                        // , {field: 'post', title: '处理描述2'}
                        // , {field: 'post', title: '处理描述3'}
                    ]]
                    ,id: 'testReload'
                    ,done: function(res){
                        // that.echarsType = res.kpi
                        echartsFun(res.kpi)
                    }
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    $('.layui-icon-close').click();
                    table.reload('testReload', {
                        url: that.$url+'repairReport' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.custName,
                            serviceArea : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
            function echartsFun(res){
            var myChart = that.$echarts.init(document.getElementById('main'),'',{width:1200,height:400})
            // 指定图表的配置项和数据
            console.log(res)
            var resName = []
            for(var i=0; i<res.length; i++){
                resName.push(res[i].name)
            }
            var option = {
                title : {
                    text: '故障现象分析',
                    // subtext: '纯属虚构',
                    x:'center'
                },
                tooltip : {
                    trigger: 'item',
                    // formatter: "{a} <br/>{b} : {c} ({d}%)"
                    formatter:function(data){ 
                        console.log(data)
                        var aa = data.seriesName
                        var bb = data.name
                        var cc = data.value
                        var dd = data.percent.toFixed(1)+"%"
                        return aa +"</br>"+ bb + "&nbsp;:&nbsp;" + cc + "&nbsp;(&nbsp;" +dd + "&nbsp;)"
                    } 
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    // data: ['硬件故障','耗材断供','软件故障']
                    data: resName
                },
                series : [
                    {
                        name: '故障类型',
                        type: 'pie',
                        radius : '55%',
                        center: ['50%', '60%'],
                        data: res,
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        label: {
                            normal: {
                                // formatter: '{b} {d}%  ',
                                fontSize: 14,
                                formatter:function(data){ 
                                    console.log(data)
                                    var aa = data.seriesName
                                    var bb = data.name
                                    var cc = data.value
                                    var dd = data.percent.toFixed(1)+"%"
                                    // return aa +"</br>"+ bb + "&nbsp;:&nbsp;" + cc + "&nbsp;(&nbsp;" +dd + "&nbsp;)"
                                    return bb+" (" + cc + "个) "+dd
                                },
                            }
                        }
                    }
                ],
                animation:false,
            };

            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
            
            //获取echarts图的base64编码，为png格式
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.pic = {picture : myChart.getDataURL()}
        }
        },
        exportExcel(){
			 var that = this;
            let userN = sessionStorage.getItem('username')
            exportRepairReport(that.pic).then((res) => {
                console.log(res)	
                window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })	
		}
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



