<template>
<!-- 总公司运维kpi -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;总公司运维KPI&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block">
                        <select name="name" lay-verify="">
                            <option value="" id="address">请选择</option>
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">起始时间</label>
                    <div class="layui-input-block">
                        <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">终止时间</label>
                    <div class="layui-input-block">
                        <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                    </div>
                </div>
            </div>
            <div class="btn">
                <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome" >查询</a>
                <a class="layui-btn layui-bg-blue"  @click='exportExcel()'>导出</a>
            </div>
            </form>
            
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','laydate','form'], function () {
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                //日期
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-300'
                    , cellMinWidth: 150
                    , url: that.$url+'headOffice' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width: 80}
                        ,{field: 'custName', title: '客户名称', }
                        , {field: 'responseTimeRate', title: '电话响应时间达标率',
                            templet:function(d){
                                return intercept(d.responseTimeRate)
                            }
                        }
                        , {field: 'arrTimeRate', title: '到达时间达标率',
                            templet:function(d){
                                return intercept(d.arrTimeRate)
                            }
                        }
                        , {field: 'probSolveRate', title: '问题解决时间达标率',
                            templet:function(d){
                                return intercept(d.probSolveRate)
                            }
                        }
                        , {field: 'custScore', title: '客户满意度',width:150}
                    ]]
                    ,id: 'testReload'
                });
                //概率保留整数
                function intercept(data){
                    var datas = data.split('%')[0]
                    return Math.round(datas)+'%'
                }
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'headOffice' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            serviceArea : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
        },
        exportExcel(){
			var that = this
			that.$exportE('exportHeadOffice')		
		}
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



