<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;参数设定&lt;&lt;</div>
            <!-- <div class="moduleDiv">
                <div class="title">角色管理</div>
                <form class="layui-form">
                    <div class="layui-form-item">
                        <div class="layui-inline">
                            <label class="layui-form-label allinput">新增角色</label>
                            <div class="layui-input-inline">
                                <input type="text" name="compLocation" lay-verify="required" class="layui-input">
                            </div>
                        </div>
                    </div>
                    <div class="layui-form-item mar-left">
                        <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="roleSave">保存</a>
                        <button type="reset" class="layui-btn dis-n reset">重置</button>
                    </div>
                </form>
            </div> -->
            <div class="moduleDiv">
                <div class="title">新增方案</div>
                <form class="layui-form">
                    <div class="layui-form-item" style="margin-top: 35px;">
                        <div class="layui-inline">
                            <label class="layui-form-label ">方案标题</label>
                            <div class="layui-input-inline" style="width: 270px;">
                                <input type="text" name="caseTitle" lay-verify="required" class="layui-input" autocomplete="off" placeholder="广东迅维信息产业股份有限公司">
                            </div>
                        </div>
                        <div class="layui-inline">
                            <label class="layui-form-label">方案链接</label>
                            <div class="layui-input-inline" style="width: 270px;">
                                <input type="text" name="caseContent" lay-verify="httpUrl||required" class="layui-input" autocomplete="off" placeholder="http://solutionyun.com">
                            </div>
                        </div>
                    </div>
                    <div class="layui-form-item mar-left">
                        <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="caseSave">保存</a>
                        <button type="reset" class="layui-btn dis-n reset">重置</button>
                    </div>
                </form>
                <div class="caseContent"></div>
            </div>
            <div class="moduleDiv">
                <div class="title">考核目标时间设定</div>
                <form class="layui-form" lay-filter="example">
                    <div class="layui-form-item">
                        <div class="layui-inline">
                            <label class="layui-form-label allinput">电话响应时间</label>
                            <div class="layui-input-inline">
                                <input type="text" name="telphone" lay-verify="required" class="layui-input" autocomplete="off">
                            </div>
                            <label class="selHeight">分钟</label>
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <div class="layui-inline">
                            <label class="layui-form-label allinput">到达现场时间</label>
                            <div class="layui-input-inline">
                                <input type="text" name="arrive" lay-verify="required" class="layui-input" autocomplete="off">
                            </div>
                            <label class="selHeight">小时</label>
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <div class="layui-inline">
                            <label class="layui-form-label allinput">问题解决时间</label>
                            <div class="layui-input-inline">
                                <input type="text" name="solve" lay-verify="required" class="layui-input" autocomplete="off">
                            </div>
                            <label class="selHeight">小时</label>
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <div class="layui-inline">
                            <label class="layui-form-label allinput">客服派单时间</label>
                            <div class="layui-input-inline">
                                <input type="text" name="sendTime" lay-verify="required" class="layui-input" autocomplete="off">
                            </div>
                            <label class="selHeight">分钟</label>
                        </div>
                    </div>
                    <div class="layui-form-item mar-left">
                        <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="timeSave">保存</a>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {selectAssessment,assessment,addSolvePlan} from '../../../config/axiosUtil'


export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            layui.use(['layer','form'], function () {
                var layer = layui.layer, form = layui.form;
                form.verify({
                    httpUrl:[/(http|https):\/\/([\w.]+\/?)\S*/,'连接地址格式不正确！']
                })
                // /(http|https):\/\/([\w.]+\/?)\S*/
                //角色管理
                // form.on('submit(roleSave)',function(data){
                //     console.log(data.field)
                //     $(".reset").click();
                //     return false;
                // })
                //新增方案 
                form.on('submit(caseSave)',function(data){
                    console.log(data.field)
                    var planData = {
                        title:data.field.caseTitle, //方案标题
                        content:data.field.caseContent //方案内容
                    }
                    addSolvePlan(planData).then((res) => {
                        layer.msg(res.data.msg)
                        $(".reset").click();
                    }).catch((error) => {
                    })
                    // $(".reset").click();
                    return false;
                })
                //考核目标时间赋值
                selectAssessment().then((res) => {
                    //表单初始赋值
                    form.val('example', {
                        "telphone": res.data.responseTime // 电话响应时间
                        ,"arrive": res.data.arriveTime // 到达现场时间
                        ,"solve": res.data.solvingTime  //问题解决时间
                        ,"sendTime": res.data.sendTime  //客服派单时间
                    })
                    
                }).catch((error) => {
                })
                //考核目标时间设定
                form.on('submit(timeSave)',function(data){
                    console.log(data.field)
                    var timeData = {
                        responseTime:data.field.telphone, //响应时间
                        arriveTime:data.field.arrive, //到达现场时间
                        solvingTime:data.field.solve, //问题解决时间
                        sendTime: data.field.sendTime  //客服派单时间
                    }
                    layer.confirm('确定修改？', function(index){
                        assessment(timeData).then((res) => {	
                            var datas = res.data;
                            if(datas.code == 0){
                                layer.close(index)
                                layer.msg(datas.msg);
                                form.render();
                            }else if(datas.code == 1){
                                layer.close(index)
                                layer.msg(datas.msg);
                                form.render();
                            }
                        })
                        .catch((error) => {				
                        })
                    })
                    
                    // $(".reset").click();
                    return false;
                })
            })
        }
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.caseContent{

}
</style>



