<template>
    <div id='assets'>
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;资产管理&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block allinput">
                            <select name="address" lay-verify="">
                                <option value=""  id="address"></option>
                                <!-- <option :value="item.compName" v-for="item in serArea">{{item.compName}}</option> -->
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="name"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    
                </div>
                <div class="btn">
                    <a class="layui-btn layui-bg-blue" lay-filter="formDome" lay-submit="">查询</a>
                    <a class="layui-btn layui-bg-blue" @click='exportE()'>导出</a>
                </div>
                </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <script type="text/html" id="barDemo">
                <!-- <a class="layui-btn layui-btn-xs" lay-event="edit">编辑</a> -->
            </script>
        </div>
        <div class="appraise dis-n">
            <form class="layui-form winForm" lay-filter="example">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth"><span>*</span>资产编码</label>
                        <div class="layui-input-inline">
                            <input type="text" name="assetNumber" class="layui-input" unselectable='on' readonly autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">单位</label>
                        <div class="layui-input-inline">
                            <input type="text" name="unit" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="winTitle">账面数</div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">数量</label>
                        <div class="layui-input-inline">
                            <input type="text" name="quantity" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">单价</label>
                        <div class="layui-input-inline">
                            <input type="text" name="unitPirce" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">原值</label>
                        <div class="layui-input-inline">
                            <input type="text" name="origValue" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">累计折旧</label>
                        <div class="layui-input-inline">
                            <input type="text" name="accDep" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">净值</label>
                        <div class="layui-input-inline">
                            <input type="text" name="netValue" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">实盘数</label>
                        <div class="layui-input-inline">
                            <input type="text" name="realNumber" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="winTitle">盘亏、馈差额</div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">数量</label>
                        <div class="layui-input-inline">
                            <input type="text" name="number" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label winLabWidth">金额</label>
                        <div class="layui-input-inline">
                            <input type="text" name="money" class="layui-input" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item btn">
                    <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="evalSub">提交</a>
                    <button type="reset" class="layui-btn dis-n" id="reset">重置</button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {changeAsset,assetManage,areaManage} from '../../../config/axiosUtil'


export default {
    name: "Assets",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            limita: 0,
            limits:{},
            serArea:[],
            serAreas:''
        }
    },
    mounted() {
        this.getData()
        // var unMask = true
    },
    methods:{
        
        getData(){
            var that = this;
            var uname = sessionStorage.getItem("username")
            var uRole = sessionStorage.getItem("userRole")
            var serviceA = sessionStorage.getItem("serviceArea")
            var unMask = true
            // var limits = {}
            layui.use(['table','layer','form'],function(){
                var table = layui.table, layer = layui.layer, form = layui.form;
                
                form.render(); 
                table.render({
                    elem: '#demo'
                    ,height: 'full-280'
                    ,cellMinWidth: 120
                    ,url: that.$url+'assetManage'  //数据接口
                    ,where:{username:uname}
                    // ,method:'post'
                    ,cols:  [[ //标题栏
                        {field: '', title: '序号', rowspan: 2, type: 'numbers', width: 80,fixed: 'left'} //rowspan即纵向跨越的单元格数
                        ,{field: 'custName', title: '客户名称', rowspan: 2,width:180,fixed: 'left'}
                        ,{field: 'serviceArea', title: '服务区域', rowspan: 2,width:180}
                        ,{field: 'assetAttr', title: '资产属性', rowspan: 2}
                        ,{field: 'holdDepartment', title: '保管部门', rowspan: 2}
                        ,{field: 'holdMan', title: '保管人', rowspan: 2}
                        ,{field: 'devName', title: '设备类型', rowspan: 2}
                        ,{field: 'assetNumber', title: '资产编码', rowspan: 2}
                        ,{field: 'assetClass', title: '资产类别', rowspan: 2}
                        ,{field: 'specifications', title: '型号规格', rowspan: 2}
                        ,{field: 'unit', title: '单位', rowspan: 2}
                        ,{align: 'center', title: '账面数', colspan: 5} //colspan即横跨的单元格数，这种情况下不用设置field和width
                        ,{field: 'realNumber', title: '实盘数', rowspan: 2}
                        ,{align: 'center', title: '盘亏、馈差额', colspan: 2}
                        ,{field: '', title: '备注', rowspan: 2}
                        ,{field: 'czx',fixed: 'right', title: '操作项', rowspan: 2, width:80,
                            templet: function (d) {
								var arr2 = ''
								return that.isShow(d);
							}
                        }
                    ], [
                        {field: 'quantity', title: '数量', }
                        ,{field: 'unitPirce', title: '单价', }
                        ,{field: 'origValue', title: '原值', }
                        ,{field: 'accDep', title: '累计折旧', }
                        ,{field: 'netValue', title: '净值', }
                        ,{field: 'number', title: '数量', }
                        ,{field: 'money', title: '金额', }
                    ]]
                    ,id: 'testReload'
                    ,page: true //开启分页
                    ,limit: 20
                    ,done: function(res){
						console.log(res.data)
                        console.log(that.serAreas)
                        if(uRole != '客服经理' && uRole != '总部客服' && uRole != '运维助理' && uRole != '驻现场人员'){
                            $("[data-field='czx']").css('display','none');
                        }
					}
                })
                table.on('tool(demo)',function(obj){
                    if(obj.event == 'edit'){
                        layer.open({
                            type: 1,
                            title: '编辑',
                            area: ['50%','80%'],
                            content: $('.appraise')
                        });
                        form.val('example',{
                            "assetNumber": obj.data.assetNumber  // 工单号码
                        })
                    }
                })
                form.on('submit(evalSub)',function(data){
                    console.log(data);
                    var data = data.field;
                    var evalContent = {
                        assetNumber : data.assetNumber,//资产编码
                        unit : data.unit,//单位
                        quantity : data.quantity,//数量
                        unitPirce : data.unitPirce,//单价
                        origValue : data.origValue,//原价
                        accDep : data.accDep,//累计折旧
                        netValue : data.netValue,//净值
                        realNumber : data.realNumber,//实盘数
                        number : data.number,//盘盈、盘亏数
                        money : data.money,//盘盈、盘亏金额
                    }
                    changeAsset(evalContent).then((res) => {
                        if(res.data.code == 0){
                            $("#reset").click();
                            layer.closeAll();
                            layer.msg(res.data.msg);
                            table.reload('testReload');
                        }else{
                            layer.msg(res.data.msg);
                        }
                    })
                    .catch((error) => {				
                    })
                });
                // 服务区域下拉内容
                that.$serviceArea(areaManage)
                
                // 查询
                form.on('submit(formDome)',function(data){
                    var data = data.field;
                    var attribute = '';
                    $('.layui-icon-close').click();
                    table.reload('testReload', {
                        url: that.$url+'assetManage' ,//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.name,//客户名称
                            serviceArea : data.address,//服务区域
                            assetAscription : attribute, //资产属性
                            username : uname
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            })
        },
		exportE(){
			var that = this
			that.$exportE('exportAssetManage')
        },
		isShow(){
			var that = this			
			var arr2 = that.$admin
			var admin = ''
			if(arr2.zbkf == that.$userRole || arr2.admin == that.$userRole || arr2.zxcry == that.$userRole|| arr2.kfzg == that.$userRole|| arr2.ywzl == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="edit">编辑</a>'
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
		}
        return admin;
			
		},
        // assetFun(){
        //     var that = this
        //             console.log("555555555")
        //             // if(unMask == true){
        //                 // var limitss = that.limits
        //             assetManage(that.limits).then((res) => {
        //                 var data = res.data.data;
        //                 var serverArea = []
        //                 for(var i=0; i<data.length; i++){
        //                     if(serverArea.indexOf(data[i].serviceArea) == -1){
        //                         serverArea.push(data[i].serviceArea);
        //                     }
        //                 }
        //                 for(var j=0; j<serverArea.length; j++){
        //                     console.log(serverArea[j])
        //                     $('#address').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
        //                 }
        //                 form.render()
        //             })
        //             .catch((error) => {				
        //             });
        //             // }
        //         }
    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
    #assets{
        width: 85%;
    }
    .winLabWidth{
        width: 100px;
    }
    .winTitle{
        font-size: 20px;
        margin-bottom: 10px;
    }
    .winForm{
        width: 80%;
        margin: 0 auto;
    }
</style>


