<template>
<!-- 维修报表 -->
    <div id="orderStat">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;工单统计&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                    
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">起始时间</label>
                        <div class="layui-input-inline">
                            <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">终止时间</label>
                        <div class="layui-input-inline">
                            <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="btn">
                        <a class="layui-btn layui-bg-blue"  lay-submit lay-filter="formDome">查询</a>
                        <!-- <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a> -->
                    </div>
                </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,exportRepairReport} from '../../../config/axiosUtil'

export default {
    name: "OrderStat",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            hcgd:'',
            echarsType:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','laydate','form'], function () {
                var table = layui.table,laydate = layui.laydate,form = layui.form;
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-280'
                    , cellMinWidth: 120
                    , url: that.$url+'orderStatistics' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    ,totalRow:true
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width: 80,totalRowText: '合计'}
                        ,{field: 'engineerName', title: '工程师姓名'}
                        ,{field: 'engineerWoNumber1', title: '事故类',totalRow: true}
                        ,{field: 'engineerWoNumber2', title: '需求类',totalRow: true}
                        , {field: 'engineerWoNumber3', title: '事件类',totalRow: true}
                        , {field: 'engineerTotalWoNumber', title: '工单总数',totalRow: true}
                        , {field: 'turnOrderNumber', title: '转单总数',totalRow: true}
                        
                    ]]
                    ,id: 'testReload'
                    , done: function () {
                        var tableView = this.elem.next();
                        console.log(tableView)
                        var totalRow = tableView.find('.layui-table-total');
                        console.log(totalRow)
                        var limit = this.page ? this.page.limit : this.limit;
                        console.log(limit)
                        layui.each(totalRow.find('td'), function (index, tdElem) {
                            console.log(tdElem)
                            console.log(index)
                        tdElem = $(tdElem);
                        var text = tdElem.text();
                        console.log(text)
                        // if (text && !isNaN(text)) {
                        text = text.split('.')[0]
                        tdElem.find('div.layui-table-cell').html(text);
                        // }
                        });
                    }
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'orderStatistics' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.custName,
                            serviceArea : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
            
        },
        // exportExcel(){
		// 	 var that = this;
        //     console.log(that.pic)
        //     // that.$exportE('exportBranchKpi')
        //     exportRepairReport(that.pic).then((res) => {
        //         console.log(res)	
        //         window.open('http://'+res.data.msg,"_blank")
        //     }).catch((error) => {
        //         // layer.msg('我的天呐，网炸了！')
        //     })	
		// }
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



