<template>
    <div id="custmorMsg">
        <!-- <Index/> -->
        <div class="body">
		<div class="titleLab">&gt;&gt;客户信息&lt;&lt;</div>
            <div class="abovebtn">
			<form class="layui-form">
				<input type="text" class="dis-n">
				<div class="layui-inline">
					<label class="layui-form-label">客户名称</label>
					<div class="layui-input-inline">
						<input type="text" name="username"  class="layui-input allinput" autocomplete="off">
					</div>
				</div>
				<div class="layui-inline" v-if="serAr">
					<label class="layui-form-label">服务区域</label>
					<div class="layui-input-inline">
						<select name="serviceArea" lay-verify="">
							<option value="" id="address">请选择</option>
                        </select>
					</div>
				</div>
				<div class="btn">
					<a class="layui-btn layui-bg-blue" lay-filter="formDome" lay-submit="">查询</a>
					<!-- <a class="layui-btn layui-bg-blue" lay-filter="add" lay-submit="">新增</a> -->
					<a class="layui-btn layui-bg-blue" v-if='addShow' @click='add()'>新增</a>
					<a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
				</div>
			</form>
			<div class="labnum">
				<label>客户数量：&nbsp;&nbsp;{{ custNum }}&nbsp;&nbsp;</label>
			</div>
			</div>
    		<div class="tab"><table id="custMsg" lay-filter="custMsg" class="layui-hide"></table></div>
        </div>
    	<!-- <script type="text/html" id="barDemo">
			
        	<a class="layui-btn layui-btn-xs" lay-event="edit" >编辑</a>
			<a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="del">删除</a>
    	</script> -->
    <!-- 新增/编辑 -->
    <div id="increased" class="dis-n sdd">
        <form class="layui-form" action="" lay-filter="example">
           <div class="layui-form-item">
           		<div class="layui-inline">
           				<label class="layui-form-label winLabW"><span>*</span>客户名称：</label>
           				<div class="layui-input-inline upWidth">
           						<input type="text" name="custName" v-model="formV.custName" lay-verify="required" class="layui-input"  autocomplete="off">
								<input type="hidden" name = "custId" v-model="formV.custId"  class="layui-input">
           				</div>
           		</div>
           </div>
           <div class="layui-form-item">
           		<div class="layui-inline">
           				<label class="layui-form-label winLabW"><span>*</span>客户地址：</label>
           				<div class="layui-input-inline upWidth">
           						<input type="text" name="custAddr" v-model="formV.custAddr" lay-verify="required" class="layui-input" autocomplete="off">
           				</div>
           		</div>
           </div>
           <div class="layui-form-item">
           		<div class="layui-inline">
           				<label class="layui-form-label winLabW"><span>*</span>联系人：</label>
           				<div class="layui-input-inline upWidth">
           						<input type="text" name="linkMan" v-model="formV.linkMan" lay-verify="required" class="layui-input" autocomplete="off">
           				</div>
           		</div>
           </div>
           <div class="layui-form-item">
           		<div class="layui-inline">
           				<label class="layui-form-label winLabW"><span>*</span>联系电话：</label>
           				<div class="layui-input-inline upWidth">
           						<input type="text" name="phone" v-model="formV.phone" lay-verify="required" class="layui-input" autocomplete="off">
           				</div>
           		</div>
           </div>
           <div class="layui-form-item">
           		<div class="layui-inline">
           				<label class="layui-form-label winLabW">上班时间：</label>
           				<div class="layui-input-inline upWidth">
           						<input type="text" name="workTime" v-model="formV.workTime"  class="layui-input" placeholder="00:00:00" autocomplete="off">
           				</div>
           		</div>
           </div>
           <div class="layui-form-item">
           		<div class="layui-inline">
           				<label class="layui-form-label winLabW">下班时间：</label>
           				<div class="layui-input-inline upWidth">
           						<input type="text" name="offWorkTime" v-model="formV.offWorkTime"  class="layui-input" placeholder="00:00:00" autocomplete="off">
           				</div>
           		</div>
           </div>
            <div class="layui-form-item btn">
                 <a class="layui-btn layui-bg-blue" @click="subMit(flag)">提交</a> 
                <!-- <a class="layui-btn layui-bg-blue" v-if="sub" lay-submit="" lay-filter="subMit">提交</a> 
                 <a class="layui-btn layui-bg-blue" v-else lay-submit="" lay-filter="exit">修改</a> -->
                <!-- <button  class="layui-btn layui-bg-blue" id="reset" type="reset">重置</button> -->
            </div>
        </form>
    </div>
</div>
</template>

<script>
import Index from '../../index'
import {del,updateCust,exportCustomerInfo,customerInfo,insertCust,areaManage} from '../../../config/axiosUtil'

export default {
    name: "CustmorMsg",
    components: {
        Index
    },
	data() {
		return {
			flag : '1',
			item : '',
			formV : {
				custId : '39',
				custName : '',
				custAddr : '',
				linkMan : '',
				phone : '',
				workTime : '',
				offWorkTime : '',					
			},
			custNum : '',
			sub: true,
			edit: false,	
			addShow: false,
			serAr: true
		}			
	},
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
			var uname = sessionStorage.getItem('username');
			var that = this;
			var uRole = sessionStorage.getItem("userRole")
			if(uRole == '总部客服' || uRole == '运维总监'){
				this.edit = false;
				this.serAr = true
			}else{
				this.serAr = false
			}
			layui.use(['form','table','layer','util'], function(){
				var table = layui.table, layer = layui.layer;
				var form = layui.form;
				// form.verify({

				// })
				table.render({
					elem: '#custMsg'
					,height: 'full-320'
					,cellMinWidth: 200 //全局定义常规单元格的最小宽度
					,url: that.$url+'customerInfo' //数据接口      
					,where:{username:uname}      
					,cols: [[ //表头
						{field: '', title: '序号', fixed: 'left',align: 'center',width: 80,type: 'numbers'}
						,{field: 'custName', title: '客户名称',align: 'center',fixed:'left',minWidth:280}
						,{field: 'custAddr', title: '客户地址',align: 'center',minWidth	:318}
						,{field: 'linkman', title: '联系人',align: 'center',minWidth:220}
						,{field: 'phone', title: '联系电话',align: 'center',minWidth:220}
						,{field: 'workTime', title: '上班时间',align: 'center',minWidth	:220,
							templet:function(d){
								if(d.workTime == 0){
									return '<span>08:00:00</span>'
								}else{
									var time = layui.util.toDateString(d.workTime);
									var woTime = time.split(" ")[1];
									return '<span>'+ woTime +'</span>'
								}
							}
						}
						,{field: 'offWorkTime', title: '下班时间',align: 'center',minWidth	:220,
							templet:function(d){
								if(d.offWorkTime == 0){
									return '<span>08:00:00</span>'
								}else{
									var time = layui.util.toDateString(d.offWorkTime);
									var woTime = time.split(" ")[1];
									return '<span>'+ woTime +'</span>'
								}
							}
						}
						// ,{field: 'custCall', title: '备注',align: 'center'}
						,{field: 'czx',fixed:'right', title: '操作项',align: 'center',width:140,
							templet: function (d) {
								var arr2 = ''
								return that.isShow(d);
							}
						}
					]]
					,page: true
					,limit: 20
					,id: 'testReload'
					,done:function(res){
						that.custNum = res.count
						// console.log(res)
						if(uRole != '运维助理' && uRole != '驻现场人员' && uRole != '客服经理'){
							$("[data-field='czx']").css('display','none');
						}
					}
				});
				// 查询服务区域下拉内容
				that.$serviceArea(areaManage)
				//查询
				form.on('submit(formDome)',function(data){
                    var data = data.field;
					// console.log(data)
					$('.layui-icon-close').click();
                    table.reload('testReload', {
                        url: that.$url+'customerInfo',//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.username,//客户名称
							serviceArea : data.serviceArea,
							username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
				form.render()				
				table.on('tool(custMsg)', function(obj){
					var data = obj.data;
					console.log(obj)
					if(obj.event == 'detail'){
						layer.msg('ID：'+ data.custId + ' 的查看操作');
					} else if(obj.event === 'del'){
						//删除
						var v = {
							custId : data.custId
						}
						layer.confirm('确定删除信息吗', function(index){
							del(v)
							.then((res) => {
								if(res.data.code == 0){
										layer.msg('删除成功', {icon: 1});
										obj.del();
										layer.close(index);	
										table.reload('testReload',{url: that.$url+'customerInfo'})										 
								}else{
										layer.msg('删除失败', {icon: 2});
								}
							})
							.catch((error) => {
								// layer.msg('我的天呐，网炸了！')
							})						
						});
					//编辑
					} else if(obj.event === 'edit'){
						
						that.sub = false;
						console.log(obj.data);
						var time = layui.util.toDateString(data.workTime);
						var woTime = time.split(" ")[1];

						var times = layui.util.toDateString(data.offWorkTime);
						var offTime = times.split(" ")[1];
						layer.open({
							type: 1,
							title: '编辑',
							area: ['60%','70%'],
							content: $('.sdd')
						});
						
						that.formV.custId = data.custId;
						that.formV.custName = data.custName;
						that.formV.custAddr = data.custAddr;
						that.formV.linkMan = data.linkman;
						that.formV.phone = data.phone;
						that.formV.workTime = woTime;
						that.formV.offWorkTime = offTime;
						that.flag = 'edit'
											
					}
				});	
			});
		},
		// 新增
		add(){
			var that = this;
			that.flag = 'add'
			layui.use(['form','table','layer'], function(){
				var table = layui.table, layer = layui.layer;
				var form = layui.form;
				
				// that.formV.custId = data.custId;
				that.formV.custName = '';
				that.formV.custAddr = '';
				that.formV.linkMan = '';
				that.formV.phone = '';
				that.formV.workTime = '';
				that.formV.offWorkTime = '';
				layer.open({
					type: 1,
					title: '新增',
					area: ['60%','70%'],
					// shade: 0,
					// shadeClose: true,
					content: $('.sdd')
				});	
			})							
		},
		// 修改
		subMit(str){
			var that = this
			if(str == 'edit'){
				updateCust(this.formV).then((res) => {
					if(res.data.code == 0){
						that.$layerClose('test')					
						// that.$layerMsg('修改成功')
						layer.msg(res.data.msg)	
						that.getData();
					}else if(res.data.code == 1){
						layer.msg(res.data.msg)
					}						
				}).catch((error) => {
					// layer.msg('我的天呐，网炸了！')
				})
			//新增
			}else if(str == 'add'){
				if(that.formV.custId && that.formV.custName && that.formV.custAddr && that.formV.linkMan && that.formV.phone){

					// if(!(/^1\d{10}$/.test(that.formV.phone))){
					// 	that.$layerMsg('手机号不正确')
					// }else{
						insertCust(this.formV).then((res) => {
							if(res.data.code == 0){
								that.$layerClose('test')
								that.$layerMsg('添加成功')
								that.getData()
							}else if(res.data.code == 1){
								layer.msg(res.data.msg)
							}						
						}).catch((error) => {
							// layer.msg('我的天呐，网炸了！')
						})
					// }
				}else{
					that.$layerMsg('必填项不能为空')
				}
				
			}
		},
		//导出
		exportExcel(){
			var that = this;
			that.$exportE('exportCustomerInfo')
		},
		reset(){
			var that = this;
			// that.formV.custId = '';
			that.formV.custName = '';
			that.formV.custAddr = '';
			that.formV.linkMan = '';
			that.formV.phone = '';
			that.formV.workTime = '';
			that.formV.offWorkTime = '';
		},
		showA(){
			var that = this
			var arr = [that.$admin.ywzl,that.$admin.zxcry,that.$admin.kfzg,that.$admin.admin]
			console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
			}
		},
		isShow(){
			var that = this			
			var arr2 = that.$admin
			var admin = ''
			if(arr2.admin == that.$userRole || arr2.zxcry == that.$userRole|| arr2.kfzg == that.$userRole|| arr2.ywzl == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" v-show="editShow" lay-event="edit">编辑</a><a class="layui-btn layui-btn-danger layui-btn-xs" v-show="delShow" lay-event="del">删除</a>'
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
		}
		return admin;
			
		},
		// shuaxin(){
		// 	this.isRouterAlive = false
     	// 	this.$nextTick(() => (this.isRouterAlive = true))
		// }
    },
    created() {
		this.showA()
    },
	destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#custmorMsg{
    width: 85%;
}
.winLabW{
	width: 120px;
}
</style>


