<template>
<!-- 工单分析表 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;工单分析表&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">起始时间</label>
                        <div class="layui-input-block">
                            <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">终止时间</label>
                        <div class="layui-input-block">
                            <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
                        </div>
                    </div>
                    <!-- div class="layui-inline">
                        <label class="layui-form-label">服务类型：</label>
                        <div class="layui-input-block">
                            <input type="checkbox" name="serviceType" value="事故类" title="事故类">
                            <input type="checkbox" name="serviceType" value="事件类" title="事件类">
                            <input type="checkbox" name="serviceType" value="需求类" title="需求类">
                        </div>
                    </div> -->
                </div>
                <div class="btn">
                    <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome">查询</a>
                    <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                </div>
                </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <div></div>
            <div id="mainA" style="width: 50%;height:400px;float: left;margin:0 auto" v-show="isShow"></div>
            <div id="main" style="width: 50%;height:400px;float: left;" v-show="isShow"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,exportWorkAnalysisTable} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            arrive1: '',
            arrive2: '',
            arrive3: '',
            arrive4: '',
            prob1: '',
            prob2: '',
            prob3: '',
            prob4: '',
            picA:'',
            pic:'',
            isShow: false,
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','form','laydate'], function () {
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-320'
                    , cellMinWidth: 120
                    , url: that.$url+'orderAnalysis' //数据接口
                    , where:{username:uname}
                    , page: true //开启分页
                    , limit: 20
                    , cols: [[ //表头
                        {field: '', title: 'NO', width: 80,type:'numbers',fixed: 'left'}
                        , {field: 'woNumber', title: '工单号码',minWidth: 160}
                        , {field: 'custName', title: '客户名称',minWidth: 200}
                        , {field: 'serviceArea', title: '服务区域',minWidth: 200}
                        , {field: 'repairType', title: '工单来源'}
                        , {field: 'devName', title: '设备类型'}
                        , {field: 'machCode', title: '机器编码'}
                        , {field: 'faultType', title: '服务类型'}
                        , {field: 'accidentType', title: '服务类别'}
                        , {field: 'post', title: '服务级别'}
                        , {field: 'repairTime', title: '报修时间',minWidth: 180}
                        , {field: 'sendTime', title: '派单时间',minWidth: 180}
                        , {field: 'telRepon', title: '电话响应时间',minWidth: 180}
                        // , {field: 'post', title: '预约时间',minWidth: 180}
                        , {field: 'arrTime', title: '到达时间',minWidth: 180}
                        , {field: 'probSolve', title: '完成时间',minWidth: 180}
                        , {field: 'sendTimeSlot', title: '客服派单用时(min)',minWidth:150,}
                        , {field: 'responseTime', title: '电话响应用时(min)'}
                        , {field: 'arrTimeSlot', title: '到达现场耗时(h)',minWidth:140,
                            // templet: function(d){
                            //     return d.arrTimeSlot.toFixed(1)
                            // }
                        }
                        , {field: 'probSolveSlot', title: '处理用时(h)',
                            // templet: function(d){
                            //     return d.probSolveSlot.toFixed(1)
                            // }
                        }
                        , {field: 'staffName', title: '责任工程师'}
                        , {field: 'orderTurnNum', title: '转单/升级'}
                        , {field: 'staffName2', title: '后续工程师'}
                        , {field: 'woStatus', title: '工单状态'}
                        , {field: 'treatmentMeasure', title: '处理措施',minWidth:180}
                    ]]
                    ,id: 'testReload'
                    
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    $('.layui-icon-close').click();
                    table.reload('testReload', {
                        url: that.$url+'orderAnalysis' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.custName,
                            serviceArea : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                        ,done:function(res){
                            if(data.custName || data.name){
                                console.log(res.data)
                                if(res.data != ''){
                                    that.isShow = true
                                    that.arrive1= res.arrive1;
                                    that.arrive2= res.arrive2;
                                    that.arrive3= res.arrive3;
                                    // that.arrive4= res.arrive4;
                                    that.prob1= res.prob1;
                                    that.prob2= res.prob2;
                                    that.prob3= res.prob3;
                                    // that.prob4= res.prob4;
                                    echartsFunA()
                                    echartsFun()
                                }else{
                                    that.isShow = false
                                }
                                
                            }else{
                                that.isShow = false
                            }
                            // console.log(res.data)
                            
                        }
                    });
                })
            });
            function echartsFunA(){
            var myChart = that.$echarts.init(document.getElementById('mainA'),'',{width:500,height:300})
            // 指定图表的配置项和数据
            // console.log(that.responseTimeRate)
            var option = {
                title : {
                    text: '到达现场时间占比',
                    subtext: '',
                    x:'center'
                },
                tooltip : {
                    trigger: 'item',
                    // formatter: "{a} <br/>{b} : {c} ({d}%)"
                    formatter:function(data){ 
                        console.log(data)
                        var aa = data.seriesName
                        var bb = data.name
                        var cc = data.value
                        var dd = data.percent.toFixed(1)+"%"
                        return aa +"</br>"+ bb + "&nbsp;:&nbsp;" + cc + "&nbsp;(&nbsp;" +dd + "&nbsp;)"
                    } 
                },
                color:['#009953', '#61a0a8','#c23531'],
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: ['2小时内','2~4小时','超过4小时']
                },
                series : [
                    {
                        name: '到达现场时间占比',
                        type: 'pie',
                        radius : '55%',
                        center: ['50%', '60%'],
                        data:[
                            {value:that.arrive1, name:'2小时内'},
                            {value:that.arrive2, name:'2~4小时'},
                            {value:that.arrive3, name:'超过4小时'},
                            // {value:that.arrive4, name:'超过4小时'}
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        label: {
                            normal: {
                                // formatter: '{b} {d}%  ',
                                fontSize: 14,
                                formatter:function(data){ 
                                    console.log(data)
                                    var aa = data.seriesName
                                    var bb = data.name
                                    var cc = data.value
                                    var dd = data.percent.toFixed(1)+"%"
                                    // return aa +"</br>"+ bb + "&nbsp;:&nbsp;" + cc + "&nbsp;(&nbsp;" +dd + "&nbsp;)"
                                    return bb+" (" + cc + "个) "+dd
                                },
                                
                            }
                        }
                    }
                ],
                animation:false,
            };

            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
            
            //获取echarts图的base64编码，为png格式
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.picA = picture
        }
        function echartsFun(){
            var myChart = that.$echarts.init(document.getElementById('main'),'',{width:600,height:300})
            // 指定图表的配置项和数据
            // console.log(that.responseTimeRate)
            var option = {
                title : {
                    text: '问题解决时间占比',
                    subtext: '',
                    x:'center'
                },
                tooltip : {
                    trigger: 'item',
                    formatter:function(data){ 
                        console.log(data)
                        var aa = data.seriesName
                        var bb = data.name
                        var cc = data.value
                        var dd = data.percent.toFixed(1)+"%"
                        return aa +"</br>"+ bb + "&nbsp;:&nbsp;" + cc + "&nbsp;(&nbsp;" +dd + "&nbsp;)"
                    } 
                },
                color:['#009953', '#61a0a8','#c23531'],
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: ['4小时内','4~8小时','超过8小时']
                },
                series : [
                    {
                        name: '问题解决时间占比',
                        type: 'pie',
                        radius : '55%',
                        center: ['50%', '60%'],
                        data:[
                            {value:that.prob1, name:'4小时内'},
                            {value:that.prob2, name:'4~8小时'},
                            {value:that.prob3, name:'超过8小时'},
                            // {value:that.prob4, name:'超过8小时'}
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        label: {
                            normal: {
                                // formatter: '{b} {d}%  ',
                                fontSize: 14,
                                formatter:function(data){ 
                                    console.log(data)
                                    var aa = data.seriesName
                                    var bb = data.name
                                    var cc = data.value
                                    var dd = data.percent.toFixed(1)+"%"
                                    // return aa +"</br>"+ bb + "&nbsp;:&nbsp;" + cc + "&nbsp;(&nbsp;" +dd + "&nbsp;)"
                                    return bb+" (" + cc + "个) "+dd
                                },
                            }
                        }
                    }
                ],
                animation:false,
            };

            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
            
            //获取echarts图的base64编码，为png格式
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.pic = picture
        }
        },
        exportExcel(){
			 var that = this;
            console.log(that.pic)
            var pict = {
                picture1:that.picA,
                picture2:that.pic,
            }
            let userN = sessionStorage.getItem('username')
            console.log(pict)
            // that.$exportE('exportBranchKpi')
            exportWorkAnalysisTable(pict).then((res) => {
                console.log(res)	
                window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })		
		}
    },
    destroyed() {
        $('.layui-icon-close').click();
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



