<template>
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;工程师KPI报表&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="">
            <div class="layui-form-item">
                <div class="layui-inline">
                    <label class="layui-form-label">工程师姓名</label>
                    <div class="layui-input-block">
                        <input type="text" name="name" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">起始时间</label>
                    <div class="layui-input-inline">
                        <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" readonly autocomplete="off" >
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">终止时间</label>
                    <div class="layui-input-inline">
                        <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" readonly autocomplete="off" >
                    </div>
                </div>
            </div>
            <div class="btn">
                <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome" >查询</a>
                <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
            </div>
            </form>
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <div id="main" style="width: 800px;height:400px;" v-show="main"></div>
            <div id="mainEng" style="width: 800px;height:400px;" v-show="mainEng"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {expoetEngineerKpiSummary} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            pic: '',
            engName: '',
            engMin: '',
            responseTimeRate: '',
            arrTimeRate:'',
            probSolveRate:'',
            main: true,
            mainEng: false,

        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            
            var that = this;
            echartsFun()
            var uname = sessionStorage.getItem('username')
            // 详细报表
            layui.use(['table','form','laydate'], function () {
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-300'
                    , cellMinWidth: 150
                    , url: that.$url+'engineerKpiSummary' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width: 80}
                        , {field: 'staffName', title: '工程师姓名', }
                        , {field: 'responseTimeRate', title: '电话响应时间达标率',
                            templet:function(d){
                                var data = d.responseTimeRate
                                return Math.round(data)+'%'
                            }
                        }
                        , {field: 'arrTimeRate', title: '到达时间达标率',
                            templet:function(d){
                                var data = d.arrTimeRate
                                return Math.round(data)+'%'
                            }
                        }
                        , {field: 'probSolveRate', title: '问题解决时间达标率',width: 180,
                            templet:function(d){
                                var data = d.probSolveRate
                                return Math.round(data)+'%'
                            }
                        }
                        , {field: 'maintenanceRate', title: '保养完成率',
                            templet:function(d){
                                var data = d.maintenanceRate
                                return Math.round(data)+'%'
                            }
                        }
                        // , {field: 'secondService', title: '二次上门率',
                        //     templet:function(d){
                        //         var data = d.secondService
                        //         return Math.round(data)+'%'
                        //     }
                        // }
                        // , {field: 'orderNum', title: '工单数量'}
                        // , {field: 'orderTurnNum', title: '转单/升级数量'}
                        // , {field: 'customerSatisfaction', title: '客户满意度'}
                        // , {field: 'custComplaints', title: '客户投诉'}
                    ]]
                    ,id: 'testReload'
                    ,done: function(res){
                        console.log(res)
                        if(res.count == 1){
                            that.main = false
                            that.mainEng = true
                            var resTimeRate = res.data[0].responseTimeRate;
                            var arrTRate = res.data[0].arrTimeRate;
                            var resTRate = res.data[0].probSolveRate;
                            that.responseTimeRate = resTimeRate
                            that.arrTimeRate = arrTRate
                            that.probSolveRate = resTRate
                            console.log(that.responseTimeRate)
                            console.log(that.arrTimeRate)
                            console.log(that.probSolveRate)
                            echartsFunEng()
                        }else if(res.count == 0){
                            that.main = false
                            that.mainEng = false
                        }else{
                            that.main = true
                            that.mainEng = false
                            var engeerN = []
                            var engeerMin = []
                            for(var i=0;i<res.data.length;i++){
                                engeerN.push(res.data[i].staffName)
                                engeerMin.push(res.data[i].arrTimeAvg)
                            }
                            that.engName = engeerN
                            that.engMin = engeerMin
                            console.log(that.engName)
                            echartsFun()
                        }
                        
                        
                    }
                });
                // 查询
                form.on('submit(formDome)',function(data){
                    // console.log(data)
                    var data = data.field;
                    // console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'engineerKpiSummary' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            staffName : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
            function echartsFun(){
            var myChart = that.$echarts.init(document.getElementById('main'))
            // 指定图表的配置项和数据
            // console.log(that.engName)
            var option = {
                // color: ['#f0f'],
                title:{
                    text:'工程师到达现场平均用时'
                },
                xAxis: {
                    type: 'category',
                    data: that.engName
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {  
                        show: true,  
                        interval: 'auto',  
                        formatter: '{value}(h)'  
                    }, 
                },
                series: [{
                    data: that.engMin,
                    type: 'bar',
                    label: {
                        normal: {
                            show: true,
                            position: 'inside'
                        }
                    },
                }],
                animation:false,
            };
            myChart.setOption(option);
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.pic = {picture : myChart.getDataURL()}
        }
        function echartsFunEng(){
            var myChart = that.$echarts.init(document.getElementById('mainEng'),'',{width:600,height:300})
            // 指定图表的配置项和数据
            console.log(that.responseTimeRate)
            var option = {
                title: {
                    text: ''
                },
                tooltip: {
                    // trigger: 'item',
                    // formatter: "{a}<br/>{b}:{c}%"
                    formatter: function (params,ticket,callback) {
                        console.log(params);
                        console.log(ticket);
                        console.log(callback);
                        // var res = params[1]+   '<br/>'+"XX人数"+' : ' + params[2];
                        return res;
                    }
                },  //提示框
                legend: {               //图例
                    
                },
                xAxis: {            //x轴数据
                    type: 'category'
                },
                yAxis: [  
                    {  
                        type: 'value',  
                        axisLabel: {  
                            show: true,  
                            interval: 'auto',  
                            formatter: '{value} %'  
                            },  
                        show: true,
                        boundaryGap: true
                    }  
                ],            //y轴数据
                series: [        //图标类型)(柱状图)
                    {
                        type: 'bar',
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true, //开启显示
                                    position: 'top', //在上方显示
                                    textStyle: { //数值样式
                                        color: 'black',
                                        fontSize: 14,
                                        formatter: '{b}\n{c}%'
                                    }
                                }
                            }
                        },
                    },
                    {
                        type: 'bar',
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true, //开启显示
                                    position: 'top', //在上方显示
                                    textStyle: { //数值样式
                                        color: 'black',
                                        fontSize: 14,
                                        formatter: '{b}\n{c}%'
                                    }
                                }
                            }
                        },
                    },
                ],
                dataset: {
                    source: [
                        ['product', '达标率目标', '当月实际达标率'],
                        ['电话响应时间', '95', that.responseTimeRate],
                        ['到达现场时间', '95', that.arrTimeRate],
                        ['问题解决时间', '95', that.probSolveRate],
                        // ['设备正常运转率', '95', 0]
                    ]
                }, 
                //关闭动画，否则有些统计图表只有背景（如扇形统计图）
                animation:false,
            };

            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
            
            //获取echarts图的base64编码，为png格式
            var picture = myChart.getDataURL();
            console.log('picture')
            that.pic = {picture : myChart.getDataURL()}
        }
        },
        exportExcel(){
			 var that = this;
            console.log(that.pic)
            let userN = sessionStorage.getItem('username')
            // that.$exportE('exportBranchKpi')
            expoetEngineerKpiSummary(that.pic).then((res) => {
                console.log(res)	
                window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })		
		}
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



