<template>
<!-- 客户kpi报表 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;客户KPI报表&lt;&lt;</div>
            <div class="abovebtn">
    <form class="layui-form" action="">
    <div class="layui-form-item">
        <div class="layui-inline">
            <label class="layui-form-label">客户名称</label>
            <div class="layui-input-block">
                <input type="text" name="name" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
            </div>
        </div>
        <!-- <div class="layui-inline">
            <label class="layui-form-label">服务类型：</label>
            <div class="layui-input-block">
                <input type="checkbox" name="serviceType" value="事故类" title="事故类">
                <input type="checkbox" name="serviceType" value="事件类" title="事件类">
                <input type="checkbox" name="serviceType" value="需求类" title="需求类">
            </div>
        </div> -->
        <div class="layui-inline">
            <label class="layui-form-label">起始时间</label>
            <div class="layui-input-inline">
                <input class="layui-input" name="startDate" id="test1" type="text" placeholder="年-月-日" autocomplete="off">
            </div>
        </div>
        <div class="layui-inline">
            <label class="layui-form-label">终止时间</label>
            <div class="layui-input-inline">
                <input class="layui-input" name="endDate" id="test2" type="text" placeholder="年-月-日" autocomplete="off">
            </div>
        </div>
    </div>
    <div class="btn">
        <a class="layui-btn layui-bg-blue" lay-filter="formDome" lay-submit="">查询</a>
        <a class="layui-btn layui-bg-blue"  @click='exportExcel()'>导出</a>
    </div>
    </form>
</div>
<div class="tab">
    <table id="demo" lay-filter="demo"></table>
</div> 
            <div id="main" style="width: 600px;height:400px;" v-show="isShow"></div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {exportCustomerKpiSummary} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            responseTimeRate: '',
            arrTimeRate:'',
            probSolveRate:'',
            pic:'',
            isShow: false,
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username')
            layui.use(['table','form','laydate'], function () {
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                //时间
                laydate.render({
                    elem: '#test1'
                });
                laydate.render({
                    elem: '#test2'
                });
                form.render();
                // 详细报表
                table.render({
                    elem: '#demo'
                    , height: 'full-300'
                    , cellMinWidth: 150
                    , url: that.$url+'customerKpiSummary' //数据接口
                    ,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: 'NO', type:'numbers',width: 80}
                        , {field: 'custName', title: '客户名称',}
                        , {field: 'responseTimeRate', title: '电话响应时间达标率',
                            templet:function(d){
                                return intercept(d.responseTimeRate)
                            }
                        }
                        , {field: 'arrTimeRate', title: '到达时间达标率',
                            templet:function(d){
                                return intercept(d.arrTimeRate)
                            }
                        }
                        , {field: 'probSolveRate', title: '问题解决时间达标率',width: 180,
                            templet:function(d){
                                return intercept(d.probSolveRate)
                            }
                        }
                        // , {field: 'bfexr', title: '设备正常运转率',
                        //     templet:function(d){
                        //         console.log(d.bfexr,'bfexr')
                        //         return intercept(d.bfexr)
                        //     }
                        // }
                    ]]
                    ,id: 'testReload'
                    ,done:function(res){
                        if(res.count == 1){
                            that.isShow = true
                            var resTimeRate = res.data[0].responseTimeRate;
                            var arrTRate = res.data[0].arrTimeRate;
                            var resTRate = res.data[0].probSolveRate;
                            that.responseTimeRate = resTimeRate.substring(0,resTimeRate.length-1);
                            that.arrTimeRate = arrTRate.substring(0,arrTRate.length-1);
                            that.probSolveRate = resTRate.substring(0,resTRate.length-1);
                            echartsFun()
                        }else{
                            that.isShow = false
                        }
                    }
                });
                //概率保留整数
                function intercept(data){
                    var datas = data.split('%')[0]
                    return Math.round(datas)+'%'
                }
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'customerKpiSummary' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.name,
                            startDate : data.startDate,//开始日期
                            endDate : data.endDate,//结束日期
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
            function echartsFun(){
            var myChart = that.$echarts.init(document.getElementById('main'),'',{width:600,height:300})
            // 指定图表的配置项和数据
            console.log(that.responseTimeRate)
            var option = {
                title: {
                    text: '客户月KPI汇总'
                },
                tooltip: {},  //提示框
                legend: {               //图例
                    
                },
                xAxis: {            //x轴数据
                    type: 'category'
                },
                yAxis: [  
                    {  
                        type: 'value',  
                        axisLabel: {  
                            show: true,  
                            interval: 'auto',  
                            formatter: '{value} %'  
                            },  
                        show: true,
                        boundaryGap: true
                    }  
                ],            //y轴数据
                series: [        //图标类型)(柱状图)
                    {
                        type: 'bar',
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true, //开启显示
                                    position: 'top', //在上方显示
                                    textStyle: { //数值样式
                                        color: 'black',
                                        fontSize: 14,
                                        formatter: '{b}\n{c} %'
                                    }
                                }
                            }
                        },
                    },
                    {
                        type: 'bar',
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true, //开启显示
                                    position: 'top', //在上方显示
                                    textStyle: { //数值样式
                                        color: 'black',
                                        fontSize: 14,
                                        formatter: '{b}\n{c} %'
                                    }
                                }
                            }
                        },
                    },
                ],
                dataset: {
                    source: [
                        ['product', '达标率目标', '当月实际达标率'],
                        ['电话响应时间', '95', that.responseTimeRate],
                        ['到达现场时间', '95', that.arrTimeRate],
                        ['问题解决时间', '95', that.probSolveRate],
                        // ['设备正常运转率', '95', 0]
                    ]
                }, 
                //关闭动画，否则有些统计图表只有背景（如扇形统计图）
                animation:false,
            };

            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
            
            //获取echarts图的base64编码，为png格式
            var picture = myChart.getDataURL();
            // console.log(picture)
            that.pic = {picture : myChart.getDataURL()}
            // that.pic = picture
            // function aaaa(){
            //     exportBranchKpi(picture).then((res) => {
            //         console,log(res)					
            //     }).catch((error) => {
            //         // layer.msg('我的天呐，网炸了！')
            //     })
            // }
        }
        },
        exportExcel(){
			 var that = this;
             let userN = sessionStorage.getItem('username')
            exportCustomerKpiSummary(that.pic).then((res) => {
                window.open('http://'+res.data.msg+'?'+'username='+userN,"_blank")
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })		
		}
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



