<template>
<!-- 客户首页 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
            <div class="upleft">
                <!--故障报修及服务需求-->
                <dl class="bg-btn">
                    <dd class="bg-blue">
                        <!-- <button data-method="notice" class="layui-btn">示范一个公告层</button> -->
                        <router-link to="/home/workMsg/WorkMsgAdd">故障报修及服务需求</router-link>
                    </dd>
                    <dd class="bg-green">
                        <a href="javascript:;" @click="winFeedBack()">反馈与建议</a>
                    </dd>
                    <dd class="bg-pink">
                        <router-link to="/home/customerMsgr">KPI报告</router-link>
                    </dd>
                </dl>
                <dl class="layui-clear"></dl>
                <!--合同管理-->
                <dl class="contract bg-white" style="width: 99.5%;padding: 0;">
                    <dd class="title">
                        <router-link to="/home/contract">合同管理</router-link>
                    </dd>
                    <dd class="subordinate">
                        <dl>
                            <dd>主合同号</dd>
                            <dd><span class="content">{{zhts}}</span></dd>
                        </dl>
                        <dl>
                            <dd>开始日期</dd>
                            <dd><span class="content">{{ksrq}}</span></dd>
                        </dl>
                        <dl>
                            <dd>结束日期</dd>
                            <dd><span class="content">{{jsrq}}</span></dd>
                        </dl>
                        <dl>
                            <dd>合同总期数</dd>
                            <dd><span class="content">{{htzqs}}</span></dd>
                        </dl>
                        <dl>
                            <dd>已执行期数</dd>
                            <dd><span class="content">{{yzxqs}}</span></dd>
                        </dl>
                        <dl>
                            <dd>未执行期数</dd>
                            <dd><span class="content">{{wzxqs}}</span></dd>
                        </dl>
                    </dd>
                </dl>
                
            </div>
            <!--讯维解决方案介绍-->
            <div class="solution bg-white">
                <p>迅维解决方案介绍</p>
                <div class="caseWid">
                    <div class="caseSole" v-for="(item,index) in caseSole" :key="index"><span>{{item.parameterName}}</span><a :href="item.parameter" target="_blank" style="margin-left: 20px;">{{item.parameter}}</a></div>
                </div>
            </div>
            <div class="layui-clear"></div>
            <div class="down">
                <div class="contract-three bg-white" style="margin-left: 2%;width: 30.5%;">
                    <div class="title">设备及资产</div>
                    <div class="subordinate">
                        <dl>
                            <dd><router-link :to="{name: 'EquipmentManage',params:{woStatus: 'a'}}">合同设备数</router-link></dd>
                            <dd><span class="content">{{htsbs}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'EquipmentManage',params:{woStatus: 'b'}}">客户资产</router-link></dd>
                            <dd><span class="content">{{khzc}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name: 'EquipmentManage',params:{woStatus: 'c'}}">服务商资产</router-link></dd>
                            <dd><span class="content">{{fwszc}}</span></dd>
                        </dl>
                    </div>
                </div>
                <div class="contract-three bg-white" style="width: 30.5%;">
                    <div class="title">工单概况</div>
                    <div class="subordinate">
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'a'}}">本月工单</router-link></dd>
                            <dd><span class="content">{{bygd}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'b'}}">未完成</router-link></dd>
                            <dd><span class="content">{{wwc}}</span></dd>
                        </dl>
                        <dl>
                            <dd><router-link :to="{name:'WorkMsg',params:{woStatus:'c'}}">超时工单</router-link></dd>
                            <dd><span class="content">{{csgd}}</span></dd>
                        </dl>
                    </div>
                </div>
                <div class="contract-three bg-white" style="width: 30.5%;">
                    <div class="title">用户满意度</div>
                    <div class="contractSatis">
                        <ul><li class="describe">总评价数</li><li class="content">{{zpjs}}</li></ul>
                        <ul class="starLevel">
                            <!-- <li class="describe" style="margin-left: 10%;">星级</li> -->
                            <li class="describe"><router-link :to="{name: 'ContractSatis',params:{woStatus: '5'}}">非常满意</router-link>
                                <ul><li class="content">{{level5}}</li></ul>
                            </li>
                            <li class="describe"><router-link :to="{name: 'ContractSatis',params:{woStatus: '4'}}">满意</router-link>
                                <ul><li class="content">{{level4}}</li></ul>
                            </li>
                            <li class="describe"><router-link :to="{name: 'ContractSatis',params:{woStatus: '3'}}">一般</router-link>
                                <ul><li class="content">{{level3}}</li></ul>
                            </li>
                            <li class="describe"><router-link :to="{name: 'ContractSatis',params:{woStatus: '2'}}">不满意</router-link>
                                <ul><li class="content">{{level2}}</li></ul>
                            </li>
                            <li class="describe"><router-link :to="{name: 'ContractSatis',params:{woStatus: '1'}}">非常不满意</router-link>
                                <ul><li class="content">{{level1}}</li></ul>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            
        </div>
        <!--问题反馈与建议-->
            <div class="winFeedBack sdd dis-n">
                <div class="win">
                    <!-- <div class="closeW"><img src="../../../../static/images/close.svg" width="30px"></div> -->
                    <!-- <div clsss="title">问题反馈与建议</div> -->
                    <div class="textareaW">
                        <textarea placeholder="请留下你的宝贵意见" v-model='feed.content' @keyup="maxLength()"></textarea>
                        <span>{{remnant}}/<i style="color: #000">200</i></span>
                    </div>
                    <div class="subFeed"><a href="javascript:;" @click="subFeed()">提交</a></div>
                </div>
            </div>
    </div>
</template>
<script>
import Index from '../../index'
import {client,doAdvice} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            zhts:'',//主合同号
            ksrq:'',//开始日期
            jsrq:'',//结束日期
            htzqs:'',//合同总期数
            yzxqs:'',//已执行期数
            wzxqs:'',//未执行期数
            htsbs:'',//合同设备数
            khzc:'',//客户资产
            fwszc:'',//服务商资产
            bygd:'',//本月工单
            wwc:'',//未完成
            csgd:'',//超时工单
            zpjs:'',//总评价数
            level5:'',//5
            level4:'',//4
            level3:'',//3
            level2:'',//2
            level1:'',//1
            caseSole:[],
            feed: {
                username:'',
                content:''
            },
            remnant: 200
        }
    },
    mounted() {
        this.getData();
    },
    methods:{
        getData(){
            var that = this;
            var userN = sessionStorage.getItem('username');
            var userName = {username : userN}
            client(userName).then((res) => {
                // console.log(res.data.five)
                // var startTime = res.data.合同管理.startDate;
                if(res.data.合同管理 == null){

                }else{
                    var startTime = res.data.合同管理.startDate;
                    var dates = new Date(startTime);
                    var year = dates.getFullYear();
                    var month = (dates.getMonth()+1 < 10 ? '0'+(dates.getMonth()+1) : dates.getMonth()+1);
                    var date = dates.getDate();
                    var endTime = res.data.合同管理.endDate;
                    var dated = new Date(endTime);
                    var yearE = dated.getFullYear();
                    var monthE = (dated.getMonth()+1 < 10 ? '0'+(dated.getMonth()+1) : dated.getMonth()+1);
                    var dateE = dated.getDate();
                    
                    that.zhts = res.data.合同管理.contractNo;   //主合同号
                    that.ksrq = year+'-'+month+'-'+date;   //开始日期
                    that.jsrq = yearE+'-'+monthE+'-'+dateE;   //结束日期
                    that.htzqs = res.data.合同管理.contractDeadline;  //合同总期数
                    that.yzxqs = res.data.合同管理.kpi3;  //已执行期数
                    that.wzxqs = res.data.合同管理.dueDays;  //未执行期数
                }
                
                
                that.htsbs = res.data.合同设备数;  //合同设备数
                that.khzc = res.data.客户资产;   //客户资产
                that.fwszc = res.data.服务商资产;  //服务商资产
                that.bygd = res.data.本月工单;   //本月工单
                that.wwc = res.data.未完成;    //未完成
                that.csgd = res.data.超时工单;              //超时工单
                that.zpjs = res.data.用户评价;   //总评价数
                that.level5 = res.data.five; //5
                that.level4 = res.data.four; //4
                that.level3 = res.data.three; //3
                that.level2 = res.data.two; //2
                that.level1 = res.data.one; //1
                that.caseSole = res.data.方案解决介绍;
                // console.log(this.level5)
            })
            .catch((error) => {				
            })
        },
        winFeedBack(){
            var that = this;
            // console.log("jjjj");
            // that.feed = true;
            layui.use(['layer'], function(){
                var layer = layui.layer;
                var index = layer.open({
                    type: 1,
                    title: '反馈与建议',
                    area: ['700px','400px'],
                    content: $('.sdd'),
                    cancel:function(){
                        // alert("jjj")
                        that.feed.content = ''
                        that.remnant = 200
                    }
                });	
            })
            
        },
        subFeed(){
            var that = this;
            that.feed.username = sessionStorage.getItem('username');
            console.log(that.feed)
            layui.use(['layer'], function(){
                var layer = layui.layer;
                if(that.feed.content == ''){
                    layer.msg("你还没有留下建议呦！")
                }else{
                    doAdvice(that.feed).then((res) => {
                        if(res.data.code == 0){
                            layer.closeAll();
                            layer.msg('感谢您的评价，我们会做的更好！')
                            that.feed.content = ''
                        }else if(res.data.code == 1){
                            // layer.closeAll();
                            layer.msg(res.data.msg)
                            that.feed.content = ''
                            that.remnant = 200
                        }
                    })
                    .catch((error) => {				
                    })
                }
            })     
        },
        maxLength(){
            var that = this;
            var txtVal = that.feed.content.length;
            that.remnant = 200 - txtVal;
            if(that.remnant <= 0){
                that.feed.content = that.feed.content.substr(0,200)
                that.remnant = 0
                that.$layerMsg("你已超出最大字数限制")
            }
        }
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.contractSatis>ul{
    float: left;
}
.contractSatis>ul:first-child{
    float: left;
    width: 25%;
}
.starLevel{
    height: 60px;
}
.winFeedBack{
    
}
.textareaW{
    text-align: center;
}
.textareaW span{
    position: absolute;
    right: 100px;
    /*bottom: 130px;*/
    top: 230px;
    color: #f00;
    font-size: 16px;
}
.caseWid{
    width: 85%;
    margin: 10px auto;
}
.caseSole span{
    font-size: 16px;
}
.subFeed{
    margin-top: 20px;
    text-align: right;
    margin-right: 80px;
    font-size: 18px;
}
.subFeed a{
    display: inline-block;
    width: 80px;
    background: #1E9FFF;
    text-align: center;
    height: 35px;
    line-height: 35px;
    color: #fff;
}
.textareaW textarea{
    width: 550px;
    height: 220px;
    margin-top: 40px;
    font-size: 16px;
}
.bg-blue a:hover{
    color: #043fab;
}
.bg-green a:hover{
    color: #027148;
}
.bg-pink a:hover{
    color: #66218a;
}
.subordinate dd a{
    text-decoration: underline;
}
.starLevel a{text-decoration: underline;}
</style>



