<template>
    <!-- 工作日历 -->
    <div id="calendar">
    <div class="body">
		<div class="titleLab">&gt;&gt;工作日历&lt;&lt;</div>
            <div class="abovebtn">
                <!-- <span id="testView"></span> -->
                <div id="calendarDate"></div>
            </div>
        </div>
    </div>

</template>
<script>
import Index from '../../index'
import {doAddWorkingCalendar,workingCalendar,getCalendar} from '../../../config/axiosUtil'

export default {
    name: "Calendar",
    components: {
        Index
    },
    data(){
        return{
            item:'',
			formV: {
				staffId: '',
				name: '',
				jobState: '',
				startDate: '',
				endDate: '',
				reson: '',
                addShow: '',
                calendarData: '',
			}
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            getCalendar().then((res) => {
                console.log(res.data.data)
                that.calendarData = res.data.data					
            }).catch((error) => {
                // layer.msg('我的天呐，网炸了！')
            })
			var uname = sessionStorage.getItem('username')
            layui.use(['table','laydate','form'], function(){
                var table = layui.table,form = layui.form,laydate = layui.laydate;
                laydate.render({
                    elem: '#calendarDate'
                    ,position: 'static'
                    ,ready: function(date){
                        // console.log(date); //得到初始的日期时间对象
                    }
                    ,change: function(value, date){ //监听日期被切换
                        lay('#testView').html(value);
                        // console.log(value)
                        // console.log(date)
                    }
                    ,showBottom: false  //不显示底部栏
                    ,theme: 'molv'    //主题
                    // ,calendar: true   //允许显示公历节日
                    ,mark: {
                        '2019-02-04': '除夕'
                        ,'2019-02-05': '春节'
                        ,'2019-02-06': ''
                        ,'2019-02-07': ''
                        ,'2019-02-08': ''
                        ,'2019-02-09': ''
                        ,'2019-02-10': ''
                        // ,'2019-02-19': '元宵节' 
                        ,'2019-04-04': '' 
                        ,'2019-04-05': '清明节' 
                        ,'2019-04-06': '' 
                        ,'2019-05-01': '劳动节' 
                        ,'2019-05-02': '' 
                        ,'2019-05-03': '' 
                        ,'2019-05-04': '' 
                        ,'2019-06-07': '端午节' 
                        ,'2019-06-08': '' 
                        ,'2019-06-09': '' 
                        // ,'2019-08-07': '七夕'
                        // ,'2019-08-15': '中元节'
                        ,'2019-09-13': '中秋节'
                        ,'2019-09-14': ''
                        ,'2019-09-15': ''
                        // ,'2019-10-07': '重阳节'
                        ,'2019-10-01': '国庆节'
                        ,'2019-10-02': ''
                        ,'2019-10-03': ''
                        ,'2019-10-04': ''
                        ,'2019-10-05': ''
                        ,'2019-10-06': ''
                        ,'2019-10-07': ''
                    }
                    ,done: function(value, date){
                        console.log(value)
                        console.log(date)
                        if(value == '2019-05-02'){
                            // $('this').style('background','#333')
                            console.log($('this'))
                        }
                        // if(date.year === 2017 && date.month === 8 && date.date === 15){ //点击2017年8月15日，弹出提示语
                        // ins1.hint('中国人民抗日战争胜利72周年');
                        // }
                    }
                });
            });
        },		
    },
	created(){
    },
	destroyed() {
        // $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#calendar{
    width: 85%;
}
#calendarDate{
    margin: 0 auto;
    text-align: center;
}
.layui-laydate{
    width: 94%;
}


</style>


