import Vue from 'vue'
import App from './App'
import router from './router'
import '../static/css/leftnav.css'
import '../static/css/body.css'
import '../static/css/loginhomepage.css'
import './components/index'
import axios from 'axios'
// import $ from 'jquery'
// import "babel-polyfill"
import promise from 'es6-promise'
promise.polyfill()
import Calendar from 'vue2-datepick';
import echarts from 'echarts'
Vue.use(Calendar);

// let host = 'http://10.52.26.13:80/som/'
let host = 'http://47.106.110.201:80/som/'   //线上
 

//导出
let userN = sessionStorage.getItem('username')
Vue.prototype.$exportE = url => {
	// window.open('about:blank')
	window.open(host+url+'?'+'username='+userN)
	
}
Vue.prototype.$layerMsg = str => {
    layui.use('layer',function(){layer.msg(str)})
}
Vue.prototype.$layerClose = str => {
    layui.use(['form','table','layer'], function(){
    	var table = layui.table, layer = layui.layer;
    	layer.closeAll()
    	table.reload(str)						
    })
}
//服务区域下拉
Vue.prototype.$serviceArea = str => {
    layui.use(['form','layer'], function(){
		var form = layui.form, layer = layui.layer;
		var uRole = sessionStorage.getItem("userRole")
		var serviceA = sessionStorage.getItem("serviceArea")
		// console.log(serviceA)
    	if(uRole == '总部客服' || uRole == '运维总监' ){
			str().then((res) => {
				var data = res.data.data;
				var serverArea = []
				for(var i=0; i<data.length; i++){
					if(serverArea.indexOf(data[i].compName) == -1){
						serverArea.push(data[i].compName);
					}
				}
				for(var j=0; j<serverArea.length; j++){
					// console.log(serverArea[j])
					$('#address').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
				}
				form.render()
			})
			.catch((error) => {				
			});
		}else if(uRole == '客户'){
			$('#address').after('<option value=\''+ serviceA +'\' id="">'+ serviceA +'</option>');
			form.render()
		}else{
			if(serviceA == '广州乐派数码科技有限公司' || serviceA == '行业客户部' || serviceA == '系统推进部'){
				$('#address').after('<option value="广州乐派数码科技有限公司">广州乐派数码科技有限公司</option><option value="行业客户部">行业客户部</option><option value="系统推进部">系统推进部</option>');
				form.render()
			}else{
				$('#address').after('<option value=\''+ serviceA +'\' id="">'+ serviceA +'</option>');
				form.render()
			}
			
		}						
    })
}
Vue.prototype.$exportEa = url => {
	$.ajax({
		url: host+url,
		type:'get',
		async: false,
		success : function(res){
			window.open('http://'+res+'?'+'username='+userN)
		},
		error : function(error){
			alert(error)
		}
	})
}
