<template>
<!-- 账户管理 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;账户管理&lt;&lt;</div>
            <div class="abovebtn">
            <form class="layui-form" action="" >
                <div class="layui-inline">
                    <label class="layui-form-label">角色</label>
                    <div class="layui-input-block">
                        <select name="role" lay-verify="role" lay-search="">
                            <option value="">请选择</option>
                            <option value="运维经理">运维经理</option>
                            <option value="技术主管">技术主管</option>
                            <option value="总部客服">总部客服</option>
                            <option value="工程师">工程师</option>		
                            <option value="驻现场人员">驻现场人员</option>								
                            <option value="运维助理">运维助理</option>								
                            <option value="客服经理">客服经理</option>								
                            <option value="运维总监">运维总监</option>	
						</select>
                        <!-- <input type="text" name="role"   lay-verify="" placeholder="" autocomplete="off" class="layui-input"> -->
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">所属公司</label>
                    <div class="layui-input-block">
                        <input type="text" name="custName"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">手机号</label>
                    <div class="layui-input-block">
                        <input type="text" name="phone"   lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                    </div>
                </div>
                <div class="btn">
                    <a class="layui-btn layui-bg-blue" lay-filter="select" lay-submit="">查询</a>
                    <a class="layui-btn layui-bg-blue" v-if='addShow' @click="add()">新增账户</a>
                </div>
            </form>
            
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            <script type="text/html" id="barDemo">
                <a class="layui-btn layui-btn-xs" lay-event="resetPwd">重置密码</a>
            </script>
        </div>
        <!-- 新增账户 -->
        <div class="sdd dis-n">
            <form class="layui-form" lay-filter="example" id="addGoodsForm">
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>角色名称</label>
                        <div class="layui-input-inline upWidth">
                            <!-- <input type="text" name="assetNumber" class="layui-input" value="客户" readonly> -->
                            <select name="role" lay-filter="role" lay-verify="required" class="upWidth">
                                <option value="">请选择</option>
                                <option value="客户">客户</option>
                                <option value="公司员工">公司员工</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div class="layui-form-item custName" v-if="custName">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>客户名称</label>
                        <div class="layui-input-inline">
                            <input type="text" name="userName" class="layui-input upWidth" lay-verify="required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item staffNum" v-if="staffNum">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>员工编码</label>
                        <div class="layui-input-inline ">
                            <input type="text" name="staffId" id="staff" @blur="staff()" class="layui-input upWidth" lay-verify="required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item affiliate" v-if="cust">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>所属公司</label>
                        <div class="layui-input-inline">
                            <input type="text" name="compName" class="layui-input upWidth" lay-verify="required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item affiliate"  v-if="worker">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>所属公司</label>
                        <div class="layui-input-inline">
                            <input type="text" name="compName" unselectable='on' readonly v-model="compName" class="layui-input upWidth" lay-verify="required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item" v-if="cust">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>账号</label>
                        <div class="layui-input-inline">
                            <input type="text" name="accout" class="layui-input upWidth" lay-verify="phone||required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item" v-if="worker">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>账号</label>
                        <div class="layui-input-inline">
                            <input type="text" name="accout" unselectable='on' readonly v-model="accout" class="layui-input upWidth" lay-verify="phone||required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item">
                    <div class="layui-inline">
                        <label class="layui-form-label"><span>*</span>密码</label>
                        <div class="layui-input-inline">
                            <input type="text" name="password" class="layui-input upWidth" lay-verify="required" autocomplete="off">
                        </div>
                    </div>
                </div>
                <div class="layui-form-item btn" v-if="refer">
                    <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="subsave" v-if="save">提交</a>
                    <a class="layui-btn layui-bg-blue" lay-submit="" lay-filter="save" v-else>保存</a>
                    <button type="reset" class="layui-btn" id="resetBut">重置</button>
                </div>
                <div class="layui-form-item btn" v-else>
                    <a class="layui-btn layui-bg-blue" v-if="save">提交</a>
                    <a class="layui-btn layui-bg-blue" v-else>保存</a>
                    <a type="reset" class="layui-btn reset dis-n">重置</a>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {resetPassword,addCust,addCompany,staffMessage} from '../../../config/axiosUtil'

export default {
    name: "Account",
    components: {
        Index
    },
    data(){
        return{
            item:'',
            custName:false,
            staffNum:false,
            save:true,
            cust: true,
            worker: false,
            compName:'',
            accout:'',
			addShow: false,
            refer: true
        }
    },
    mounted() {
        this.getData()
        // $(".layui-layer-setwin").click(function(){
        //     $(".reset").click()
        // })
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem('username');
            layui.use(['table','form'], function(){
                var table = layui.table,form = layui.form;
                
                form.render();
                table.render({
                    elem: '#demo'
                    ,method: 'post'
                    ,height: 'full-280'
                    ,cellMinWidth: 120
                    ,url: that.$url+'selectUser' //数据接口
                    ,where:{username:uname}
                    ,page: true //开启分页
                    ,limit: 20
                    ,cols: [[ //表头
                        {title: '序号',  type:'numbers', width: 80}
                        ,{field: 'role', title: '角色'}
                        ,{field: 'userName', title: '姓名'}
                        ,{field: 'userId', title: '账户ID'}
                        ,{field: 'custName', title: '所属公司'}
                        ,{field: 'gnx', title: '操作项',toolbar: '#barDemo'}
                    ]]
                    ,id: 'testReload'
                });
                //查询
                form.on('submit(select)', function(data){
                    var data = data.field;
                    table.reload('testReload', {
                        url: that.$url+'selectUser' ,//数据接口
						method: 'post'
                        ,where: { //设定异步数据接口的额外参数，任意设
                            role : data.role,
                            custName : data.custName,
                            phone : data.phone,
                            username:uname
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                    return false;
                });
                //重置密码
                table.on('tool(demo)',function(obj){
                    if(obj.event == 'resetPwd'){
                        var usernames = {userId : obj.data.userId};
                        console.log(usernames)
                        layer.confirm('确认重置密码？', function(index){
                            layer.close(index);
                            resetPassword(usernames).then((res) => {
                                console.log(res)
                                if(res.data.code == 0){
                                    layer.msg('重置成功！密码为123456')
                                }else{
                                    layer.msg(res.data.msg)
                                }
                                
                            }).catch((error) => {
                                // layer.msg(res.)
                            }) 
                        });
                    }
                })
                function monitor(){
                    //监听客户和其他角色
                    // var role = '';
                    form.on('select(role)',function(data){
                        // console.log(data)
                        // console.log(data.value)
                        var role = data.value;
                        if(role == '客户'){
                            that.custName = true;
                            that.staffNum = false;
                            that.save = true;
                            that.cust = true;
                            that.worker = false;
                            // that.compName = '';
                            // that.accout = '';
                        }else if(role == '公司员工'){
                            that.staffNum = true;
                            that.custName = false;
                            that.save = false;
                            that.cust = false;
                            that.worker = true;
                            that.compName = '';
                            that.accout = '';
                        }else{
                            that.custName=false;
                            that.staffNum=false;
                            that.save=true;
                            that.cust= true;
                            that.worker= false;
                            that.compName = '';
                            that.accout = '';
                        }
                    })
                }
                monitor()
                //企业员工新增
                form.on('submit(save)', function(data){
                    that.refer = false
                    var addDatas = {
                        staffId : data.field.staffId,   //员工编码
                        password : data.field.password    //密码
                    }
                    
                    addCompany(addDatas).then((res) => {
                        var datas = res.data;
                        //提交成功
                        if(datas.code == 0){
                            layer.closeAll()
                            layer.msg(datas.msg);
                            table.reload('testReload');
                            that.refer = true
                            $("#addGoodsForm")[0].reset();
                            form.val('example',{
                                'role':''
                            })
                            that.custName=false;
                            that.staffNum=false;
                            that.save=true;
                            that.cust= true;
                            that.worker= false;
                            that.compName = '';
                            that.accout = '';
                            
                            layui.form.render();
                        }else{
                            layer.msg(datas.msg);
                            $("#addGoodsForm")[0].reset();
                            that.refer = true
                            form.val('example',{
                                'role':'公司员工'
                            })
                            that.staffNum = true;
                            that.custName = false;
                            that.save = false;
                            that.cust = false;
                            that.worker = true;
                            that.compName = '';
                            that.accout = '';
                            
                            layui.form.render();
                        }
                    })
                    .catch((error) => {				
                    })
                    return false;
                });
                // 客户新增
                form.on('submit(subsave)', function(data){
                    console.log(data)
                    that.refer = false
                    var addData = {
                        role : data.field.role,    //角色名称
                        compName : data.field.compName,   //公司名称
                        userName : data.field.userName,    //客户姓名
                        accout : data.field.accout,    //账号
                        password : data.field.password    //密码
                    }
                    addCust(addData).then((res) => {
                        var datas = res.data;
                        if(datas.code == 0){
                            that.refer = true
                            layer.closeAll()
                            layer.msg(datas.msg);
                            table.reload('testReload');
                            $("#addGoodsForm")[0].reset();
                            form.val('example',{
                                'role':''
                            })
                            that.custName=false;
                            that.staffNum=false;
                            that.save=true;
                            that.cust= true;
                            that.worker= false;
                            that.compName = '';
                            that.accout = '';
                            
                            layui.form.render();
                            
                        }else{
                            layer.msg(datas.msg);
                            that.refer = true
                            $("#addGoodsForm")[0].reset();
                            layui.form.render();
                            form.val('example',{
                                'role':'客户'
                            })
                        }
                    })
                    .catch((error) => {				
                    })
                    return false;
                });

                
            });
        },
        add(){
            layui.use(['layer','form'],function(){
                
                var layer = layui.layer,form = layui.form;
                layer.open({
					type: 1,
                    title: '新增账户',
					area: ['700px','500px'],
					content: $('.sdd'),
                    // cancel: function(index, layero){
                    //     console.log(index)
                    //     console.log(layero)
                    //     form.on('select(role)',function(data){
                    //         data.value = ''
                    //         console.log(data)
                    //     })
                    // }
				});	
               
            })
        },
        //员工编号
        staff(){
            var that = this;
            var staffNum = $('#staff').val()
            var staffData = {staffId:staffNum};
            console.log(staffNum)
            // if(staffNum == ''){
            //     staffMessage(staffData).then((res) => {	
            //         var datas = res.data;
            //         if(datas.code == 1){
            //             layer.msg(datas.msg);
            //             // $('.reset').click();
            //         }else if(datas.code == 0){
            //             that.compName = res.data.data.compName;
            //             that.accout = res.data.data.phone;
            //         }
            //     })
            //     .catch((error) => {				
            //     })
            // }
            staffMessage(staffData).then((res) => {	
                var datas = res.data;
                if(datas.code == 1){
                    layer.msg(datas.msg);
                    // $('.reset').click();
                }else if(datas.code == 0){
                    that.compName = res.data.data.compName;
                    that.accout = res.data.data.phone;
                }
            })
            .catch((error) => {				
            });
            // $('.layui-layer-close1').click(function(){
                
            //     layer.closeAll();
            //     $('.reset').click();
            //      that.custName=false;
            //             that.staffNum=false;
            //             that.save=true;
            //             that.cust= true;
            //             that.worker= false;
            //     // that.custName = true;
            //     // that.staffNum = false;
            //     // that.save = true;
            //     // that.cust = true;
            //     // that.worker = false;
            //     that.compName = '';
            //     that.accout = '';

            // })
        },
		showA(){
			var that = this
			var arr = [that.$admin.zbkf,that.$admin.ywzj,that.$admin.admin]
			console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
			}
		},	

// 		isShow(){
// 			var that = this
// 			var arr2 = that.$admin
// 			var admin = ''
// 			if(arr2.zbkf == that.$userRole || arr2.admin == that.$userRole || arr2.ywzj == that.$userRole){
// 				admin = '<a class="layui-btn layui-btn-xs" lay-event="edit">修改</a><a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="del">删除</a>'	
// 			}else{
// 				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
// 			}
// 			return admin;			
// 		},
	},
	created(){
		this.showA()
	}
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



