<template>
<!-- 设备管理报表 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;设备管理报表&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                    <div class="layui-inline">
                        <label class="layui-form-label">服务区域</label>
                        <div class="layui-input-block">
                            <select name="name" lay-verify="">
                                <option value="" id="address">请选择</option>
                            </select>
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName"  lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">合同号码</label>
                        <div class="layui-input-block">
                            <input type="text" name="contartctNo" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="btn">
                        <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome">查询</a>
                        <a class="layui-btn layui-bg-blue"  @click='exportExcel()'>导出</a>
                    </div>
                </form>
                
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,exportEquipmentManagement} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem("username")
            layui.use(['table','form'], function () {
                var table = layui.table,form = layui.form;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                form.render();
                table.render({
                    elem: '#demo'
                    , height: 'full-280'
                    , cellMinWidth: 140
                    ,url: that.$url+'equipmentManagementReport' //数据接口
					,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: '序号', type:'numbers',width:60,fixed:"left"}
                        , {field: 'machCode', title: '机器编码'}
                        , {field: 'assetAttr', title: '设备归属',minWidth:100}
                        , {field: 'assetNumber', title: '固定资产编号'}
                        , {field: 'contractNo', title: '合同号码',minWidth:180}
                        , {field: 'custArea', title: '客户名称'}
                        , {field: 'installedTime', title: '装机日期'}
                        , {field: 'deviceBrand', title: '品牌'}
                        , {field: 'devName', title: '设备类型'}
                        , {field: 'unitType', title: '设备型号规格'}
                        , {field: 'esNumber', title: '设备序列号'}
                        , {field: 'deviceUserYear', title: '设备已使用年数'}
                        , {field: 'mainFrequency', title: '设备使用状态'}
                        , {field: 'secret', title: '是否保密设备'}
                        , {field: 'secretLevel', title: '密级'}
                        , {field: 'location', title: '设备存放位置'}
                        , {field: 'ip', title: 'IP地址'}
                        , {field: 'holdMan', title: '资产管理人'}
                        , {field: 'mainFrequency', title: '备注'}
                    ]]
                    ,id: 'testReload'
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'equipmentManagementReport' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.custName,
                            serviceArea : data.name,
                            contartctNo : data.contartctNo,
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
        },
        exportExcel(){
			var that = this
			that.$exportE('exportEquipmentManagement')			
		},
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



