<template>
    <div id="workerOrder">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;工单概况&lt;&lt;</div>
			<!-- <input class="file" name="file" type="file" multiple accept="image/png,image/gif,image/jpeg" @change="update"/> -->
            <div class="abovebtn">
                <div class="layui-inline pos-abs">
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block allinput">
                        <select  v-model='v.serviceArea' class="allinput bor-col">
                            <option value="" id="address">请选择</option>
                        </select>
                    </div>
                </div>
                <div class="layui-form" action="">
                    <div class="layui-form-item">
                        <div class="layui-inline" style="margin-left: 324px;">
                            <label class="layui-form-label">客户名称</label>
                            <div class="layui-input-block allinput">
                                <input type="text" name='custName' class="layui-input" id="demoReload" v-model="v.custName" autocomplete="off">
                            </div>
                        </div>
                    
                        <div class="layui-inline">
                            <label class="layui-form-label">工单状态</label>
                            <div class="layui-input-block allinput">
                                <select name="workState" lay-verify="">
                                    <option value="">请选择</option>
                                    <option value="0">待处理</option>
                                    <option value="1">已受理</option>
                                    <option value="2">处理中</option>
                                    <option value="3">二次上门</option>
                                    <option value="4">维修完成</option>
                                </select>
                            </div>
                        </div>
                        <div class="layui-inline">
                            <label class="layui-form-label">服务类型</label>
                            <div class="layui-input-block">
                                <input type="checkbox" name="a" title="事故类" >
                                <input type="checkbox" name="b" title="事件类" >
                                <input type="checkbox" name="c" title="需求类" >
                            </div>
                        </div>
                        <div class="layui-inline">
                            <label class="layui-form-label">开始日期</label>
                            <div class="layui-input-inline">
                            <input type="text" class="layui-input" id="startDate" placeholder="年-月-日" name="startDate" lay-verify="startDate" readonly  autocomplete="off">
                            </div>
                        </div>
                        <div class="layui-inline">
                            <label class="layui-form-label">结束日期</label>
                            <div class="layui-input-inline">
                            <input type="text" class="layui-input" id="endDate" placeholder="年-月-日" name="endDate" lay-verify="endDate" readonly autocomplete="off">
                            </div>
                        </div>
                    </div>
                    <div class="layui-form-item btn">
                        <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDemo" @click='selectAll()'>查询</a>
                        <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                    </div>
                </div>
                
                <div class="labnum">
                    <label>工单总数：&nbsp;&nbsp;{{itemn.count}}&nbsp;&nbsp;</label>
                    <label>未完成工单：&nbsp;&nbsp;{{itemn.count2}}&nbsp;&nbsp;</label>
                    <label>已完成工单：&nbsp;&nbsp;{{itemn.count3}}&nbsp;&nbsp;</label>
                </div>
            </div>
            <div class="tab">
                <table id="workTable" lay-filter="workTable"></table>
            </div>
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {orderSituation,img} from '../../../config/axiosUtil.js'

export default {
    name: "WorkerOrder",
    components: {
        Index
    },
    data() {
      return {        
            v: {
                custName:'',
                serviceArea:'',
                startDate:'',
                endDate:'',
                workState:'',
                // faultType:[],
                conver: '',
                a: '',
                b: '',
                c: ''
            },				
            item: {
                post2: ''
            },
            item2: {
                
            },
            itemn: {
                count: '',
                count2: '',
                count3: ''
            }
            // sss: ''
      };
			
    },
    mounted() {
        this.getData()
    },
    methods: {        
        getData() {
            var that = this;
            var uname = sessionStorage.getItem('username')
            orderSituation().then((res) => {				
                // var a = res.data.data;	
                // var b = []
                // for(var i = 0;i<a.length;i++){
                //     b.push(a[i].serviceArea)						
                // }
                // that.item.post2 = [...new Set(b)];		
                // console.log(that.item.post2)
                var data = res.data.data;
                    var serverArea = []
                    for(var i=0; i<data.length; i++){
                        if(serverArea.indexOf(data[i].serviceArea) == -1){
                            serverArea.push(data[i].serviceArea);
                        }
                    }
                    for(var j=0; j<serverArea.length; j++){
                        console.log(serverArea[j])
                        $('#address').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
                    }
            })
            .catch((error) => {							
            })
            layui.use(['form','laydate','table'], function(){
                var form = layui.form,table = layui.table,laydate = layui.laydate;
                //模糊查询和客户数量
                form.render()
				// var cusName = that.item.cusName;
                table.render({
                    elem: '#workTable'
                    ,height: 'full-320'
                    ,cellMinWidth: 120
                    ,url: that.$url+'orderSituation',//数据接口
					method: 'post'
                    ,where:{username:uname}
                    ,page: true //开启分页
                    ,cols: [[ //表头
                        {field: 'id', title: '序号', sort: true, align: 'center', width:80,type:'numbers'}
                        ,{field: 'custName', title: '客户名称', align: 'center',width:180}
                        ,{field: 'woNumber', title: '工单号码', align: 'center', sort: true,width:180}
                        ,{field: 'custAddr', title: '客户地址', align: 'center',width:180}
                        ,{field: 'repairMan', title: '报修人姓名', align: 'center',width:120},
						,{field: 'serviceArea', title: '服务区域', align: 'center',width:150}
                        ,{field: 'repairService', title: '联系电话', align: 'center',width:120}
                        ,{field: 'devName', title: '设备名称', align: 'center',width:120}
                        ,{field: 'machCode', title: '机器编码', align: 'center', sort: true,width:120}
                        ,{field: 'unitType', title: '设备型号'}
                        ,{field: 'custAddr', title: '设备位置'}
                        ,{field: 'esNumber', title: '设备序列号', align: 'center',width:120}             
                        ,{field: 'enginner', title: '指定工程师'}
                        ,{field: 'repairTime', title: '报修时间',templet:"<div>{{layui.util.toDateString(d.repairTime,'yyyy-MM-dd')}}</div>"}
                        ,{field: 'faultType', title: '服务类型'}
                        ,{field: 'accidentType', title: '服务类别'}
                        ,{field: 'remark', title: '故障描述'}
                        ,{field: 'woStatus', title: '处理状态', align: 'center',width:120}
                        ,{field: 'maintenanceFeedback', title: '处理措施', align: 'center',width:120}
                        ,{field: 'repairType', title: '工单来源', align: 'center',width:120}
                        // ,{field: 'remark', title: '备注', align: 'center',width:120}
                    ]]
                    ,id: 'testReload'
                    ,parseData: function(res){
                        console.log(res)
                        that.itemn.count = res.count;
                        that.itemn.count2 = res.count2;
                        that.itemn.count3 = res.count3;
                        return {
                            "code": res.code, //解析接口状态
                            "count": res.count, //解析数据长度
                            "data": res.data //解析数据列表
                        };
                    }
                });
                // 下拉
                // orderSituation().then((res) => {
                //     var data = res.data.data;
                //     var serverArea = []
                //     for(var i=0; i<data.length; i++){
                //         if(serverArea.indexOf(data[i].serviceArea) == -1){
                //             serverArea.push(data[i].serviceArea);
                //         }
                //     }
                //     for(var j=0; j<serverArea.length; j++){
                //         console.log(serverArea[j])
                //         $('#address').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
                //     }
                //     form.render()
                // })
                // .catch((error) => {				
                // });
                laydate.render({
                    elem: '#startDate',
                    // done: function (value, date, endDate){
                    //     console.log(value)
                    //     console.log(date)
                    //     console.log(endDate)
                    // }
                });
                laydate.render({
                    elem: '#endDate'
                });
                //监听提交
                
            });
        },		
// 		update(e){  //上传图片
// 		  console.log(e)
//           let file = e.target.files;
// 		  let param = new FormData(); //创建form对象
// 		  
// 		  for(var i = 0,l = file.length;i<l;i++){				  
// 			  param.append('file',file,file[i].name);//通过append向form对象添加数据
// 			  param.append('chunk','0');//添加form表单中其他数据
// 		  }
// 		 
//           console.log(file)
//           // console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
//           
//           img(param)
//           .then(response=>{
//             console.log(response.data);
//           })        
// 		},
        // 查询		
        selectAll(){
            var that = this;
            $('.layui-icon-close').click();
            layui.use(['form','laydate','table'], function(){
                    var form = layui.form,table = layui.table;
                    var laydate = layui.laydate;
                    //模糊查询和客户数量
                    form.render()
                    form.on('submit(formDemo)', function(data){
                        // that.v.faultType = []
                        var ab = JSON.stringify(data.field);
                        // console.log(data.field)
                        ab = JSON.parse(ab)
                        var bb = {
                            serviceArea : that.v.serviceArea}
                        var cv  = that.$extend(ab,bb)						
                        that.item = cv;												
                        var custName = that.item.cusName;
                        var serviceArea = that.item.serviceArea;
                        var startDate = that.item.startDate;
                        var endDate = that.item.endDate;
                        var workState = that.item.workState;
                        var a = that.item.a;		
                        var b = that.item.b;
                        var c = that.item.c;
                        var username = sessionStorage.getItem("username")
                        console.log(bb)
                        console.log(that.item)
                        table.render({
                        elem: '#workTable'
                        ,height: 300
                        ,cellMinWidth: 120
                        ,url: that.$url+'orderSituation' //数据接口
                        ,method: 'post'
                        ,where: {custName,serviceArea,startDate,endDate,workState,a,b,c,username}
                        ,page: true //开启分页
                        ,cols: [[ //表头
                                {field: 'id', title: '序号', sort: true, align: 'center', width:80,type:'numbers'}
                                ,{field: 'custName', title: '客户名称', align: 'center',width:180}
                                ,{field: 'woNumber', title: '工单号码', align: 'center', sort: true,width:180}
                                ,{field: 'custAddr', title: '客户地址', align: 'center',width:180}
                                ,{field: 'repairMan', title: '报修人姓名', align: 'center',width:120},
                                ,{field: 'serviceArea', title: '服务区域', align: 'center',width:150}
                                ,{field: 'repairService', title: '联系电话', align: 'center',width:120}
                                ,{field: 'devName', title: '设备名称', align: 'center',width:120}
                                ,{field: 'machCode', title: '机器编码', align: 'center', sort: true,width:120}
                                ,{field: 'unitType', title: '设备型号'}
                                ,{field: 'custAddr', title: '设备位置'}
                                ,{field: 'esNumber', title: '设备序列号', align: 'center',width:120}             
                                ,{field: 'enginner', title: '指定工程师'}
                                ,{field: 'repairTime', title: '报修时间',templet:"<div>{{layui.util.toDateString(d.repairTime,'yyyy-MM-dd')}}</div>"}
                                ,{field: 'faultType', title: '服务类型'}
                                ,{field: 'accidentType', title: '服务类别'}
                                ,{field: 'remark', title: '故障描述'}
                                ,{field: 'woStatus', title: '处理状态', align: 'center',width:120}
                                ,{field: 'maintenanceFeedback', title: '处理措施', align: 'center',width:120}
                                ,{field: 'repairType', title: '工单来源', align: 'center',width:120}
                        ]]
                        ,id: 'testReload'
                        ,parseData: function(res){
                            console.log(res)
                            that.itemn.count = res.count;
                            that.itemn.count2 = res.count2;
                            that.itemn.count3 = res.count3;
                            return {
                                "code": res.code, //解析接口状态
                                "count": res.count, //解析数据长度
                                "data": res.data //解析数据列表
                            };
                        }
                });
                        return false;
                    });							
                    
            });
        },
				exportExcel(){
					var that = this
					that.$exportE('exportOrderSituation')
				}
    },
    created() {
        // get()
    },
    destroyed() {
        $('.layui-icon-close').click();
    },
}
</script>
<style scoped>
#workerOrder{
    width:85%;
    
}

</style>



