<template>
<!-- 运维合同报表 -->
    <div id="assistant">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;运维合同报表&lt;&lt;</div>
            <div class="abovebtn">
                <form class="layui-form" action="">
                    <div class="layui-inline">
                        <label class="layui-form-label">客户名称</label>
                        <div class="layui-input-block">
                            <input type="text" name="custName" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-inline">
                        <label class="layui-form-label">合同编码</label>
                        <div class="layui-input-block">
                            <input type="text" name="contartctNo" lay-verify="" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="btn">
                        <a class="layui-btn layui-bg-blue" lay-submit lay-filter="formDome">查询</a>
                        <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
                    </div>
                </form>
                
            </div>
            <div class="tab">
                <table id="demo" lay-filter="demo"></table>
            </div>
            
        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {areaManage,exportOperationalContract} from '../../../config/axiosUtil'

export default {
    name: "Assistant",
    components: {
        Index
    },
    data(){
        return{
            item:''
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            var that = this;
            var uname = sessionStorage.getItem("username")
            layui.use(['table','form'], function () {
                var table = layui.table,form = layui.form;
                //监听提交
                form.on('submit(formDemo)', function(data){
                    layer.msg(JSON.stringify(data.field));
                    return false;
                });
                table.render({
                    elem: '#demo'
                    , height: 'full-280'
                    , cellMinWidth: 120
                    ,url: that.$url+'operationalContract' //数据接口
					,where:{username:uname}
                    , page: true //开启分页
                    ,limit: 20
                    , cols: [[ //表头
                        {field: 'id', title: '序号', type:'numbers',width: 60,fixed:'left'}
                        , {field: 'custName', title: '客户名称',width: 180,fixed:'left'}
                        , {field: 'mainService', title: '服务区域'}
                        , {field: 'contractNo', title: '合同编码'}
                        , {field: 'contractNature', title: '合同性质'}
                        , {field: 'contractType', title: '合同类型'}
                        , {field: 'regTime', title: '登记时间'}
                        , {field: 'handlingDepartment', title: '经办部门'}
                        , {field: 'agent', title: '经办人'}
                        , {field: 'contractHoldman', title: '合同保管人'}
                        , {field: 'startDate', title: '签约日期'}
                        , {field: 'endDate', title: '到期日期'}
                        , {field: 'contractDeadline', title: '合同期限(月)'}
                        , {field: 'dueDays', title: '离到期天数'}
                    ]]
                    ,id: 'testReload'
                });
                // 查询服务区域下拉内容
				that.$serviceArea(areaManage)
                // 查询
                form.on('submit(formDome)',function(data){
                    console.log(data)
                    var data = data.field;
                    console.log(data)
                    table.reload('testReload', {
                        url: that.$url+'operationalContract' //数据接口
                        ,where: { //设定异步数据接口的额外参数，任意设
                            custName : data.custName,
                            contractNo : data.contartctNo,
                            username : uname,
                        }
                        ,page: {
                            curr: 1 //重新从第 1 页开始
                        }
                    });
                })
            });
        },
        exportExcel(){
			var that = this
			that.$exportE('exportOperationalContract')			
		},
    }
}
</script>
<style scoped>
#assistant{
    width: 85%;
}
</style>



