<template>
    <!-- 在岗人数 -->
    <div id="atWork">
        <!-- <Index/> -->
        <div class="body">
        <div class="titleLab">&gt;&gt;在岗人数&lt;&lt;</div>
            <div class="abovebtn">

                <div class="layui-inline" >
                    <label class="layui-form-label">服务区域</label>
                    <div class="layui-input-block">
                        <select name="name" v-model='v.serviceArea' class="allinput bor-col">
                            <option value="">请选择</option>
                            <option v-for='vb in item.compName' :value='vb'>{{ vb }}</option>                            
                        </select>
                    </div>
                </div>
                <div class="layui-inline">
                    <label class="layui-form-label">岗位</label>
                    <div class="layui-input-block">
                        <select name="post" v-model='v.post' class="allinput bor-col">
                            <option value="">请选择</option>
                            <option v-for='vc in item.post2' :value='vc'>{{ vc }}</option>                           
                        </select>
                    </div>
                </div>            
            <div class="btn">
                <a class="layui-btn layui-bg-blue" @click='selectAll()'>查询</a>
                <a class="layui-btn layui-bg-blue" @click='exportExcel()'>导出</a>
            </div>
            <div class="labnum">
                <label>在岗人数：&nbsp;&nbsp;{{number.count}}&nbsp;&nbsp;</label>
                <label>客服人数：&nbsp;&nbsp;{{number.count2}}&nbsp;&nbsp;</label>
                <label>工程师：&nbsp;&nbsp;{{number.count3}}&nbsp;&nbsp;</label>
                <label>客服经理：&nbsp;&nbsp;{{number.count4}}&nbsp;&nbsp;</label>
                <label>技术主管：&nbsp;&nbsp;{{number.count5}}&nbsp;&nbsp;</label>
                <label>运维经理：&nbsp;&nbsp;{{number.count6}}&nbsp;&nbsp;</label>
                <label>驻现场人员：&nbsp;&nbsp;{{number.count7}}&nbsp;&nbsp;</label>
            </div>
            </div>
            <div class="tab">
                <table id="tableAtWork" lay-filter="tableAtWork"></table>
            </div>

        </div>
    </div>
</template>
<script>
import Index from '../../index'
import {onTheJob,exportE} from '../../../config/axiosUtil'

export default {
    name: "AtWork",
    components: {
        Index
    },
    mounted() {
        this.getData();
    },
	data() {
		return {
			v : {
				serviceArea: '',
				post: ''
			},
			item: {
				post2: '',
				compName: ''
			},
            number: {
                count: '',
                count2: '',
                count3: '',
                count4: '',
                count5: '',
                count6: '',
                count7: ''
            }
		}
	},
    methods:{
        getData(){
            var that = this;		
            var uname = sessionStorage.getItem('username')	
            onTheJob(that.v).then((res) => {	
                console.log(res.data)		
                that.number.count = res.data.count;			
                that.number.count2 = res.data.count2;			
                that.number.count3 = res.data.count3;			
                that.number.count4 = res.data.count4;			
                that.number.count5 = res.data.count5;			
                that.number.count6 = res.data.count6;			
                that.number.count7 = res.data.count7;			
            	var a = res.data.data;	
            	var b = []
				var c = []
            	for(var i = 0;i<a.length;i++){
            		b.push(a[i].post)
					c.push(a[i].compName)
            	}
            	that.item.post2 = [...new Set(b)];		
            	that.item.compName = [...new Set(c)];					
            })
            .catch((error) => {				
            })
            layui.use(['table','form'], function(){
                var table = layui.table,form = layui.form;
                table.render({
                    elem: '#tableAtWork'
                    ,height: 'full-320'
                    ,cellMinWidth: 120
                    ,url: that.$url+'onTheJob' //数据接口
                    ,where:{username:uname}
                    ,page: true //开启分页
                    ,cols: [[ //表头
                        {field: 'id', title: '序号', sort: true,type:'numbers',width:80}
                        ,{field: 'compName', title: '服务区域'}
                        ,{field: 'name', title: '姓名',}
                        ,{field: 'phone', title: '联系电话'}
                        ,{field: 'post', title: '岗位'}
                        ,{field: 'workCond', title: '工作状态'}
                        ,{field: 'remark', title: '备注'}
                    ]]
                });
            });
        },
        //查询
		selectAll(){
            $('.layui-icon-close').click();
			var that = this;				
			layui.use(['table','form'], function(){
				var table = layui.table,form = layui.form;
				var serviceArea = that.v.serviceArea;	
				var post = that.v.post;		
                var username = sessionStorage.getItem('username')		
				table.render({
					elem: '#tableAtWork'
					,height: 312
					,cellMinWidth: 80
					,url: that.$url+'onTheJob' //数据接口
					,where: {serviceArea,post,username}
					,page: true //开启分页
					,cols: [[ //表头
						{field: 'id', title: '序号', sort: true,type:'numbers',width:80}
						,{field: 'compName', title: '服务区域'}
						,{field: 'name', title: '姓名',}
						,{field: 'phone', title: '联系电话'}
						,{field: 'post', title: '岗位'}
						,{field: 'workCond', title: '工作状态'}
						,{field: 'remark', title: '备注'}
					]]
				});
			});
		},
		exportExcel(){
			var that = this;
			that.$exportE('exportOnTheJob')			
		}
    },
    // destroyed() {
    //     $('.layui-icon-close').click();
    // },
}
</script>
<style scoped>
#atWork{
    width:85%;
}
</style>



