<template>
  <div id="login">
    <div class="contents">
            <!--登陆左边-->
            <div class="left">
                <div class="">
                    <img src="static/images/logo.png"/>
                    <img src="static/images/logop.png"/>
                    <!-- <p class="font">广东讯维信息产业股份有限公司</p> -->
                </div>
                <div class="doubleCenter">
                    <p>服务运维管理系统</p>
                    <p>Service Operation Management System</p>
                </div>
            </div>
            <!-- <div class="center">
                <img src="static/images/line.png" class="line"/></div> -->
            <!--登陆右边-->
            <div class="right">
                <div class="doubleCenter">
                    <!-- <img src="static/images/people.png" class="people"/> -->
                    
                    <div class="login-main">
                        <p>登录</p>
                        <div>
                            <div class="inp username">
                                <img src="static/images/username.png">
                                <input type="text" required lay-verify="required" placeholder="  请输入用户名" v-model='value.username' autocomplete="off"  v-focus autofocus="autofocus" @keyup.enter="loginFun">
                            </div>
                            <div class="inp pwd">
                                <img src="static/images/key.png">
                                <input type="password" required lay-verify="required" placeholder="  请输入密码" v-model='value.password' autocomplete="off" @keyup.enter="loginFun">
                            </div>
                            <div class="login-btn">
                                <button class="login" @click="loginFun()">登录</button>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
  </div>
</template>

<script>
  import {login,permissions} from '../config/axiosUtil'
  import '../../static/css/login.css'
  import { layerMsg } from '../public/public.js'

  export default {
    name: "Login",
    // components: {'Header': header, 'Footer': footer},
    data () {
      return {
        value : {
            username:'',
            password:'',
        }
      }
    },
    directives: {
        focus: {
            // 指令的定义
            inserted: function (el) {
            el.focus()
            // console.log(el)
            }
        }
    },
    created() {
    },
    methods: {
        loginFun(){
            var that = this;
            layui.use('layer',function(){
                var layer = layui.layer;
                
                if(that.value.username && that.value.password){
                    var index = layer.load(0);
                    login(that.value).then((res) =>{
                        console.log(res);
                        // return false;
                        // var index = layer.load(0);
                        // return false
                        if(res.data.code == "0"){
                            sessionStorage.setItem('username',res.data.user)		//账号					
                            sessionStorage.setItem('userRole',res.data.userRole)   //角色
							sessionStorage.setItem('userRoleA',res.data.userRole2)
							sessionStorage.setItem('serviceArea',res.data.custName)   //服务区域
                            if(res.data.userRole == '运维助理'||res.data.userRole == '驻现场人员'||res.data.userRole == '客服经理'){

                                that.$router.push({path:'/home/assistant'})
                                location.reload()
                                layer.close(index)
                            }else if(res.data.userRole == '客户'){
                                that.$router.push({path:'/home/client'})
                                location.reload()
                                layer.close(index)
                            }else if(res.data.userRole == '总部客服'){
                                that.$router.push({path:'/home/serviceHome'})
                                location.reload()
                                layer.close(index)
                            }else if(res.data.userRole == '运维总监'||res.data.userRole == '超级管理员'){
                                
                                that.$router.push({path:'/home/majordomo'})
                                location.reload()
                                layer.close(index)
                            }else if(res.data.userRole == '运维经理'||res.data.userRole == '技术主管'){
                                that.$router.push({path:'/home/operations'})
                                location.reload()
                                layer.close(index)
                            }else if(res.data.userRole == '工程师'){
                                layer.close(index)
                                layer.msg("此账户并无登录权限")
                            }
                            // location.reload()
                        }else{
                            layer.close(index);
                            layer.msg(res.data.msg)
                        }
                    })
                    .catch((error) => {
                        layer.close(index)
                        layer.msg("请检查你的网络连接")
                    })
                }else{
                    layer.close(index)
                    layer.msg('请输入用户名和密码')
                }
                
            })
            
        },
        // document.body.addEventListener('keyup', function (e) {
        //     var ev= window.event||e;
        //     if (e.keyCode == '13') {
        //         $(".login").click()
        //     }
        // })
  },
    created() {
    }

  }
</script>

<style scoped>
    
</style>



