		},
		// 新增合同
		add(){
			var that = this;	
			that.flag = 'add'
			layui.use(['form','table','layer','util'], function(){
				var table = layui.table, layer = layui.layer;
				var form = layui.form;				
				layer.open({
					type:1,
					title: '新增合同',
					area: ['700px','500px'],
					content: $('.sdd')
				})
				var userN = {username:sessionStorage.getItem('username')}
				areaManage(userN).then((res) => {
					// console.log(res)
					var data = res.data.data;
                    var serverArea = []
                    for(var i=0; i<data.length; i++){
                        if(serverArea.indexOf(data[i].compName) == -1){
                            serverArea.push(data[i].compName);
                        }
                    }
                    for(var j=0; j<serverArea.length; j++){
                        // console.log(serverArea[j])
                        $('#addmainSer').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
                        $('#addchildSer').after('<option value=\''+ serverArea[j] +'\' id="">'+ serverArea[j] +'</option>');
                    }
                    form.render()
				})
				.catch((error) => {
				})
			})
		},		
		subMit(){
			var that = this
			// console.log(that.formVa)
			deviceBindingContract(that.formVa)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerClose('test')
					that.$layerMsg('绑定成功')

				}else{
					that.$layerMsg(res.data.msg)
				}				
			})
			.catch((error) => {
				
			})
		},
		exportExcel(){
			var that = this;
			that.$exportE('exportContractManage')	
		},
		showA(){
			var that = this
			var arr = [that.$admin.ywzl,that.$admin.zxcry,that.$admin.kfzg,that.$admin.admin]
			// console.log(arr)
			for(var i = 0,l = arr.length;i<l;i++){
				if(arr[i] == that.$userRole){
					that.addShow = true
				}
			}
		},
		isShow(){
			var that = this			
			var arr2 = that.$admin
			var admin = ''
			if(arr2.admin == that.$userRole || arr2.zxcry == that.$userRole|| arr2.kfjl == that.$userRole|| arr2.ywzl == that.$userRole){
				admin = '<a class="layui-btn layui-btn-xs" lay-event="add">绑定设备信息</a><a class="layui-btn layui-btn-danger layui-btn-xs" lay-event="edit">修改</a>'					
			}else{
				admin = '<a class="layui-btn layui-btn-xs noAdmin">'+that.$noAdmin+'</a>'
			}
			return admin;
			
		},
    },
	created(){
		this.showA()
    },
	destroyed() {
        $('.layui-icon-close').click();
    },

}
</script>
<style scoped>
#assistant{
    width: 85%;
}
.sdd .layui-input-inline{
	width: 350px;
}
.labnum>label>span{
	color: #f00;
	font-size: 14px;
    vertical-align: middle;
}
.sdd .layui-input-inline,.sdd2 .layui-input-inline{
	width: 300px;
}
.selinput{
	width: 300px;
	height: 38px;
    /* line-height: 38px; */
}
.sdd3 .layui-input-inline{
	width: 300px;
}
</style>



